import { Map } from 'immutable';

import { MapBase } from '../../base/mapBase';

import {
    BizActionParameter, BizMgrActionParameter, BizParameterType, BizParameterMode, BizParameterBase, BizOperationConst
} from '../../../ref';

export abstract class ViewParameterBase extends MapBase {

    /**
     * 
     * @param parameter
     */
    constructor(parameter: BizParameterBase | Map<string, any>) {
        super(parameter);
    }

    get ParamCode(): string {
        return this.get(BizOperationConst.ParamCode);
    }
    set ParamCode(value: string) {
        this.set(BizOperationConst.ParamCode, value);
    }

    get ParamName(): string {
        return this.get(BizOperationConst.ParamName);
    }
    set ParamName(value: string) {
        this.set(BizOperationConst.ParamName, value);
    }

    get ParameterType(): BizParameterType {
        return this.get(BizOperationConst.ParameterType);
    }
    set ParameterType(value: BizParameterType) {
        this.set(BizOperationConst.ParameterType, value);
    }

    get Assembly(): string {
        return this.get(BizOperationConst.Assembly);
    }
    set Assembly(value: string) {
        this.set(BizOperationConst.Assembly, value);
    }

    get ClassName(): string {
        return this.get(BizOperationConst.ClassName);
    }
    set ClassName(value: string) {
        this.set(BizOperationConst.ClassName, value);
    }

    get Mode(): BizParameterMode {
        return this.get(BizOperationConst.Mode);
    }
    set Mode(value: BizParameterMode) {
        this.set(BizOperationConst.Mode, value);
    }

    get ParamDescription(): string {
        return this.get(BizOperationConst.ParamDescription);
    }
    set ParamDescription(value: string) {
        this.set(BizOperationConst.ParamDescription, value);
    }
}

export class ViewBizActionParameter extends ViewParameterBase {
    constructor(parameter: BizActionParameter | Map<string, any>) {
        super(parameter);
    }
}

export class ViewBizMgrActionParameter extends ViewParameterBase {
    constructor(parameter: BizMgrActionParameter | Map<string, any>) {
        super(parameter);
    }
}



