import { Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter, SimpleChanges } from '@angular/core';
import { FuncoperationEntity } from '../../entity/funcoperationentity';
import { FuncoperationTreeComponent } from '../funcoperation-tree/funcoperation-tree.component';
import { isNullOrUndefined } from 'util';

import { AuthoperationEntity } from '../../entity/authoperationentity';
import { ModalDirective } from '@farris/ui/container/modal/modal';

@Component({
  selector: 'app-funcoperation-select',
  templateUrl: './funcoperation-select.component.html',
  styleUrls: ['./funcoperation-select.component.css']
})
export class FuncoperationSelectComponent implements OnInit, OnChanges {
  @ViewChild('autoShownModal') autoShownModal: ModalDirective;
  @ViewChild('treeDialog') treeDialog: FuncoperationTreeComponent;

  // @Input() 
  @Input() triggerShowWindow: boolean;

   // 
   @Output() funcoperation: EventEmitter<FuncoperationEntity> = new EventEmitter<FuncoperationEntity>();

  isModalShown = false;
  operationSelected: FuncoperationEntity;

  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    // 
    if (isNullOrUndefined(changes.triggerShowWindow.previousValue)) {
      return;
    }
    if (!this.isModalShown) {
      this.showModal();
    }
  }

  showModal(): void {
    this.isModalShown = true;
  }

  hideModal(): void {
    this.autoShownModal.hide();
  }

  onHidden(): void {
    this.isModalShown = false;
  }

  btnOkClick() {
    this.operationSelected = this.treeDialog.dialogOk();
    this.hideModal();
    this.funcoperation.next(this.operationSelected);
  }

 btnClearClick() {
    this.operationSelected = null;
    this.hideModal();
    this.funcoperation.next(this.operationSelected);
  }
}




