import { MethodparamListComponent } from './methodparam-list.component';
import {
    Component, Input, ViewChild, Renderer2, OnInit, OnChanges, Output, EventEmitter, SimpleChanges
} from '@angular/core';
import {
    ViewParameterListBase, BizActionParameter, ViewBizActionParaList,
    BizParameterBase, ViewBizActionParameter,
    ViewParameterBase, BizMgrActionParameter, ViewBizMgrActionParaList, ViewBizMgrActionParameter, ViewActionBase
} from '../../../util/be-reference';

@Component({
    selector: 'app-mgr-action-methodparam-list',
    templateUrl: './methodparam-list.component.html',
    styleUrls: ['./methodparam-list.component.css']
})
export class MgrActionMethodparamListComponent extends MethodparamListComponent implements OnInit, OnChanges {

    createNewViewParameters(): ViewParameterListBase {
        const paras = new Array<BizMgrActionParameter>();
        return new ViewBizMgrActionParaList(paras);
    }

    /**
     * 
     */
    createViewParaInstance(para: BizParameterBase): ViewParameterBase {
        return new ViewBizMgrActionParameter(para);
    }

    createParaInstance(): BizParameterBase {
        return new BizMgrActionParameter();
    }
}



