import { TemplateRef, Component, OnInit, Input, OnChanges, SimpleChanges, ElementRef, OnDestroy, ViewChild } from '@angular/core';
import {
  PopupService,
  PopupRef
} from '@progress/kendo-angular-popup';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import { Subscription } from 'rxjs/Subscription';
import { Map, List } from 'immutable';

import {
  ViewValidation, Validation, RequestNodeTriggerType,
  GSPComponent, BEMethod, IGSPCommonElement, GSPBizEntityObject,
  ListBase, RequestChildElement, IGSPCommonObject, ViewRequestChildElementList,
  SelectedComponentInfo, MetadataTypeName
} from '../../util/be-reference';
import {
  DeterminationTriggerElementEditComponent
} from '../../determinations-tab/determination-trigger-element-edit/determination-trigger-element-edit.component';

import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';
import { ComponentGeneratorChooseComponent } from '../../refModule/component-generator-choose/component-generator-choose.component';

/**
 * Validation
 */
@Component({
  selector: 'app-validation-card',
  templateUrl: './validation-card.component.html',
  styleUrls: ['./validation-card.component.css']
})
export class ValidationCardComponent implements OnInit, OnChanges, OnDestroy {

  @ViewChild('editElements') editElements: DeterminationTriggerElementEditComponent;
  @ViewChild('componentSelector') componentSelector: ComponentGeneratorChooseComponent;
  /**
   * Validation
   */
  @Input() viewOperationData: ViewValidation;
  @Input() currentObject: GSPBizEntityObject;
  opType = 'Validation';
  componentName: string;
  /**
   * 
   */
  triggerObject: IGSPCommonObject;

  /**
   * 
   */
  triggerElements: IGSPCommonElement[];
  operation: Validation;

  public formGroup: FormGroup;

  /**
   * viewValidation
   */
  refreshSubscriptions = new Array<Subscription>();

  /**
   * 
   * Subscription
   */
  formGroupSubscriptions: any[];

  /**
   * 
   */
  public components: GSPComponent[];

  /**
   * 
   */
  popupRef: PopupRef;

  /**
   * 
   */
  method: BEMethod;

  /**
   * 
   */
  get requestElementsValue(): string {
    if (isNullOrUndefined(this.triggerObject) || isNullOrUndefined(this.triggerElements) || this.triggerElements.length === 0) {
      return '';
    }
    let triggerElementsString = this.triggerObject.Name + ' (';
    this.triggerElements.forEach(ele => {
      triggerElementsString += ele.Name + ',';
    });
    const value = triggerElementsString.slice(0, triggerElementsString.length - 1) + ')';
    return value;
  }

  ngOnInit() {
    this.load();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['viewOperationData']) {
      this.refresh();
    }
  }

  ngOnDestroy(): void {
    this.unsubscribeRefreshLayer();
  }

  constructor(private formBuilder: FormBuilder, private popupService: PopupService) {

  }

  /**
   * 
   */
  load(): any {
    this.createForm();
    this.refresh();
  }

  /**
   * 
   */
  refresh() {
    if (isNullOrUndefined(this.viewOperationData)) {
      this.clearFormValue();
      return;
    }

    this.unsubscribeRefreshLayer();
    this.operation = this.viewOperationData.toJS();
    this.initTriggerObjectAndElements(this.operation);

    this.setFormValue(this.operation);
    this.subscribeRefreshViewLayer();
  }

  /**
   * 
   */
  createForm() {
    this.formGroup = this.formBuilder.group({
      code: ['', Validators.required],
      name: ['', Validators.required],
      component: [''],
      method: [''],
      description: [''],
      createdTrigger: [''],
      updatedTrigger: [''],
      deletedTrigger: [''],
      requestElements: [''],
    });
  }

  /**
   * 
   * @param validation
   */
  setFormValue(validation: Validation) {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(validation)) {
      this.clearFormValue();
    }
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.enable();
    this.formGroup.patchValue({
      code: validation.Code || '',
      name: validation.Name || '',
      component: validation.ComponentName || '',
      // component: this.getComponentNameById(validation.ComponentId) || '',
      method: this.getMethodNameById(validation.ComponentId) || '',
      description: validation.Description || '',
      createdTrigger: this.getCreatedTrigger(validation.RequestNodeTriggerType),
      updatedTrigger: this.getUpdatedTrigger(validation.RequestNodeTriggerType),
      deletedTrigger: this.getDeletedTrigger(validation.RequestNodeTriggerType),
      requestElements: this.requestElementsValue,
    });

    // dbe
    if (this.isDbeOp()) {
      this.formGroup.disable();
      return;
    }

    this.subscribeFormGroupValueChanges();
  }

  /**
   * 
   * @param triggerType
   */
  getCreatedTrigger(triggerType: RequestNodeTriggerType): boolean {
    // tslint:disable-next-line:no-bitwise
    return (triggerType & RequestNodeTriggerType.Created) === RequestNodeTriggerType.Created;
  }

  getUpdatedTrigger(triggerType: RequestNodeTriggerType): boolean {
    // tslint:disable-next-line:no-bitwise
    return (triggerType & RequestNodeTriggerType.Updated) === RequestNodeTriggerType.Updated;
  }

  getDeletedTrigger(triggerType: RequestNodeTriggerType): boolean {
    // tslint:disable-next-line:no-bitwise
    return (triggerType & RequestNodeTriggerType.Deleted) === RequestNodeTriggerType.Deleted;
  }

  setCreatedTrigger(created: boolean): any {
    if (created) {
      this.viewOperationData.RequestNodeTriggerType += RequestNodeTriggerType.Created;
    } else {
      this.viewOperationData.RequestNodeTriggerType -= RequestNodeTriggerType.Created;
    }
  }

  setUpdatedTrigger(updated: boolean): any {
    if (updated) {
      this.viewOperationData.RequestNodeTriggerType += RequestNodeTriggerType.Updated;
    } else {
      this.viewOperationData.RequestNodeTriggerType -= RequestNodeTriggerType.Updated;
    }
  }

  setDeletedTrigger(deleted: boolean): any {
    if (deleted) {
      this.viewOperationData.RequestNodeTriggerType += RequestNodeTriggerType.Deleted;
    } else {
      this.viewOperationData.RequestNodeTriggerType -= RequestNodeTriggerType.Deleted;
    }
  }

  /**
   * 
   */
  clearFormValue() {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.disable();
    this.formGroup.patchValue({
      code: '',
      name: '',
      component: '',
      method: '',
      description: '',
      createdTrigger: '',
      updatedTrigger: '',
      deletedTrigger: '',
      requestElements: '',
    });
    this.subscribeFormGroupValueChanges();
  }

  /**
   * Id
   * @param cmpId
   */
  getComponentNameById(cmpId: string): string {
    return '';
    // if (isNullOrUndefined(cmpId)) {
    //   return '';
    // }
    // const cmp = this.componentService.getComponentById(cmpId);
    // if (isNullOrUndefined(cmp)) {
    //   return '';
    // }
    // return cmp.ComponentName;
  }

  /**
   * Id
   * @param cmpId
   */
  getMethodNameById(cmpId: string): string {
    return '';
    // if (isNullOrUndefined(cmpId)) {
    //   return '';
    // }
    // const method = this.componentService.getMethodByCmpId(cmpId);
    // if (isNullOrUndefined(method)) {
    //   return '';
    // }
    // return method.MethodName;
  }

  /**
   * 
   * 
   */
  subscribeFormGroupValueChanges() {
    const subscriptionCode = this.formGroup.controls.code.valueChanges.subscribe(code => this.viewOperationData.Code = code);
    const subscriptionName = this.formGroup.controls.name.valueChanges.subscribe(name => this.viewOperationData.Name = name);
    const subscriptionDes = this.formGroup.controls.description.valueChanges.subscribe(description =>
      this.viewOperationData.Description = description);
    const subscriptionCreated = this.formGroup.controls.createdTrigger.valueChanges.subscribe(created => this.setCreatedTrigger(created));
    const subscriptionDeleted = this.formGroup.controls.deletedTrigger.valueChanges.subscribe(deleted => this.setDeletedTrigger(deleted));
    const subscriptionUpdated = this.formGroup.controls.updatedTrigger.valueChanges.subscribe(updated => this.setUpdatedTrigger(updated));
    if (isNullOrUndefined(this.formGroupSubscriptions)) {
      this.formGroupSubscriptions = new Array<any>();
    }
    this.formGroupSubscriptions.push(subscriptionCode);
    this.formGroupSubscriptions.push(subscriptionName);
    this.formGroupSubscriptions.push(subscriptionDes);
    this.formGroupSubscriptions.push(subscriptionCreated);
    this.formGroupSubscriptions.push(subscriptionDeleted);
    this.formGroupSubscriptions.push(subscriptionUpdated);
  }

  /**
   * viewValidation
   */
  subscribeRefreshViewLayer(): any {
    const subscription = this.viewOperationData.refreshView.subscribe(value => {
      this.operation = value.toJS();
      this.setFormValue(this.operation);
    });
    this.refreshSubscriptions.push(subscription);
  }

  /**
   * viewValidation
   */
  unsubscribeRefreshLayer() {
    if (this.refreshSubscriptions) {
      while (this.refreshSubscriptions.length > 0) {
        const subscription = this.refreshSubscriptions.pop();
        subscription.unsubscribe();
      }
    }
  }

  /**
   * 
   */
  unsubscribeFormGroupValueChanges() {
    if (this.formGroupSubscriptions && this.formGroupSubscriptions.length > 0) {
      while (this.formGroupSubscriptions.length > 0) {
        const subscription = this.formGroupSubscriptions.pop();
        subscription.unsubscribe();
      }
    }
  }

  /**
   * 
   * @param anchor
   * @param template
   */
  public cmpTogglePopup(anchor: ElementRef, template: TemplateRef<any>) {
    // this.components = this.componentService.getComponents();
    // this.popupClick(anchor, template);
  }

  /**
   * 
   * @param anchor
   * @param template
   */
  popupClick(anchor: ElementRef, template: TemplateRef<any>) {
    if (this.popupRef) {
      this.popupClose();
    } else {
      this.popupRef = this.popupService.open({
        anchor: anchor,
        content: template
      });
    }
  }

  /**
   * 
   */
  popupClose() {
    this.popupRef.close();
    this.popupRef = null;
  }

  /**
   * 
   * @param selectComponent
   */
  confirmSelectComponent(selectComponent: GSPComponent) {
    this.popupClose();

    if (selectComponent.ComponentID === this.viewOperationData.ComponentId) {
      return;
    } else {
      this.changeComponent(selectComponent);
    }
  }

  /**
   * 
   * @param selectComponent
   */
  changeComponent(selectComponent: GSPComponent) {
    // this.viewOperationData.ComponentId = selectComponent.ComponentID;
    // // this.componentService.addComponent(selectComponent);

    // this.changeMethod(selectComponent.Method);
  }

  /**
   * 
   * @param selectMethod
   */
  changeMethod(selectMethod: BEMethod) {
    // if (isNullOrUndefined(selectMethod)) {
    //   // this.viewOperationData.MethodId = '';
    //   this.viewOperationData.Code = '';
    //   this.viewOperationData.Name = '';
    //   this.viewOperationData.Description = '';
    //   return;
    // }
    // // this.viewOperationData.MethodId = selectMethod.MethodID;
    // this.viewOperationData.Code = selectMethod.MethodName;
    // this.viewOperationData.Name = selectMethod.MethodName;
    // this.viewOperationData.Description = selectMethod.MethodDescription;
  }

  /**
   * 
   */
  editTriggerElements() {
    if (this.isDbeOp()) {
      return;
    }
    this.editElements.load(this.triggerObject, this.triggerElements, this.currentObject);
  }

  /**
   * 
   * @param requestKeyValuePair 
   */
  saveHandler(requestKeyValuePair: RequestChildElement) {
    if (isNullOrUndefined(requestKeyValuePair)) {
      this.viewOperationData.RequestElements = null;
      this.viewOperationData.RequestChildElements = null;
      this.refresh();
      return;
    }

    const ids = requestKeyValuePair.RequestChildElementValue;

    // 
    if (isNullOrUndefined(requestKeyValuePair.RequestChildElementKey) || requestKeyValuePair.RequestChildElementKey.length === 0) {
      this.viewOperationData.RequestElements = new ListBase(List(ids));
      this.viewOperationData.RequestChildElements = null;
      this.refresh();
      return;
    }
    // 
    this.viewOperationData.RequestChildElements = new ViewRequestChildElementList([requestKeyValuePair]);
    this.viewOperationData.RequestElements = null;
    this.refresh();
  }

  /**
   * 
   * @param args
   */
  cancelHandler(args: any) {

  }

  /**
   * 
   * @param validation 
   */
  initTriggerObjectAndElements(validation: Validation): any {
    if (isNullOrUndefined(validation)) {
      this.triggerObject = null;
      this.triggerElements = null;
      return;
    }

    // 
    if (!isNullOrUndefined(validation.RequestElements) && validation.RequestElements.length > 0) {
      const eleIds = validation.RequestElements;
      // 
      this.triggerObject = this.currentObject;
      this.triggerElements = this.initTriggerElementsByIds(this.triggerObject, eleIds);
      return;
    }

    // 1
    if (!isNullOrUndefined(validation.RequestChildElements) && validation.RequestChildElements.length > 0) {
      // 1
      const requestKeyValulePair = <RequestChildElement>validation.RequestChildElements[0];
      const objID = requestKeyValulePair.RequestChildElementKey;
      const childEleIds = requestKeyValulePair.RequestChildElementValue;
      // 
      this.triggerObject = this.currentObject.ContainChildObjects.find(item => item.ID === objID);
      this.triggerElements = this.initTriggerElementsByIds(this.triggerObject, childEleIds);
      return;
    }

    // 
    this.triggerObject = null;
    this.triggerElements = null;
    return;

  }

  initTriggerElementsByIds(obj: IGSPCommonObject, ids: string[]): IGSPCommonElement[] {
    if (isNullOrUndefined(obj) || isNullOrUndefined(ids)) {
      return null;
    }
    return obj.ContainElements.filter(ele => ids.includes(ele.ID));
  }

  /**
   * ""
   */
  public compSelectorClickHander() {
    if (this.isDbeOp()) {
      return;
    }
    if (isNullOrUndefined(this.operation.ComponentName) || !this.operation.IsGenerateComponent) {
      this.componentSelector.load(MetadataTypeName.ValidationComponent);
    }
  }
  /**
   * 
   * @param cmpMetadata 
   */
  excuteAfterGetMetadata(selectedCmpInfo: SelectedComponentInfo) {
    this.updateOperationInfo(selectedCmpInfo);
  }

  /**
   * Operation
   * @param cmpMetadata 
   */
  updateOperationInfo(selectedCmpInfo: SelectedComponentInfo) {
    // 
    this.viewOperationData.ComponentId = selectedCmpInfo.component.ComponentID;
    if (isNullOrEmptyString(this.viewOperationData.Code)) {
      this.viewOperationData.Code = selectedCmpInfo.component.ComponentCode;
    }
    if (isNullOrEmptyString(this.viewOperationData.Name)) {
      this.viewOperationData.Name = selectedCmpInfo.component.ComponentName;
    }
    if (isNullOrEmptyString(this.viewOperationData.Description)) {
      this.viewOperationData.Description = selectedCmpInfo.component.ComponentDescription;
    }
    this.viewOperationData.ComponentName = selectedCmpInfo.cmpFileName;
    this.viewOperationData.ComponentPkgName = selectedCmpInfo.cmpPkgName;
    this.viewOperationData.IsGenerateComponent = false;
    this.operation = this.viewOperationData.toJS();
  }

  /**
   * dbe
   */
  isDbeOp(): boolean {
    return this.viewOperationData.IsRef;
  }
}



