import { isNullOrUndefined } from 'util';
import {
    BaseDataType, BizElementConst, GSPAssociation, GSPAssociationKey,
    GSPBizEntityElement, GSPElementDataType, GSPElementObjectType, Guid,
    IGSPCommonElement, SimpleDataTypeDef, UdtAssociation, UdtAssociationKey,
    UdtElement, UdtObjectType, GSPEnumValue,
    UdtEnumValue, UseType, SimpleDataTypeConst, UdtMetadataContent, UdtTypeConst, ComplexDataTypeDef
} from './be-reference';

export class UdtUtil {

    /**
     * udt
     * @param element 
     * @param udtContent udt
     */
    static loadUdtProperty(element: GSPBizEntityElement, udtContent: UdtMetadataContent) {
        if (isNullOrUndefined(udtContent)) {
            alert('');
            return;
        }
        const type = udtContent.Type;

        switch (type) {
            case UdtTypeConst.ComplexDataType:
                // udt,codeName
                const cUdt = <ComplexDataTypeDef>JSON.parse(udtContent.Content);
                this.mapComplexUdtToUdtElement(element, cUdt);
                element.UnifiedDataType = null;
                break;
            case UdtTypeConst.SimpleDataType:
                const sUdt = <SimpleDataTypeDef>JSON.parse(udtContent.Content);
                element.UnifiedDataType = sUdt;
                // udt,be
                this.mapSimpleUdtToElement(element, sUdt);
                break;
        }
    }

    /**
     * udt
     * @param element 
     * @param udtContent udt
     */
    static updateUdtProperty(element: GSPBizEntityElement, udtContent: UdtMetadataContent) {
        if (isNullOrUndefined(udtContent)) {
            alert('');
            return;
        }
        const type = udtContent.Type;

        switch (type) {
            case UdtTypeConst.ComplexDataType:
                // udt,
                element.UnifiedDataType = null;
                break;
            case UdtTypeConst.SimpleDataType:
                const sUdt = <SimpleDataTypeDef>JSON.parse(udtContent.Content);
                element.UnifiedDataType = sUdt;
                // udt,
                UdtUtil.updateUdtConstraintPropertyToElement(element, sUdt);
                break;
        }
    }

    /**
     * ()be
     * @param element be
     * @param udt 
     */
    private static mapSimpleUdtToElement(element: GSPBizEntityElement, udt: SimpleDataTypeDef): any {
        if (this.isEmpty(element.Code)) {
            element.Code = udt.Code;
        }
        if (this.isEmpty(element.Name)) {
            element.Name = udt.Name;
        }
        if (this.isEmpty(element.LabelID)) {
            element.LabelID = udt.Code;
        }
        element.MDataType = this.convertUdtDataType(udt.DataType);
        element.Length = udt.Length;
        element.Precision = udt.Precision;
        element.ObjectType = this.convertUdtObjectType(udt.ObjectType);
        element.ChildAssociations = this.convertUdtAssociations(udt.ChildAssociations, element);
        element.ContainEnumValues = this.convertUdtEnumValues(udt.ContainEnumValues);
        element.ObjectType = this.convertUdtObjectType(udt.ObjectType);
        // []udtDesigner/beElement
        element.DefaultValue = udt.DefaultValue;
        element.IsRequire = udt.IsRequired;
    }

    /**
     * ()
     * @param element be
     * @param udt 
     */
    private static mapComplexUdtToUdtElement(element: GSPBizEntityElement, cUdt: ComplexDataTypeDef) {
        // 
        if (this.isEmpty(element.Code)) {
            element.Code = cUdt.Code;
        }
        if (this.isEmpty(element.Name)) {
            element.Name = cUdt.Name;
        }
        if (this.isEmpty(element.LabelID)) {
            element.LabelID = cUdt.Code;
        }
        // 
        const newElement = new GSPBizEntityElement();
        element.ObjectType = newElement.ObjectType;
        element.Length = newElement.Length;
        element.Precision = newElement.Precision;
        element.ObjectType = newElement.ObjectType;
        element.DefaultValue = newElement.DefaultValue;
    }

    /**
     * (be)
     * @param element be
     * @param udt 
     */
    static updateUdtConstraintPropertyToElement(element: GSPBizEntityElement, udt: SimpleDataTypeDef): GSPBizEntityElement {
        const infos = udt.PropertyUseTypeInfos;
        if (isNullOrUndefined(infos) || infos.length === 0) {
            return;
        }
        infos.forEach(info => {
            if (info.PropertyUseType === UseType.AsConstraint) {
                this.setPropertyValue(info.PropertyName, element, udt);
            }
        });
    }

    static canPropertyBeEdit(bizPropertyName: string, udt: SimpleDataTypeDef): boolean {
        const udtPropertyName = this.mapBizElementPropertyToUdtProperty(bizPropertyName);
        //  
        if (isNullOrUndefined(udtPropertyName)) {
            return true;
        }
        //  [][][]
        if (udtPropertyName === SimpleDataTypeConst.DataType) {
            return true;
        }
        //  be
        const infos = udt.PropertyUseTypeInfos;
        const info = infos.find(item => item.PropertyName === this.mapBizElementPropertyToUdtProperty(bizPropertyName));
        if (isNullOrUndefined(info)) {
            alert(`[${udtPropertyName}](/)`);
        }
        // 
        return info.PropertyUseType === UseType.AsTemplate;
    }

    private static mapBizElementPropertyToUdtProperty(bizPropertyName: string): string {
        switch (bizPropertyName) {
            case BizElementConst.MDataType:
                return SimpleDataTypeConst.DataType;
            case BizElementConst.Length:
                return SimpleDataTypeConst.Length;
            case BizElementConst.Precision:
                return SimpleDataTypeConst.Precision;
            case BizElementConst.ObjectType:
                return SimpleDataTypeConst.ObjectType;
            case BizElementConst.ChildAssociations:
                return SimpleDataTypeConst.ChildAssociations;
            case BizElementConst.ContainEnumValues:
                return SimpleDataTypeConst.ContainEnumValues;
            case BizElementConst.DefaultValue:
                return SimpleDataTypeConst.DefaultValue;
            case BizElementConst.IsRequire:
                return SimpleDataTypeConst.IsRequired;
            default:
                return null;
        }
    }

    /**
     * 
     * @param propertyName 
     * @param element be
     * @param udt 
     */
    private static setPropertyValue(propertyName: string, element: GSPBizEntityElement, udt: SimpleDataTypeDef): any {
        switch (propertyName) {
            case SimpleDataTypeConst.DataType:
                element.MDataType = this.convertUdtDataType(udt.DataType);
                break;
            case SimpleDataTypeConst.Length:
                element.Length = udt.Length;
                break;
            case SimpleDataTypeConst.Precision:
                element.Precision = udt.Precision;
                break;
            case SimpleDataTypeConst.ObjectType:
                element.ObjectType = this.convertUdtObjectType(udt.ObjectType);
                break;
            case SimpleDataTypeConst.ChildAssociations:
                element.ChildAssociations = this.convertUdtAssociations(udt.ChildAssociations, element);
                break;
            case SimpleDataTypeConst.ContainEnumValues:
                element.ContainEnumValues = this.convertUdtEnumValues(udt.ContainEnumValues);
                break;
            case SimpleDataTypeConst.DefaultValue:
                element.DefaultValue = udt.DefaultValue;
                break;
            case SimpleDataTypeConst.IsRequired:
                element.IsRequire = udt.IsRequired;
                break;
        }
    }

    /**
     * 
     * @param type
     */
    private static convertUdtDataType(type: BaseDataType): GSPElementDataType {
        switch (type) {
            case BaseDataType.Binary:
                return GSPElementDataType.Binary;
            case BaseDataType.Boolean:
                return GSPElementDataType.Boolean;
            case BaseDataType.Date:
                return GSPElementDataType.Date;
            case BaseDataType.DateTime:
                return GSPElementDataType.DateTime;
            case BaseDataType.Decimal:
                return GSPElementDataType.Decimal;
            case BaseDataType.Integer:
                return GSPElementDataType.Integer;
            case BaseDataType.String:
                return GSPElementDataType.String;
            case BaseDataType.Text:
                return GSPElementDataType.Text;
            default:
                alert('udt');
        }
    }

    /**
     * 
     * @param type
     */
    private static convertUdtObjectType(type: UdtObjectType): GSPElementObjectType {
        switch (type) {
            case UdtObjectType.None:
                return GSPElementObjectType.None;
            case UdtObjectType.Association:
                return GSPElementObjectType.Association;
            case UdtObjectType.Enum:
                return GSPElementObjectType.Enum;
            default:
                alert('udt');
        }
    }

    /**
     * 
     * @param assos
     */
    private static convertUdtAssociations(assos: UdtAssociation[], element: GSPBizEntityElement): GSPAssociation[] {
        if (isNullOrUndefined(assos) || assos.length === 0) {
            return null;
        }
        const assoList = new Array<GSPAssociation>();
        assos.forEach(udtAsso => {
            assoList.push(this.convertUdtAssociation(udtAsso, element));
        }
        );
        return assoList;
    }

    private static convertUdtAssociation(udtAsso: UdtAssociation, element: GSPBizEntityElement): GSPAssociation {
        const asso = new GSPAssociation();
        asso.ID = Guid.newGuid().toString();
        asso.KeyCollection = this.convertUdtAssoKeys(udtAsso.KeyCollection, element);
        asso.RefElementCollection = this.convertUdtElements(udtAsso.RefElementCollection);
        asso.RefModelCode = udtAsso.RefEntityCode;
        asso.RefModelID = udtAsso.RefEntityId;
        asso.RefModelName = udtAsso.RefEntityName;
        asso.RefModelPkgName = udtAsso.RefEntityPkgName;
        return asso;
    }

    /**
     * 
     * @param udtEnums
     */
    private static convertUdtEnumValues(udtEnums: UdtEnumValue[]): GSPEnumValue[] {
        if (isNullOrUndefined(udtEnums) || udtEnums.length === 0) {
            return null;
        }
        const enumList = new Array<GSPEnumValue>();
        udtEnums.forEach(udtEnum => {
            enumList.push(this.convertUdtEnumValue(udtEnum));
        }
        );
        return enumList;
    }

    private static convertUdtEnumValue(udtEnum: UdtEnumValue): GSPEnumValue {
        const enumValue = new GSPEnumValue();
        enumValue.Name = udtEnum.Name;
        enumValue.Value = udtEnum.Value;
        return enumValue;
    }

    private static convertUdtElements(udtEles: UdtElement[]): IGSPCommonElement[] {
        if (isNullOrUndefined(udtEles) || udtEles.length === 0) {
            return null;
        }
        const eles = new Array<IGSPCommonElement>();
        udtEles.forEach(udtEle => {
            eles.push(this.convertUdtElement(udtEle));
        });
        return eles;
    }

    /**
     * 
     * @param udtEle
     */
    private static convertUdtElement(udtEle: UdtElement): IGSPCommonElement {
        const ele = new GSPBizEntityElement();
        ele.Code = udtEle.Code;
        ele.LabelID = udtEle.LabelId;
        ele.Name = udtEle.Name;
        ele.RefElementID = udtEle.RefElementId;
        return ele;
    }

    /**
     * 
     * @param udtKeys
     */
    private static convertUdtAssoKeys(udtKeys: UdtAssociationKey[], element: GSPBizEntityElement): GSPAssociationKey[] {
        if (isNullOrUndefined(udtKeys) || udtKeys.length === 0) {
            return null;
        }
        const keys = new Array<GSPAssociationKey>();
        udtKeys.forEach(udtKey => {
            keys.push(this.convertUdtAssoKey(udtKey, element));
        });
        return keys;
    }

    private static convertUdtAssoKey(udtKey: UdtAssociationKey, element: GSPBizEntityElement): GSPAssociationKey {
        const key = new GSPAssociationKey();
        key.SourceElement = udtKey.SourceElement;
        key.SourceElementDisplay = udtKey.SourceElementDisplay;
        key.TargetElement = element.ID;
        key.TargetElementDisplay = element.Name;
        return key;
    }

    /**
     * /
     * @param testString array
     */
    private static isEmpty(testString: any): boolean {
        if (isNullOrUndefined(testString) || testString.length === 0) {
            return true;
        }
        return false;
    }
}



