import { Injectable } from '@angular/core';
import {
    BizOperation, BizOperationConst, BizAction, Validation, Determination, HttpUrlConst
} from '../util/be-reference';
import { Http, RequestOptions, Headers } from '@angular/http';
import { Subject } from 'rxjs/Subject';
import { ValidateBizActionBase, ValidateDetermination, ValidateValidation } from '../util/validateBizEntity';
import { Observable } from 'rxjs/Observable';
import { CacheService } from '@farris/ide-devkit/services';

@Injectable()
export class CmpCodeTemplateService {
    currentUrl = HttpUrlConst.currentUrl;

    constructor(private http: Http, private cache: CacheService) {
        this.cmpGeneratorUrl = `${this.currentUrl}/api/dev/main/v1.0/componentcode`;
    }

    private cmpGeneratorUrl = `/api/dev/main/v1.0/componentcode`;
    private sessionId = this.cache.get('sessionId');
    generateSuccess: Subject<string> = new Subject<string>();

    generateCode(path: string, metadataName: string, operation: BizOperation, type: string, assemblyName: string, childObjectCode: string
    ): Observable<any> {
        if (!this.validateOperation(type, operation)) {
            return;
        }

        const opJson = JSON.stringify(operation);
        const cmpCodeGenerator = new CmpCodeGenerator();
        cmpCodeGenerator.operationType = type;
        cmpCodeGenerator.operation = opJson;
        cmpCodeGenerator.nameSpace = assemblyName;
        cmpCodeGenerator.path = path;
        cmpCodeGenerator.metadataName = metadataName;
        cmpCodeGenerator.childObjectCode = childObjectCode;

        const headers = new Headers();
        headers.append('sessionId', this.sessionId);

        const options = new RequestOptions({
            params: {},
            headers: headers
        });
        // webApi
        return this.http.post(this.cmpGeneratorUrl, cmpCodeGenerator);
    }

    validateOperation(type: string, operation: BizOperation): boolean {
        let result = true;
        switch (type) {
            case BizOperationConst.BizMgrAction:
                result = result && ValidateBizActionBase.Validate(operation as BizAction);
                break;
            case BizOperationConst.BizAction:
                result = result && ValidateBizActionBase.Validate(operation as BizAction);
                break;
            case BizOperationConst.Determination:
                result = result && ValidateDetermination.Validate(operation as Determination);
                break;
            case BizOperationConst.Validation:
                result = result && ValidateValidation.Validate(operation as Validation);
                break;
        }
        return result;
    }
}

class CmpCodeGenerator {
    operationType: string;
    operation: string;
    // ''
    childObjectCode: string;
    nameSpace: string;
    // 
    metadataName: string;
    // 
    path: string;
}




