/**
 * 
 */
export enum BizParameterMode {
    IN = 0,
    OUT = 1,
    INOUT = 2
}

export const BizParameterModes: Array<{ text: string, value: BizParameterMode }> = [
    { text: 'IN', value: BizParameterMode.IN },
    { text: 'OUT', value: BizParameterMode.OUT },
    { text: 'INOUT', value: BizParameterMode.INOUT }
];



