import { BEOperationType } from '../Enum/BEOperationType';
import { BizOperation } from './BizOperation';
import { RequestNodeTriggerType } from '../Enum/RequestNodeTriggerType';
import { BEValidationType } from '../Enum/BEValidationType';
import { RequestChildElement } from './RequestElement/RequestChildElement';
import { isNullOrUndefined } from 'util';


/**
* 
* @author wangjiegj<wangjiegj@inspur.com>
 */


/**
* 
*/
export class Validation extends BizOperation {

    constructor() {
        super();
    }

    /**
     * 
     */
    get OpType() {
        return BEOperationType.Validation;
    }

    /**
     * 
     */
    public ValidationType: BEValidationType;

    /**
     * 
     */
    public Order: number;

    /**
     * 
     */
    public Precedings: Validation[];

    /**
     * 
     */
    public Succeedings: Validation[];

    /**
     * 
     */
    public PrecedingIds: string[];

    /**
     * 
     */
    public SucceedingIds: string[];

    /**
     * 
     */
    public RequestNodeTriggerType: RequestNodeTriggerType;

    /**
     * LabelID
     * 20180427_ID
     */
    public RequestElements: string[];

    /**
     * ID
     */
    public RequestChildElements: RequestChildElement[];

    /**
     * 
     */
    static countainRequestElement(validation: Validation, id: string): boolean {
        if (this.countainEle(id, validation.RequestElements)) {
            return true;
        }
        if (!isNullOrUndefined(validation.RequestChildElements) && validation.RequestChildElements.length > 0) {
            return this.countainEle(id, validation.RequestChildElements[0].RequestChildElementValue);
        }
        return false;
    }

    private static countainEle(id: string, requestEles: string[]): boolean {
        if (!isNullOrUndefined(id) && !isNullOrUndefined(requestEles) && requestEles.length > 0) {
            return requestEles.includes(id);
        }
        return false;
    }
}



