import { Component } from '@angular/core';
import { BizAction, Guid, ViewBizAction } from '../util/be-reference';
import { BaseOperationComponentComponent } from '../base-operation-component/base-operation-component.component';

@Component({
  selector: 'app-biz-entity-action',
  templateUrl: './biz-entity-action.component.html',
  styleUrls: ['./biz-entity-action.component.css']
})
export class BizEntityActionComponent extends BaseOperationComponentComponent {

  constructor() {
    super();
  }

  addBeAction() {
    const newAction = new BizAction();
    this.viewOperations.add(new ViewBizAction(newAction));
    this.viewOperations.setSelectIndexByID(newAction.ID);
    this.refresh();
  }
}



