import { Map } from 'immutable';

import { MapBase } from '../base/mapBase';

import { BizOperation, BizOperationConst } from '../../ref';

export class ViewOperation extends MapBase {
    constructor(operation: BizOperation | Map<string, any>) {
        super(operation);
    }

    // get MethodId(): string {
    //     return this.get(bizOperationConst.MethodId);
    // }
    // set MethodId(value: string) {
    //     this.set(bizOperationConst.MethodId, value);
    // }

    get ComponentId(): string {
        return this.get(BizOperationConst.ComponentId);
    }
    set ComponentId(value: string) {
        this.set(BizOperationConst.ComponentId, value);
    }

    get ID(): string {
        return this.get(BizOperationConst.ID);
    }

    get Code(): string {
        return this.get(BizOperationConst.Code);
    }
    set Code(value: string) {
        this.set(BizOperationConst.Code, value);
    }

    get Name(): string {
        return this.get(BizOperationConst.Name);
    }
    set Name(value: string) {
        this.set(BizOperationConst.Name, value);
    }

    get Description(): string {
        return this.get(BizOperationConst.Description);
    }
    set Description(value: string) {
        this.set(BizOperationConst.Description, value);
    }

    get IsVisible(): boolean {
        return this.get(BizOperationConst.IsVisible);
    }
    set IsVisible(value: boolean) {
        this.set(BizOperationConst.IsVisible, value);
    }
    get ComponentName(): string {
        return this.get(BizOperationConst.ComponentName);
    }
    set ComponentName(value: string) {
        this.set(BizOperationConst.ComponentName, value);
    }
    get IsGenerateComponent(): boolean {
        return this.get(BizOperationConst.IsGenerateComponent);
    }
    set IsGenerateComponent(value: boolean) {
        this.set(BizOperationConst.IsGenerateComponent, value);
    }
    get IsRef(): boolean {
        return this.get(BizOperationConst.IsRef);
    }
    set IsRef(value: boolean) {
        this.set(BizOperationConst.IsRef, value);
    }
    get ComponentPkgName(): string {
        return this.get(BizOperationConst.ComponentPkgName);
    }
    set ComponentPkgName(value: string) {
        this.set(BizOperationConst.ComponentPkgName, value);
    }
}



