import { isNullOrUndefined } from 'util';

/**
 * 
 */
export class ValidateUtil {

    /**
     * 
     * @param list 
     */
    static isEmptyList(list: Array<any>) {
        if (isNullOrUndefined(list) || list.length === 0) {
            return true;
        }
        return false;
    }

    /**
     * 
     * @param stringValue 
     */
    static isEmptyString(stringValue: string): boolean {
        if (isNullOrUndefined(stringValue) || stringValue.length === 0) {
            return true;
        }
        return false;
    }
}



