import { List } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ListBase } from '../../base/listBase';
import { ViewRequestChildElement } from './viewRequestChildElement';

import { RequestChildElement } from '../../../ref';

export class ViewRequestChildElementList extends ListBase<ViewRequestChildElement> {

    constructor(paras: RequestChildElement[] | List<any>) {
        super(paras);
        if (isNullOrUndefined(paras)) {
            return;
        }
        this.data = this.getViewParameters(this.data);
    }

    private getViewParameters(paras: List<any>): List<ViewRequestChildElement> {
        if (isNullOrUndefined(paras)) {
            return;
        }

        let paraList = paras;

        if (paraList.size > 0) {
            for (let i = 0; i < paraList.size; i++) {
                paraList = paraList.update(i, value => this.getViewRequestChildElement(value));
            }
            return paraList;
        }
    }

    private getViewRequestChildElement(childEle: any) {
        return new ViewRequestChildElement(childEle);
    }
}






