import { Injectable } from '@angular/core';
import { Headers, RequestOptions, Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { CacheService } from '@farris/ide-devkit/services';
import { HttpUrlConst } from '../util/be-reference';
import { BeGuideInfo } from '../refModule/be-designer-guide/be-designer-guide.component';

/**
 * be
 */
@Injectable()
export class BeInitWithDbeService {

  constructor(private http: Http, private cache: CacheService) {
  }
  private sessionId = this.cache.get('sessionId');
  private urlBase = `${HttpUrlConst.currentUrl}/api/dev/main/v1.0/becodegenerate`;
  private initUrl = this.urlBase + '/initialWithDbe';

  /**
   * dbebe
   */
  init(beGuideInfo: BeGuideInfo): Observable<any> {
    const headers = new Headers();
    headers.append('sessionId', this.sessionId);

    const options = new RequestOptions({
      params: {},
      headers: headers
    });
    return this.http.put(this.initUrl, beGuideInfo, options);
  }
}



