/**
* Determination
*/
enum BETriggerTimePointType {
    /**
     * ,
     */
    None = 0,

    /**
     * , retrievequery
     *  BODeterminationType.TransientDetermination 
     * ,
     */
    AfterLoading = 1,

    /**
     * 
     * Node
     */
    AfterModify = 2,

    /**
     * 
     * ValidateAndDetermine 
     */
    Determine = 4,

    /**
     * Validation
     */
    BeforeCheck = 8,

    /**
     * 
     * 
     */
    AfterNumbersAdjusted = 16,

    /**
     * 
     * Finalize,Validation,
     */
    AfterFailedSaveAttempt = 32,

    /**
     * RetrieveDefault
     */
    RetrieveDefault = 64


}

export { BETriggerTimePointType };


export const BETriggerTimePointTypes: Array<{ index: number, value: BETriggerTimePointType }> = [
    { index: 1, value: BETriggerTimePointType.AfterLoading },
    { index: 2, value: BETriggerTimePointType.RetrieveDefault },
    { index: 3, value: BETriggerTimePointType.AfterModify },
    { index: 4, value: BETriggerTimePointType.Determine },
    { index: 5, value: BETriggerTimePointType.BeforeCheck },
    { index: 6, value: BETriggerTimePointType.AfterNumbersAdjusted },
];



