import {
    GSPBusinessEntity, GSPBizEntityObject, Guid, DataObjectType,
    GSPElementObjectType, GSPBizEntityElement, GSPElementDataType,
    GSPColumnGenerate, BizAction, AuthEntryEntity, AuthFieldInfo, GSPCommonObjectType
} from '../../util/be-reference';
import { isNullOrUndefined } from 'util';
// import { AuthorizationEntity } from '../refModule/authorization/authorization/entity/authorizationentity';

/**
 * 
 */
export class InitBizObject {
    /**
     * 
     */
    static initBizObject(): GSPBizEntityObject {
        const mainObj = new GSPBizEntityObject();
        const idElement = this.getIDElement();
        mainObj.ID = Guid.newGuid().toString();
        mainObj.Code = 'newObject';
        mainObj.Name = 'newObject';
        mainObj.ColumnGenerateID = new GSPColumnGenerate();
        mainObj.ColumnGenerateID.ElementID = idElement.ID;
        mainObj.ColumnGenerateID.GernerateType = 'Guid';
        mainObj.ContainElements = new Array<GSPBizEntityElement>();
        mainObj.ContainElements.push(idElement);
        mainObj.ObjectType = GSPCommonObjectType.ChildObject;

        // this.addElements(mainObj);
        // this.addBizActions(mainObj);
        // this.addAuthField(mainObj);
        return mainObj;
    }

    /**
     * id
     */
    private static getIDElement(): GSPBizEntityElement {
        const idElement = new GSPBizEntityElement();
        idElement.ID = Guid.newGuid().toString();
        idElement.Code = 'ID';
        idElement.Name = 'ID';
        idElement.LabelID = 'ID';
        idElement.MDataType = GSPElementDataType.String;
        idElement.ObjectType = GSPElementObjectType.None;
        idElement.Length = 36;
        idElement.Precision = 0;
        return idElement;
    }

    /**
     * bizAction
     * @param obj
     */
    private static addBizActions(obj: GSPBizEntityObject) {
        const bizAction1 = new BizAction();
        bizAction1.ID = Guid.newGuid();
        bizAction1.Name = 'BizAction1';
        const bizAction2 = new BizAction();
        bizAction2.ID = Guid.newGuid();
        bizAction2.Name = 'BizAction2';
        if (isNullOrUndefined(obj.BizActions)) {
            obj.BizActions = new Array<BizAction>();
        }
        obj.BizActions.push(...[bizAction1, bizAction2]);
    }

    private static addElements(obj: GSPBizEntityObject) {
        const ele1 = new GSPBizEntityElement();
        ele1.ID = Guid.newGuid();
        ele1.Name = '';
        ele1.Code = 'Code';
        ele1.LabelID = 'Code';
        const ele2 = new GSPBizEntityElement();
        ele2.ID = Guid.newGuid();
        ele2.Name = '';
        ele2.Code = 'Name';
        ele2.LabelID = 'Name';
        const ele3 = new GSPBizEntityElement();
        ele3.ID = Guid.newGuid();
        ele3.Name = '';
        ele3.LabelID = 'Department';
        ele3.Code = 'Department';
        obj.ContainElements.push(...[ele1, ele2, ele3]);
    }

    private static addAuthField(obj: GSPBizEntityObject) {
        const newAuthField1 = new AuthFieldInfo();
        newAuthField1.AuthFieldID = 'mockId1';
        newAuthField1.AuthFieldName = 'mockName1';
        newAuthField1.AuthorizationID = '';
        const newAuthField2 = new AuthFieldInfo();
        newAuthField2.AuthFieldID = 'mockId2';
        newAuthField2.AuthFieldName = 'mockName2';
        newAuthField2.AuthorizationID = '';
        if (isNullOrUndefined(obj.AuthFieldInfos)) {
            obj.AuthFieldInfos = new Array<AuthFieldInfo>();
        }
        obj.AuthFieldInfos.push(...[newAuthField1, newAuthField2]);
    }
}



