import { Component, Input, OnInit, ViewChild, SimpleChanges, OnChanges, OnDestroy } from '@angular/core';
import { isNullOrUndefined } from 'util';
import {
    GSPBizEntityElement, ViewAssociationList, GSPAssociation,
    GSPBizEntityObject, Guid, ViewElement, MetadataDto, GSPBusinessEntity, Metadata4RefDto, MetadataTypeName
} from '../../util/be-reference';
import { AssoKeyEditFormComponent } from './association-edit-form/association-edit-form.component';
import { GridComponent } from '@progress/kendo-angular-grid';

@Component({
    selector: 'app-be-association-edit',
    templateUrl: './association-edit.component.html',
    styleUrls: ['./association-edit.component.css']
})
export class AssociationEditComponent implements OnInit, OnChanges, OnDestroy {

    @Input() bizEntity: GSPBusinessEntity;
    @Input() currentObject: GSPBizEntityObject;
    @Input() viewElement: ViewElement;
    @Input() canEdit: boolean;

    /**
     * be
     */
    beTypeName = MetadataTypeName.GSPBusinessEntity;

    /**
     * 
     */
    addNew = true;

    /**
     * 
     */
    currentAsso: GSPAssociation;

    /**
     * 
     */
    assoValues: GSPAssociation[];

    // tempAssos: GSPAssociation[];
    assoSelection = [];
    get currentElement(): GSPBizEntityElement {
        if (isNullOrUndefined(this.viewElement)) {
            return null;
        }
        return this.viewElement.toJS();
    }

    showLoadMetadataWindow = false;

    @ViewChild('grid') grid: GridComponent;
    @ViewChild('assoKeyFormComponent') assoKeyFormComponent: AssoKeyEditFormComponent;

    get canAdd(): boolean {
        if (isNullOrUndefined(this.assoValues)) {
            return true;
        }
        return this.assoValues.length < 1;
    }

    get canDelete(): boolean {
        if (this.assoSelection && this.assoSelection.length > 0) {
            return true;
        }
        return false;
    }

    ngOnInit() {
        this.refresh();
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.refresh();
    }

    ngOnDestroy(): void {
    }

    constructor() {
    }

    /**
     * 
     */
    protected refresh() {
        if (isNullOrUndefined(this.viewElement) || isNullOrUndefined(this.viewElement.associations)) {
            this.assoValues = [];
            return;
        }
        this.assoValues = this.viewElement.associations.toJS();
        if (!isNullOrUndefined(this.assoValues) && this.assoValues.length > 0) {
            this.assoSelection = [this.assoValues[0].ID];
            this.currentAsso = this.assoValues[0];
        }
    }

    /**
     * 
     * @param e
     */
    public onConfirm(): void {
        this.viewElement.associations = new ViewAssociationList(this.assoValues);
    }

    checkBeforeSave(): boolean {
        if (isNullOrUndefined(this.currentElement.LabelID) || this.currentElement.LabelID.length === 0) {
            alert('[]');
            return false;
        }
        if (isNullOrUndefined(this.assoValues) || this.assoValues.length === 0) {
            alert('');
            return false;
        }

        for (let i = 0; i < this.assoValues.length; i++) {
            if (!this.checkAssociation(this.assoValues[i])) {
                return false;
            }
        }
        return true;
    }

    private checkAssociation(asso: GSPAssociation): boolean {
        if (isNullOrUndefined(asso)) {
            alert('');
            return false;
        }
        if (isNullOrUndefined(this.currentAsso.RefElementCollection) || this.currentAsso.RefElementCollection.length === 0) {
            alert(`${asso.RefModelName}`);
            return false;
        }
        return true;
    }

    /**
     * 
     * -step1.
     * @param param0
     */
    public addHandler() {
        this.addNew = true;
        // 
        this.selectRefModel();
    }

    /**
     * 
     * @param args
     */
    public removeHandler(): void {
        if (isNullOrUndefined(this.assoSelection) || this.assoSelection.length === 0) {
            alert('');
            return;
        }
        const rowIndex = this.assoValues.findIndex(value => value.ID === this.assoSelection[0]);
        this.assoValues.splice(rowIndex, 1);

        // ;;
        // -1
        if (this.assoValues.length > 0) {
            if (rowIndex > 0) {
                this.currentAsso = this.assoValues[rowIndex - 1];
            } else {
                this.currentAsso = this.assoValues[rowIndex];
            }
        } else {
            this.currentAsso = null;
        }
    }

    /**
     * 
     */
    selectionChangeHandler(args: any) {
        const { index } = args;
        this.currentAsso = this.assoValues[index];
    }

    /**
     * 
     */
    changeRefModel() {
        this.addNew = false;
        this.selectRefModel();
    }

    /**
     * 
     */
    selectRefModel() {
        this.showLoadMetadataWindow = !this.showLoadMetadataWindow;
    }

    GetBeMetadata(beMetadata: Metadata4RefDto) {
        const be = JSON.parse(beMetadata.metadata.content) as GSPBusinessEntity;

        // be
        if (be.ID === this.bizEntity.ID) {
            alert('');
            return;
        }

        // 
        if (this.addNew) {
            const newAsso = new GSPAssociation();
            newAsso.ID = Guid.newGuid();
            if (isNullOrUndefined(this.assoValues)) {
                this.assoValues = new Array<GSPAssociation>();
            }
            this.assoValues.push(newAsso);
            this.assoSelection = [newAsso.ID];
            this.currentAsso = newAsso;
            this.assoKeyFormComponent.load(newAsso);
        }
        this.assoKeyFormComponent.GetBeMetadata(beMetadata);
    }
}



