    /**
     * 
     * @param param0
     */
    public removeHandler({ sender, dataItem, rowIndex }) {
        if (this.canRemove(dataItem.ID)) {
            this.viewObject.elements.remove(rowIndex);
        }
        sender.cancelCell();
    }

    /**
     * 
     * @param id
     */
    private canRemove(id: string) {
        //  dbe
        if (this.isDbeElement(id)) {
            alert('');
            return false;
        }
        //  ID
        if (this.isIDElement(id)) {
            alert('ID');
            return false;
        }
        //  ParentID
        if (this.isParentIDElement(id)) {
            alert('ParentID');
            return false;
        }
        //  
        if (this.isRequestElement(id)) {
            return false;
        }
        return true;
    }

    /**
     * 
     */
    isRequestElement(id: string): boolean {
        const validationCountains = GSPBusinessEntity.countainValidationRequestElement(this.bizEntity, id);
        const determinationCountains = GSPBusinessEntity.countainDeterminationRequestElement(this.bizEntity, id);
        if (!isNullOrUndefined(validationCountains)) {
            alert(`[${validationCountains}]`);
            return true;
        }
        if (!isNullOrUndefined(determinationCountains)) {
            alert(`--[${determinationCountains}]`);
            return true;
        }
        return false;
    }

    /**
     * id
     * id,parentId,
     * @param dataItem 
     */
    private isIDType(dataItem: GSPBizEntityElement) {
        if (this.isIDElement(dataItem.ID) || this.isParentIDElement(dataItem.ID)
            || dataItem.ObjectType === GSPElementObjectType.Association) {
            return true;
        }
        return false;
    }

    /**
     * dbe
     * @param id
     */
    private isDbeElement(id: string) {
        const viewElement = <ViewElement>this.getViewElementById(id);
        const ele = viewElement.toJS() as GSPBizEntityElement;
        return ele.IsRef;
    }

    /**
     * id
     * @param id
     */
    private isIDElement(id: string) {
        if (isNullOrUndefined(this.currentObject.ColumnGenerateID)) {
            return false;
        } else {
            return id === this.currentObject.ColumnGenerateID.ElementID;
        }

    }

    /**
     * 
     * @param id
     */
    private isParentIDElement(id: string) {
        if (isNullOrUndefined(this.currentObject.Keys) || this.currentObject.Keys.length === 0) {
            return false;
        } else {
            const assoKey = this.currentObject.Keys.find(key => key.SourceElement === id);
            return assoKey != null;
        }
    }

    /**
     * 
     */
    public removeSelectedElement() {
        if (this.eleSelection.length === 0) { alert(''); } else {
            while (this.eleSelection.length !== 0) {
                const id = this.eleSelection.pop();
                if (this.canRemove(id)) {
                    this.viewObject.elements.removeByID(id);
                }
            }
        }
    }

    private createFormGroup(dataItem: any): FormGroup {
        return this.formBuilder.group({
            'Code': [dataItem.Code],
            'Name': [dataItem.Name],
            'LabelID': [dataItem.LabelID],
            // 'Code': [dataItem.Code, Validators.required],
            // 'Name': [dataItem.Name, Validators.required],
            // 'LabelID': [dataItem.LabelID, Validators.required],
            'Length': [dataItem.Length, Validators.compose([Validators.required, Validators.pattern('^[0-9]{1,4}')])],
            'Precision': [dataItem.Precision, Validators.compose([Validators.required, Validators.pattern('^[0-9]{1,3}')])],
            'DefaultValue': [dataItem.DefaultValue],
            'MDataType': this.getDataTypeWithUdt(dataItem),
            'ObjectType': dataItem.ObjectType,

            'ID': [dataItem.ID], // ID
            'Element': [dataItem], // 
        });
    }

    private getDataTypeWithUdt(ele: GSPBizEntityElement) {
        if (ele.IsUdt) {
            return BizElementConst.UnifiedDataType;
        } else {
            return ele.MDataType;
        }
    }

    /**
     * 
     * @param formGroup
     */
    private mergeFormGroupValue(formGroup: FormGroup): GSPBizEntityElement {
        if (isNullOrUndefined(formGroup)) {
            alert(''); // 
        }
        const ele = formGroup.value['Element'];
        ele.Code = formGroup.value['Code'];
        ele.Name = formGroup.value['Name'];
        ele.LabelID = formGroup.value['LabelID'];
        ele.Length = formGroup.value['Length'];
        ele.Precision = formGroup.value['Precision'];
        ele.MDataType = formGroup.value['MDataType'];
        ele.ObjectType = formGroup.value['ObjectType'];
        return ele;
    }

    /**
     * 
     * @param dataItem 
     */
    public HasAssociation(dataItem: any): boolean {
        const ele = dataItem as GSPBizEntityElement;
        if (ele) {
            if (ele.ChildAssociations) {
                return ele.ChildAssociations.length > 0;
            }
            return false;
        } else {
            return false;
        }
    }

