import { Component, OnInit, OnChanges, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';
import { IGSPCommonElement, GSPBizEntityObject, RequestChildElement, IGSPCommonObject } from '../../util/be-reference';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-be-determination-trigger-element-edit',
  templateUrl: './determination-trigger-element-edit.component.html',
  styleUrls: ['./determination-trigger-element-edit.component.css']
})
export class DeterminationTriggerElementEditComponent implements OnInit, OnChanges {

  /**
   * 
   */
  @Input() triggerElements: IGSPCommonElement[];
  @Input() currentObject: GSPBizEntityObject;
  @Input() triggerObject: IGSPCommonObject;

  private currentSelectObject: IGSPCommonObject;
  elements: IGSPCommonElement[];
  bizObjects: any[];
  active = false;
  expandedKeys: string[] = [];
  selectedKeys: string[] = [];

  @Output() cancel: EventEmitter<any> = new EventEmitter();
  @Output() save: EventEmitter<RequestChildElement> = new EventEmitter();
  /**
   * 
   */
  private selectableSettings: SelectableSettings = {
    mode: 'multiple'
  };

  /**
   * id
   */
  private eleSelection: string[] = [];

  constructor() { }

  ngOnInit() {

  }

  ngOnChanges(changes: SimpleChanges): void {

  }

  /**
   * 
   * @param refViewElements 
   * @param refModel 
   */
  load(triggerObject: IGSPCommonObject, triggerElements: IGSPCommonElement[], currentObject: GSPBizEntityObject) {
    this.active = true;
    this.triggerElements = triggerElements;
    // 
    this.currentSelectObject = triggerObject ? triggerObject : currentObject;
    this.selectedKeys = [this.currentSelectObject.ID];
    this.getChildrenObjects(currentObject);
    this.getObjectElements(this.currentSelectObject);
    this.getSelectElements(triggerElements);
  }

  getChildrenObjects(obj: GSPBizEntityObject): any {
    if (isNullOrUndefined(obj)) {
      return;
    }
    const rootNode = Object.assign(obj);
    if (isNullOrUndefined(obj.ContainChildObjects) || obj.ContainChildObjects.length === 0) {
      this.bizObjects = [rootNode];
      return;
    }
    rootNode.ContainChildObjects.forEach(
      item => item.ContainChildObjects = null
    );
    this.bizObjects = [rootNode];
    this.expandedKeys = ['0'];
  }

  clearChildNodes(obj: GSPBizEntityObject) {
    obj.ContainChildObjects = null;
  }
  /**
   * 
   * @param refModel 
   */
  private getObjectElements(currentObject: IGSPCommonObject): any {
    this.elements = currentObject.ContainElements;
  }

  /**
   * 
   */
  private getSelectElements(triggerElements: IGSPCommonElement[]): any {
    const selectionIds = new Array<string>();
    if (triggerElements && triggerElements.length > 0) {
      triggerElements.forEach(ele => {
        selectionIds.push(ele.ID);
      });
    }
    this.eleSelection = selectionIds;
  }


  public onConfirm(e): void {
    e.preventDefault();
    const requestChildElement = new RequestChildElement();
    // IDkey
    if (this.currentObject.ID !== this.currentSelectObject.ID) {
      requestChildElement.RequestChildElementKey = this.currentSelectObject.ID;
    }
    if (this.eleSelection.length === 0) {
      this.save.emit(null);
    } else {
      requestChildElement.RequestChildElementValue = this.eleSelection;
      this.save.emit(requestChildElement);
    }
    this.closeForm();
  }

  public onCancel(e): void {
    e.preventDefault();
    this.closeForm();
  }

  private closeForm(): void {
    this.active = false;
  }

  /**
   * 
   * @param node
   */
  public fetchChildren(node: any): Observable<any[]> {
    // nodeBE
    if (node.MainObject) {
      return of([node.MainObject]); // BEMainObject
    } else {
      return of(node.ContainChildObjects); // 
    }
  }

  /**
   * 
   * @param node
   */
  public hasChildren(node: any): boolean {
    // BE
    if (node.MainObject) {
      return true;
    } else {
      return node.ContainChildObjects && node.ContainChildObjects.length > 0;
    }
  }

  /**
   * ,
   * @param param0
   */
  public handleSelection(args: any): void {
    const { dataItem } = args;
    if (this.currentSelectObject.ID !== dataItem.ID) {
      this.currentSelectObject = dataItem;
      this.getObjectElements(dataItem);
      this.eleSelection = [];
    }
  }
}



