import { Component, OnInit, OnChanges, Input, ViewChild, SimpleChanges } from '@angular/core';
import {
  ViewModel, ViewObject, BizActionBase,
  ViewAuthFieldActionInfo, ViewAuthFieldActionInfoList, GSPBizEntityObject,
  GSPBusinessEntity, BizMgrAction, ViewAuthFiled, ViewAuthFiledInfoList, AuthorizationInfo, AuthFieldInfo
} from '../util/be-reference';
import { isNullOrUndefined } from 'util';
import { InternalMgrActionUtil } from '../util/internal-mgr-action-util';
import { AuthFieldGridComponent } from './auth-field-grid/auth-field-grid.component';
import { AuthOpGridComponent } from './auth-op-grid/auth-op-grid.component';

@Component({
  selector: 'app-auth-edit',
  templateUrl: './auth-edit.component.html',
  styleUrls: ['./auth-edit.component.css']
})
export class AuthEditComponent implements OnInit, OnChanges {

  @ViewChild('fieldGrid') fieldGrid: AuthFieldGridComponent;
  @ViewChild('opGrid') opGrid: AuthOpGridComponent;

  @Input() currentViewModel: ViewModel;

  /**
   * []
   */
  viewAuthFieldInfos: ViewAuthFiledInfoList;

  /**
   * []
   */
  authorizations: AuthorizationInfo[];

  /**
   * []--
   */
  mainObject: GSPBizEntityObject;

  /**
   * []-
   */
  bizActions: BizActionBase[];

  /**
   * []-
   */
  get viewAuthFieldInfo(): ViewAuthFiled {
    return this.viewAuthFieldInfos ? this.viewAuthFieldInfos.currentSelectItem : null;
  }

  constructor() { }

  ngOnInit() {
    this.load();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.load();
  }

  load() {
    if (isNullOrUndefined(this.currentViewModel)) {
      this.clearAll();
      return;
    }
    const be = this.currentViewModel.toJS();
    this.mainObject = be.MainObject;
    this.bizActions = this.getBizActions(be, this.mainObject);
    this.authorizations = be.Authorizations;
    this.viewAuthFieldInfos = this.currentViewModel.mainObject.authFields;
  }

  clearAll() {
    this.viewAuthFieldInfos = null;
    this.mainObject = null;
    this.bizActions = null;
  }

  /**
   * bizActionQueryMgrAction
   * @param be 
   * @param mainObj 
   */
  getBizActions(be: GSPBusinessEntity, mainObj: GSPBizEntityObject): BizActionBase[] {
    const actions = new Array<BizActionBase>();
    const queryMgrAction = this.getQueryMgrAction(be.BizMgrActions);
    const bizActions = mainObj.BizActions;
    if (queryMgrAction) {
      actions.push(queryMgrAction);
    }
    if (bizActions && bizActions.length !== 0) {
      actions.push(...bizActions);
    }
    return actions;
  }

  /**
   * beMgrAction
   * @param mgrActions
   */
  getQueryMgrAction(mgrActions: BizMgrAction[]): any {
    if (isNullOrUndefined(mgrActions) || mgrActions.length === 0) {
      return null;
    }
    for (let i = 0; i < mgrActions.length; i++) {
      if (InternalMgrActionUtil.isQueryMgrAction(mgrActions[i])) {
        return mgrActions[i];
      }
    }
    return null;
  }


  /**
   * []
   */
  addAuthFieldInfo() {
    this.fieldGrid.addAuthFieldInfo();
  }

  /**
   * []
   */
  removeAuthFieldInfo() {
    this.fieldGrid.removeAuthFieldInfo();
  }

  /**
   * []
   * @param index
   */
  changeSelectAuthFieldInfo(index: number) {
    // this.viewAuthFieldInfos.setSelectIndex(index);
  }

  /**
   * []
   */
  editAuthField() {
    this.fieldGrid.editAuthField();
  }

  /**
   * []bizElement
   */
  editBizElement() {
    this.fieldGrid.editBizElement();
  }

  /**
   * []
   */
  editAuthActionInfo() {
    this.opGrid.editAuthActionInfo();
  }

  /**
   * []
   */
  clearAuthActionInfo() {
    this.opGrid.clearAuthActionInfo();
  }
}



