import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ListBase } from '../base/listBase';
import { MapBase } from '../base/mapBase';

import { AuthorizationInfo } from '../../ref';

export class ViewAuthorizationInfoList extends ListBase<ViewAuthorizationInfo> {

  constructor(enumArray: AuthorizationInfo[] | List<any>) {
    super(enumArray);
    if (isNullOrUndefined(enumArray)) {
      return;
    }
    this.data = this.getViewAuthInfos(this.data);
  }

  getViewAuthInfos(authInfos: List<any>): any {
    if (isNullOrUndefined(authInfos)) {
      return;
    }

    let viewAuthInfos = authInfos;

    if (authInfos.size > 0) {
      for (let i = 0; i < authInfos.size; i++) {
        viewAuthInfos = viewAuthInfos.update(i, value => this.getViewAuthInfo(authInfos.get(i)));
      }
      return viewAuthInfos;
    }
  }

  getViewAuthInfo(authInfo: AuthorizationInfo): any {
    return new ViewAuthorizationInfo(authInfo);
  }
}

export class ViewAuthorizationInfo extends MapBase {

  /**
   * 
   * @param element
   */
  constructor(authInfo: AuthorizationInfo | Map<string, any>) {
    super(authInfo);
  }
}




