import { isNullOrUndefined } from 'util';
import { BizOperation } from '../validateUtil/ref';
import { ValidateUtil } from '../validateUtil/validateUtil';


/**
 * Operation
 */
export class ValidateBizOperation {

    /**
     * 
     */
    static Validate(operation: BizOperation): boolean {
        if (ValidateUtil.isEmptyString(operation.Code)) {
            alert('');
            return false;
        }
        if (ValidateUtil.isEmptyString(operation.Name)) {
            alert('');
            return false;
        }
        return true;
    }
}



