import { Component, OnInit, OnChanges, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';
import { DataBindingDirective, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';
import { IGSPCommonElement, GSPBizEntityObject, IGSPCommonObject } from '../../util/be-reference';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-biz-element-grid',
  templateUrl: './biz-element-grid.component.html',
  styleUrls: ['./biz-element-grid.component.css']
})
export class BizElementGridComponent implements OnInit, OnChanges {

  /**
   * 
   */
  @Input() triggerElementID: string;
  @Input() currentObject: GSPBizEntityObject;

  private currentSelectObject: IGSPCommonObject;
  elements: IGSPCommonElement[];
  bizObjects: any[];
  active = false;

  @Output() cancel: EventEmitter<any> = new EventEmitter();
  @Output() save: EventEmitter<string> = new EventEmitter();

  /**
   * id
   */
  private eleSelection: string[] = [];

  /**
   * 
   */
  public selectableSettings: SelectableSettings = {
    mode: 'single'
  };

  constructor() { }

  ngOnInit() {

  }

  ngOnChanges(changes: SimpleChanges): void {

  }

  /**
   * 
   * @param refViewElements 
   * @param refModel 
   */
  load() {
    this.active = true;
    // 
    this.currentSelectObject = this.currentObject;
    this.getObjectElements(this.currentSelectObject);
    this.getSelectElement(this.triggerElementID);
  }

  /**
   * 
   * @param refModel 
   */
  private getObjectElements(currentObject: IGSPCommonObject): any {
    this.elements = currentObject.ContainElements;
  }

  /**
   * 
   */
  private getSelectElement(triggerElementID: string): any {
    if (triggerElementID) {
      this.eleSelection = [triggerElementID];
    } else {
      this.eleSelection = [];
    }
  }


  private onConfirm(e): void {
    e.preventDefault();
    if (this.eleSelection.length === 0) {
      alert('');
      return;
    }
    const eleId = this.eleSelection.pop();
    this.save.emit(eleId);
    this.closeForm();
  }

  private onCancel(e): void {
    e.preventDefault();
    this.closeForm();
  }

  private closeForm(): void {
    this.active = false;
  }
}




