import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { GSPBizEntityElement, HttpUrlConst } from '../util/be-reference';

/**
 * udtElementudtChildElementsMapping
 */
@Injectable()
export class UdtElementChooseUdtService {

    constructor(private http: Http) {
    }

    currentUrl = HttpUrlConst.currentUrl;

    /**
     * udt
     */
    chooseUdt(refUdtId: string, ele: GSPBizEntityElement): Observable<any> {
        const refUdtInfo = new UpdateElementWithUdtInfo();
        refUdtInfo.refUdtId = refUdtId;
        refUdtInfo.path = HttpUrlConst.currentPath;
        refUdtInfo.udtElementJson = JSON.stringify(ele);
        const chooseUdtUrl = `${this.currentUrl}` + `/api/dev/main/v1.0/becodegenerate/chooseUdt`;
        return this.http.put(chooseUdtUrl, refUdtInfo);
    }
}

/**
 * jit
 */
class UpdateElementWithUdtInfo {
    refUdtId: string;
    path: string;
    udtElementJson: string;
}



