
import { BizOperation } from './BizOperation';
import { BEOperationType } from '../Enum/BEOperationType';
import { AuthType } from '../Enum/AuthType';
import { BizParameterBase } from './Parameter/BizParameterBase';
import { BizReturnValue } from './ReturnValue/BizReturnValue';

/**
* BE
* @author wangjiegj<wangjiegj@inspur.com>
 */


/**
* BE
*/
export abstract class BizActionBase extends BizOperation {
    constructor() {
        super();
    }

    /**
     * 
     */
    get OpType() {
        return BEOperationType.BizAction;
    }

    /**
     * 
     */
    public CurentAuthType: AuthType;

    /**
     * ID
     */
    public OpIdList: string[];

    /**
     * 
     */
    public abstract Parameters: BizParameterBase[];

    /**
     * 
     */
    public ReturnValue: BizReturnValue;
}



