import { Component, OnInit, OnChanges, Input, ViewChild, SimpleChanges, Output, EventEmitter } from '@angular/core';
import {
  GSPBizEntityObject, ViewAuthFiledInfoList, ViewAuthFiled, AuthFieldInfo,
  GSPBizEntityElement, AuthorizationInfo, AuthConst, AuthEntryEntity, BizElementConst
} from '../../util/be-reference';
import { isNullOrUndefined } from 'util';
import { BizElementGridComponent } from '../biz-element-grid/biz-element-grid.component';

@Component({
  selector: 'app-auth-field-grid',
  templateUrl: './auth-field-grid.component.html',
  styleUrls: ['./auth-field-grid.component.css']
})
export class AuthFieldGridComponent implements OnInit, OnChanges {

  @ViewChild('bizElementGrid') bizElementGrid: BizElementGridComponent;

  /**
   * []
   */
  @Input() viewAuthFieldInfos: ViewAuthFiledInfoList;

  /**
   * []--
   */
  @Input() mainObject: GSPBizEntityObject;

  /**
   * []-
   */
  @Input() authorizations: AuthorizationInfo[];

  /**
   * 
   */
  @Output() changeSelectAuthFieldIndex: EventEmitter<number> = new EventEmitter<number>();

  authFiedlds: AuthFieldInfo[];
  authFieldSelection = [];

  get currentSelectIndex(): number {
    if (this.authFieldSelection.length === 0) {
      return -1;
    }
    const index = this.authFieldSelection[0];
    return index;
  }

  set currentSelectIndex(index: number) {
    this.viewAuthFieldInfos.setSelectIndex(index);
    this.authFieldSelection = [index];
  }

  get currentSelectAuthFieldInfo(): AuthFieldInfo {
    const index = this.currentSelectIndex;
    if (this.currentSelectIndex === -1) {
      return null;
    }
    return this.authFiedlds[index];
  }

  get triggerElementID(): string {
    if (this.currentSelectAuthFieldInfo) {
      return this.currentSelectAuthFieldInfo.ElementID;
    } else {
      return null;
    }
  }

  /**
   * []
   * 
   */
  get currentAuthEntry(): AuthEntryEntity[] {
    return null;
  }

  /**
   * 
   */
  get authorizationIds(): string[] {
    if (this.authorizations && this.authorizations.length > 0) {
      const ids = new Array<string>();
      this.authorizations.forEach(authInfo => {
        ids.push(authInfo.ID);
      });
      return ids;
    }
    return null;
  }

  /**
   * 
   */
  showAuthFieldWindow = false;

  constructor() { }

  ngOnInit() {
    this.load();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.load();
  }

  load() {
    if (isNullOrUndefined(this.viewAuthFieldInfos)) {
      this.authFiedlds = [];
      return;
    }
    this.authFiedlds = this.viewAuthFieldInfos.toJS();
    this.viewAuthFieldInfos.refreshView.subscribe(vm => {
      this.load();
    });
  }

  /**
   * [][]
   */
  getAuthorizationName(dataItem: AuthFieldInfo) {
    if (this.authorizations && dataItem && dataItem.AuthorizationID) {
      const auth = this.authorizations.find(au => au.ID === dataItem.AuthorizationID);
      return auth ? auth.Name : '';
    }
    return '';
  }

  /**
   * 
   */
  getBizElementName(dataItem: AuthFieldInfo) {
    if (this.mainObject && this.mainObject.ContainElements && dataItem && dataItem.ElementID) {
      const element = this.mainObject.ContainElements.find(ele => ele.ID === dataItem.ElementID);
      return element ? element.Name : '';
    }
    return '';
  }

  /**
   * 
   */
  selectionChangeHandler(args: any) {
    const { index } = args;
    this.currentSelectIndex = index;
  }


  /**
   * []
   */
  addAuthFieldInfo() {
    if (isNullOrUndefined(this.authorizations) || this.authorizations.length === 0) {
      alert('[Tab][]');
      return;
    }
    const newAuthField = new AuthFieldInfo();
    this.viewAuthFieldInfos.add(new ViewAuthFiled(newAuthField));
    this.currentSelectIndex = this.viewAuthFieldInfos.length - 1;
    // []
    this.editAuthField();
  }

  removeAuthFieldInfo(): any {
    if (this.currentSelectIndex === -1) {
      alert('');
    } else {
      this.viewAuthFieldInfos.remove(this.currentSelectIndex);
    }
  }

  /**
   * []
   */
  editAuthField() {
    if (this.currentSelectIndex === -1) {
      alert('');
      return;
    } else {
      this.showAuthFieldWindow = !this.showAuthFieldWindow;
    }
  }

  /**
   * []bizElement
   */
  editBizElement() {
    if (this.currentSelectIndex === -1) {
      alert('[]');
      return;
    }
    this.bizElementGrid.load();
  }

  /**
   * 
   * @param eleId
   */
  selectBizElement(eleId: string) {
    if (this.currentSelectIndex !== -1) {
      const newInfo = new AuthFieldInfo();
      newInfo.ElementID = eleId;
      this.viewAuthFieldInfos.update(this.currentSelectIndex, oldValue => {
        const oldInfo = <AuthFieldInfo>oldValue.toJS();
        if (oldInfo) {
          newInfo.AuthFieldID = oldInfo.AuthFieldID;
          newInfo.AuthFieldName = oldInfo.AuthFieldName;
          newInfo.AuthorizationID = oldInfo.AuthorizationID;
        }
        newInfo.ActionInfos = [];
        return new ViewAuthFiled(newInfo);
      });
    }
  }

  /**
   * 
   * @param result
   */
  getAuthEntry(result: AuthEntryEntity[]) {
    if (isNullOrUndefined(result) || result.length === 0) {
      this.viewAuthFieldInfos.update(this.currentSelectIndex, oldValue => {
        return new ViewAuthFiled(null);
      });
      return;
    }
    const newValue = result[0];
    const newInfo = new AuthFieldInfo();
    newInfo.AuthFieldID = newValue.authField;
    newInfo.AuthFieldName = newValue.authFieldName;
    newInfo.AuthorizationID = newValue.authorizationId;
    this.viewAuthFieldInfos.update(this.currentSelectIndex, oldValue => {
      if (oldValue.toJS()) {
        newInfo.ElementID = (<AuthFieldInfo>oldValue.toJS()).ElementID;
      }
      newInfo.ActionInfos = [];
      return new ViewAuthFiled(newInfo);
    });
  }

  cellClickHandler(args: any) {
    const { sender, rowIndex, columnIndex, column, dataItem, isEdited, isNew } = args;

    //  
    switch (column.field) {
      // case AuthConst.AuthFieldName:
      // case 'AuthorizationName':
      //   this.editAuthField();
      //   break;
      case 'BizElementName':
        this.editBizElement();
        break;
    }
  }
}



