import { List, Map } from 'immutable';

import { ViewOperationList } from './viewOperationList';
import { ViewValidation } from './viewValidation';

import { Validation } from '../../ref';

export class ViewValidationList extends ViewOperationList {

    constructor(validationArray: Validation[] | List<any>) {
        super(validationArray);
    }

    protected getViewOperation(action: Map<string, any>): ViewValidation {
        return new ViewValidation(action);
    }
}



