import { ListBase } from '../base/listBase';
import { MapBase } from '../base/mapBase';
import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ViewOperation } from './viewOperation';
import { BizActionBase } from '../../../das/BizEntity/src/Operation/BizActionBase';
import { BizMgrAction } from '../../../das/BizEntity/src/Operation/BizMgrAction';
import { ViewParameterListBase, ViewBizActionParaList, ViewBizMgrActionParaList } from './viewParameters/viewParameterList';
import { ViewReturnValue } from './viewParameters/viewReturnValue';

import { BizAction, AuthType, BizReturnValue, BizOperationConst } from '../../ref';

export abstract class ViewActionBase extends ViewOperation {

    constructor(action: BizActionBase | Map<string, any>) {
        super(action);
        this.update(BizOperationConst.OpIdList, values => this.getOpIdList(values));
        this.update(BizOperationConst.Parameters, values => this.getParameterList(values));
        this.update(BizOperationConst.ReturnValue, value => this.getViewReturnValue(value));
    }

    private getOpIdList(opIds: List<string>): ListBase<MapBase> {
        return new ListBase<MapBase>(opIds);
    }

    protected abstract getParameterList(paras: List<any>): ViewParameterListBase;

    getViewReturnValue(value: BizReturnValue): any {
        return new ViewReturnValue(value);
    }

    get CurentAuthType(): AuthType {
        return this.get(BizOperationConst.CurentAuthType);
    }
    set CurentAuthType(value: AuthType) {
        this.set(BizOperationConst.CurentAuthType, value);
    }

    get OpIdList(): ListBase<MapBase> {
        return this.get(BizOperationConst.OpIdList);
    }
    set OpIdList(value: ListBase<MapBase>) {
        this.set(BizOperationConst.OpIdList, value);
    }

    get ReturnValue(): ViewReturnValue {
        const returnValue = this.get(BizOperationConst.ReturnValue);
        // if (isNullOrUndefined(returnValue) || isNullOrUndefined(returnValue.toJS())) {
        //     this.set(BizOperationConst.ReturnValue, new ViewReturnValue(new BizVoidReturnType()));
        // }
        return returnValue;
    }
    set ReturnValue(value: ViewReturnValue) {
        this.set(BizOperationConst.ReturnValue, value);
    }

    abstract get Parameters(): ViewParameterListBase;

    abstract set Parameters(value: ViewParameterListBase);
}

export class ViewBizAction extends ViewActionBase {

    constructor(action: BizAction | Map<string, any>) {
        super(action);
    }

    protected getParameterList(paras: List<any>): ViewParameterListBase {
        return new ViewBizActionParaList(paras);
    }

    get Parameters(): ViewParameterListBase {
        const paras = this.get(BizOperationConst.Parameters);
        if (isNullOrUndefined(paras)) {
            this.set(BizOperationConst.Parameters, new ViewBizActionParaList(null));
        }
        return paras;
    }

    set Parameters(value: ViewParameterListBase) {
        this.set(BizOperationConst.Parameters, value);
    }
}

export class ViewBizMgrAction extends ViewActionBase {

    constructor(action: BizMgrAction | Map<string, any>) {
        super(action);
    }

    protected getParameterList(paras: List<any>): ViewParameterListBase {
        return new ViewBizMgrActionParaList(paras);
    }

    get Parameters(): ViewParameterListBase {
        const paras = this.get(BizOperationConst.Parameters);
        if (isNullOrUndefined(paras)) {
            this.set(BizOperationConst.Parameters, new ViewBizMgrActionParaList(null));
        }
        return paras;
    }

    set Parameters(value: ViewParameterListBase) {
        this.set(BizOperationConst.Parameters, value);
    }
}



