import {
    GSPBusinessEntity, GSPBizEntityObject, Guid, DataObjectType,
    GSPElementObjectType, GSPBizEntityElement, GSPElementDataType, GSPColumnGenerate, GSPAssociationKey
} from '../../util/be-reference';
import { InitBizObject } from './initialBizObject';
import { isNullOrUndefined } from 'util';

/**
 * 
 */
export class InitChildObject {
    /**
     * 
     */
    static initChildObject(parentObject: GSPBizEntityObject): GSPBizEntityObject {
        const newObject = InitBizObject.initBizObject();

        // id
        const parentIDElement = this.initParentIDElement();
        newObject.ContainElements.push(parentIDElement);

        // 
        const key = new GSPAssociationKey();
        const parentIdElement = this.getIDElement(parentObject);
        key.TargetElement = parentIdElement.ID;
        key.TargetElementDisplay = parentIdElement.Name;
        key.SourceElement = parentIDElement.ID;
        key.SourceElementDisplay = parentIDElement.Name;
        if (isNullOrUndefined(newObject.Keys)) {
            newObject.Keys = new Array<GSPAssociationKey>();
        }
        newObject.Keys.push(key);
        return newObject;
    }

    /**
     * parentID
     */
    private static initParentIDElement(): GSPBizEntityElement {
        const parentIDElement = new GSPBizEntityElement();
        parentIDElement.ID = Guid.newGuid().toString();
        parentIDElement.Code = 'ParentID';
        parentIDElement.Name = 'ParentID';
        parentIDElement.LabelID = 'ParentID';
        parentIDElement.MDataType = GSPElementDataType.String;
        parentIDElement.ObjectType = GSPElementObjectType.None;
        parentIDElement.Length = 36;
        parentIDElement.Precision = 0;
        return parentIDElement;
    }

    /**
     * 
     */
    private static getIDElement(bizObject: GSPBizEntityObject): GSPBizEntityElement {
        if (bizObject.ColumnGenerateID != null) {
            return bizObject.ContainElements.find(ele => ele.ID === bizObject.ColumnGenerateID.ElementID) as GSPBizEntityElement;
        }
        return null;
    }
}



