import { Component, OnInit, Input, OnChanges, OnDestroy, SimpleChanges, ViewChild, } from '@angular/core';
import {
  BizAction, GSPBizEntityObject,
  BizOperationConst, BizOperation, ViewOperationList
} from '../../util/be-reference';
import { isNullOrUndefined } from 'util';
import { Subscription } from 'rxjs/Subscription';
import { DataBindingDirective, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';
import { InternalBeActionUtil } from '../../util/internal-biz-action-util';
import { InternalMgrActionUtil } from '../../util/internal-mgr-action-util';

/**
 * 
 */
@Component({
  selector: 'app-base-operation-grid',
  templateUrl: './base-operation-grid.component.html',
  styleUrls: ['./base-operation-grid.component.css']
})
export class BaseOperationGridComponent implements OnInit, OnChanges, OnDestroy {

  @ViewChild('grid') grid: GridComponent;
  /**
   * Action
   */
  @Input() viewOperations: ViewOperationList;
  public operations: BizOperation[];
  @Input() columnName: string;
  /**
   * id
   */
  public operationSelection: string[] = [];
  protected subscriptions: Subscription[];
  constructor() { this.subscriptions = new Array<Subscription>(); }

  ngOnInit() {
    this.refresh();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['viewOperations']) {
      this.refresh();
    }
  }

  ngOnDestroy(): void {
    this.unsubscribeRefreshView();
  }
  /**
   * 
   */
  protected refresh() {
    if (isNullOrUndefined(this.viewOperations)) {
      this.operations = [];
      return;
    }
    this.unsubscribeRefreshView();
    this.operations = this.viewOperations.toJS();
    // BaseOperationComponentComponent
    if (!isNullOrUndefined(this.operations) && this.operations.length > 0) {
      this.operationSelection = [this.operations[0].ID];
    }
    this.subscribeRefreshViewLayer();
    this.subscribeSelectNewItem();
    this.subscribeChildViewEvent();
  }

  /**
   * 
   * @param args 
   */
  public selectionChangeHandler(args: any) {
    const { selectedRows } = args;
    if (selectedRows.length > 0) {
      const dataItem = selectedRows[0].dataItem;
      this.viewOperations.setSelectIndexByID(dataItem.ID);
    }
  }

  public remove() {
    const dataItem = this.viewOperations.currentSelectItem.toJS() as BizOperation;
    if (this.isDbeOperation(dataItem)) {
      alert('');
      return;
    }
    if (this.isInternalAction(dataItem)) {
      alert('');
      return;
    }
    const id = dataItem.ID;
    const rowIndex = this.viewOperations.getIndexByID(id);
    this.viewOperations.removeByID(id);

    // ;;
    // -1
    if (this.viewOperations.length > 0) {
      if (rowIndex > 0) {
        this.viewOperations.setSelectIndex(rowIndex - 1);
      } else {
        this.viewOperations.setSelectIndex(rowIndex);
      }
    } else {
      this.viewOperations.setSelectIndex(-1);
    }
  }

  private isDbeOperation(dataItem: BizOperation): boolean {
    return dataItem.IsRef;
  }

  private isInternalAction(dataItem: BizOperation): boolean {
    return InternalMgrActionUtil.isInternalMgrAction(dataItem) || InternalBeActionUtil.isInternalBizAction(dataItem);
  }
  // /**
  //  * 
  //  * @param dataItem 
  //  */
  // public removeHandler(dataItem: BizOperation) {
  //   this.viewOperations.removeByID(dataItem.ID);
  //   this.refresh();
  //   const rowIndex = this.viewOperations.getIndexByID(dataItem.ID);

  //   // ;;
  //   // -1
  //   if (this.viewOperations.length > 0) {
  //     if (rowIndex > 0) {
  //       this.viewOperations.setSelectIndex(rowIndex - 1);
  //     } else {
  //       this.viewOperations.setSelectIndex(rowIndex);
  //     }
  //   } else {
  //     this.viewOperations.setSelectIndex(-1);
  //   }
  // }

  /**
   * 
   */
  private subscribeSelectNewItem(): any {
    // 
    const subscription =
      this.viewOperations.selectNewItem.subscribe(
        item => {
          if (isNullOrUndefined(item)) {
            return;
          }
          this.operationSelection = [item.get(BizOperationConst.ID)];
        }
      );
    // 
  }

  /**
   * viewAction
   */
  private subscribeRefreshViewLayer(): any {
    // 
    const subscription = this.viewOperations.refreshView.subscribe(
      viewOperations => {
        if (isNullOrUndefined(viewOperations)) {
          return;
        } else {
          this.operations = viewOperations.toJS();
        }
      }
    );

    this.subscriptions.push(subscription);

  }

  /**
   * 
   */
  protected subscribeChildViewEvent(): any {

  }

  /**
   * 
   */
  private unsubscribeRefreshView() {
    if (this.subscriptions) {
      while (this.subscriptions.length > 0) {
        const subscription = this.subscriptions.pop();
        subscription.unsubscribe();
      }
    }
  }
}



