import { Component, OnInit, OnDestroy, OnChanges, ViewChild, ElementRef, TemplateRef, Input, SimpleChanges } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { GridComponent } from '@progress/kendo-angular-grid';

import { BaseOperationGridComponent } from '../../base-operation-component/base-operation-grid/base-operation-grid.component';
import {
  Determination, GSPComponent, BEMethod, RequestNodeTriggerType, BizOperationConst,
  ViewDeterminationList, BETriggerTimePointType, ViewOperationList, ViewOperation, SelectedComponentInfo, MetadataTypeName
} from '../../util/be-reference';
import {
  PopupService,
  PopupRef
} from '@progress/kendo-angular-popup';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';
import { ComponentGeneratorChooseComponent } from '../../refModule/component-generator-choose/component-generator-choose.component';

/**
 * 
 * 
 * code,name,description
 */
@Component({
  selector: 'app-be-base-determination-grid',
  templateUrl: './base-determination-grid.component.html',
  styleUrls: ['./base-determination-grid.component.css']
})
export class BaseDeterminationGridComponent extends BaseOperationGridComponent implements OnInit, OnChanges, OnDestroy {

  @ViewChild('grid') grid: GridComponent;
  @ViewChild('componentSelector') componentSelector: ComponentGeneratorChooseComponent;
  /**
   * 
   */
  public components: GSPComponent[];

  /**
   * Action
   */
  viewDeterminations: ViewOperationList;

  /**
   * 
   */
  popupRef: PopupRef;
  /**
   * RequestNodeTriggerType
   * [][]
   */
  @Input() showNodeTriggerType = false;
  @Input() beTriggerTimePointType: BETriggerTimePointType = BETriggerTimePointType.AfterLoading;
  opType = 'Determination';
  compName: string;
  /**
   * 
   */
  get determinations(): Determination[] {
    if (isNullOrUndefined(this.operations)) {
      return null;
    }

    return this.operations.filter(dtm => (<Determination>dtm).TriggerTimePointType === this.beTriggerTimePointType) as Determination[];
  }

  get isAfterModify(): boolean {
    return this.beTriggerTimePointType === BETriggerTimePointType.AfterModify;
  }

  ngOnDestroy(): void {
    super.ngOnDestroy();
    this.popupClose();

  }

  ngOnChanges(changes: SimpleChanges) {
    // viewDeterminations
    // viewOperations
    // 
    this.grid.closeCell();
    this.viewDeterminations = this.viewOperations;

    super.ngOnChanges(changes);
    // 
    if (isNullOrUndefined(this.viewDeterminations)) {
      return;
    }
    // Tab,
    if (changes['beTriggerTimePointType']) {
      if (isNullOrUndefined(this.determinations)) {
        return;
      }
      if (this.determinations.length > 0) {
        this.viewDeterminations.setSelectIndexByID(this.determinations[0].ID);
        this.operationSelection = [this.determinations[0].ID];
      }
    }
  }

  constructor(private formBuilder: FormBuilder, private popupService: PopupService) {
    super();
  }

  /**
   * 
   * @param dataItem 
   */
  getComponenetName(dataItem: Determination) {
    // const cmpId = dataItem.ComponentId;
    // const cmp = this.componentService.getComponentById(cmpId);
    // if (isNullOrUndefined(cmp)) {
    //   return '';
    // }
    // return cmp.ComponentName;
  }

  /**
   * 
   * @param param0 
   */
  cellClickHandler(args: any) {
    const { sender, rowIndex, columnIndex, column, dataItem, isEdited, isNew } = args;
    this.viewDeterminations.setSelectIndexByID(dataItem.ID);

    // dbeOp
    if (this.isDbeOp()) {
      // alert('');
      return;
    }

    if (column.field === 'ComponentName'
      && (isNullOrUndefined(dataItem.ComponentName) || !this.viewDeterminations.currentSelectItem.IsGenerateComponent)) {
      this.componentSelector.load(MetadataTypeName.DeterminationComponent);
    }
    if (!isEdited) {
      sender.editCell(rowIndex, columnIndex, this.createFormGroup(dataItem));
      return;
    }
  }

  /**
   * 
   * @param args 
   */
  cellCloseHandler(args: any) {
    const { sender, formGroup, dataItem, rowIndex, column } = args;

    if (isNullOrUndefined((<FormGroup>formGroup).controls[column.field])) {
      return;
    }
    if (!(<FormGroup>formGroup).controls[column.field].valid) {
      // 
      args.preventDefault();
    } else if (formGroup.dirty) {

      const propertyName = column.field;
      const newValue = formGroup.value[propertyName];
      this.viewDeterminations.updateByID(dataItem.ID, elementView => {
        elementView.update(propertyName, oldValue => newValue);
        return elementView;
      });
    }

    // 
    if (column.field === 'ComponentId') {
      this.popupClose();
    }
  }

  protected createFormGroup(dataItem: any): FormGroup {
    // // 
    // this.components = this.componentService.getComponents();

    return this.formBuilder.group({
      'Code': [dataItem.Code],
      'Name': [dataItem.Name],
      // 'ComponentId': [this.getComponenetName(dataItem)],
      'Description': [dataItem.Description],
    });
  }

  /**
   * /
   * @param anchor 
   * @param template 
   */
  cmpTogglePopup(anchor: ElementRef, template: TemplateRef<any>) {
    if (this.popupRef) {
      this.popupClose();
    } else {
      this.popupRef = this.popupService.open({
        anchor: anchor,
        content: template
      });
    }
  }

  /**
   * 
   */
  popupClose() {
    if (isNullOrUndefined(this.popupRef)) {
      return;
    }
    this.popupRef.close();
    this.popupRef = null;
  }

  /**
   * 
   * @param selectComponent 
   */
  confirmSelectComponent(selectComponent: GSPComponent) {
    this.popupClose();

    if (selectComponent.ComponentID === this.viewDeterminations.currentSelectItem.ComponentId) {
      return;
    } else {
      this.changeComponent(selectComponent);
    }
  }

  /**
   * 
   * @param selectComponent 
   */
  changeComponent(selectComponent: GSPComponent) {
    // this.viewDeterminations.currentSelectItem.ComponentId = selectComponent.ComponentID;
    // // this.componentService.addComponent(selectComponent);

    // this.changeMethod(selectComponent.Method);
  }

  /**
   * 
   * @param selectMethod 
   */
  changeMethod(selectMethod: BEMethod) {
    // if (isNullOrUndefined(selectMethod)) {
    //   this.viewDeterminations.currentSelectItem.Code = '';
    //   this.viewDeterminations.currentSelectItem.Name = '';
    //   this.viewDeterminations.currentSelectItem.Description = '';
    //   return;
    // }

    // this.viewDeterminations.currentSelectItem.Code = selectMethod.MethodName;
    // this.viewDeterminations.currentSelectItem.Name = selectMethod.MethodName;
    // this.viewDeterminations.currentSelectItem.Description = selectMethod.MethodDescription;
  }

  /**
   * 
   * @param triggerType 
   */
  getCreatedTrigger(triggerType: RequestNodeTriggerType): boolean {
    // tslint:disable-next-line:no-bitwise
    return (triggerType & RequestNodeTriggerType.Created) === RequestNodeTriggerType.Created;
  }

  getUpdatedTrigger(triggerType: RequestNodeTriggerType): boolean {
    // tslint:disable-next-line:no-bitwise
    return (triggerType & RequestNodeTriggerType.Updated) === RequestNodeTriggerType.Updated;
  }

  getDeletedTrigger(triggerType: RequestNodeTriggerType): boolean {
    // tslint:disable-next-line:no-bitwise
    return (triggerType & RequestNodeTriggerType.Deleted) === RequestNodeTriggerType.Deleted;
  }

  setCreatedTrigger(created: boolean, dataItem: Determination): any {
    if (isNullOrUndefined(dataItem.RequestNodeTriggerType)) {
      dataItem.RequestNodeTriggerType = RequestNodeTriggerType.None;
    }
    if (created) {
      dataItem.RequestNodeTriggerType += RequestNodeTriggerType.Created;
    } else {
      dataItem.RequestNodeTriggerType -= RequestNodeTriggerType.Created;
    }
    this.viewDeterminations.updateByID(dataItem.ID, viewDtm => {
      viewDtm.set(BizOperationConst.RequestNodeTriggerType, dataItem.RequestNodeTriggerType);
      return viewDtm;
    });
  }

  setUpdatedTrigger(updated: boolean, dataItem: Determination): any {
    if (isNullOrUndefined(dataItem.RequestNodeTriggerType)) {
      dataItem.RequestNodeTriggerType = RequestNodeTriggerType.None;
    }

    if (updated) {
      dataItem.RequestNodeTriggerType += RequestNodeTriggerType.Updated;
    } else {
      dataItem.RequestNodeTriggerType -= RequestNodeTriggerType.Updated;
    }
    this.viewDeterminations.updateByID(dataItem.ID, viewDtm => {
      viewDtm.set(BizOperationConst.RequestNodeTriggerType, dataItem.RequestNodeTriggerType);
      return viewDtm;
    });
  }

  setDeletedTrigger(deleted: boolean, dataItem: Determination): any {
    if (isNullOrUndefined(dataItem.RequestNodeTriggerType)) {
      dataItem.RequestNodeTriggerType = RequestNodeTriggerType.None;
    }
    if (deleted) {
      dataItem.RequestNodeTriggerType += RequestNodeTriggerType.Deleted;
    } else {
      dataItem.RequestNodeTriggerType -= RequestNodeTriggerType.Deleted;
    }
    this.viewDeterminations.updateByID(dataItem.ID, viewDtm => {
      viewDtm.set(BizOperationConst.RequestNodeTriggerType, dataItem.RequestNodeTriggerType);
      return viewDtm;
    });
  }

  public remove() {
    if (this.isDbeOp()) {
      alert('');
      return;
    }
    const id = this.viewDeterminations.currentSelectItem.ID;
    const index = this.viewDeterminations.getIndexByID(id);
    this.viewOperations.removeByID(id);

    // ;;
    // -1
    if (this.determinations.length > 0) {
      if (index > 0) {
        const newDtmId = this.determinations[index - 1].ID;
        this.viewDeterminations.setSelectIndexByID(newDtmId);
      } else {
        const newDtmId = this.determinations[index].ID;
        this.viewDeterminations.setSelectIndexByID(newDtmId);
      }
    } else {
      this.viewDeterminations.setSelectIndex(-1);
    }
  }

  isDbeOp() {
    const item = this.viewDeterminations.currentSelectItem;
    if (item && item.toJS() && (<Determination>item.toJS()).IsRef) {
      return true;
    }
    return false;
  }
  // /**
  //  * 
  //  * @param args 
  //  */
  // public removeHandler(dataItem: Determination) {
  //   const index = this.determinations.indexOf(dataItem);
  //   this.viewDeterminations.removeByID(dataItem.ID);

  //   // ;;
  //   // -1
  //   if (this.determinations.length > 0) {
  //     if (index > 0) {
  //       const newDtmId = this.determinations[index - 1].ID;
  //       this.viewDeterminations.setSelectIndexByID(newDtmId);
  //     } else {
  //       const newDtmId = this.determinations[index].ID;
  //       this.viewDeterminations.setSelectIndexByID(newDtmId);
  //     }
  //   } else {
  //     this.viewDeterminations.setSelectIndex(-1);
  //   }
  // }

  /**
   * 
   */
  protected subscribeChildViewEvent(): any {
    // 
  }

  /**
   * 
   * @param dataItem 
   */
  upwardDetermination(dataItem: Determination) {
    // const index = this.viewDeterminations.getIndexByID(dataItem.ID);
    // const index = this.determinations.indexOf(dataItem);
    const index = this.determinations.findIndex(dtm => dtm.ID === dataItem.ID);

    // 
    if (this.determinations.length > 0) {
      if (index > 0) {
        const newDtmId = this.determinations[index - 1].ID;
        (<ViewDeterminationList>this.viewDeterminations).changeIndexByIds(newDtmId, dataItem.ID);
        this.viewDeterminations.setSelectIndexByID(dataItem.ID);
      } else {
        return;
      }
    }
  }

  /**
   * 
   * @param dataItem 
   */
  downwardDetermination(dataItem: Determination) {
    this.viewDeterminations.setSelectIndexByID(dataItem.ID);
    const index = this.determinations.findIndex(dtm => dtm.ID === dataItem.ID);

    // 
    if (this.determinations.length > 0) {
      if (index < this.determinations.length - 1) {
        const newDtmId = this.determinations[index + 1].ID;
        (<ViewDeterminationList>this.viewDeterminations).changeIndexByIds(newDtmId, dataItem.ID);
        this.viewDeterminations.setSelectIndexByID(dataItem.ID);
      } else {
        return;
      }
    }
  }

  /**
   * 
   * @param dataItem 
   */
  topwardDetermination(dataItem: Determination) {
    this.viewDeterminations.setSelectIndexByID(dataItem.ID);
    const index = this.determinations.findIndex(dtm => dtm.ID === dataItem.ID);

    // 
    if (this.determinations.length > 0) {
      if (index > 0) {
        const newDtmId = this.determinations[0].ID;
        (<ViewDeterminationList>this.viewDeterminations).changeIndexByIds(newDtmId, dataItem.ID);
        this.viewDeterminations.setSelectIndexByID(dataItem.ID);
      } else {
        return;
      }
    }
  }

  /**
   * 
   * @param dataItem 
   */
  bottomwardDetermination(dataItem: Determination) {
    this.viewDeterminations.setSelectIndexByID(dataItem.ID);
    const index = this.determinations.findIndex(dtm => dtm.ID === dataItem.ID);

    // 
    if (this.determinations.length > 0) {
      if (index < this.determinations.length - 1) {
        const newDtmId = this.determinations[this.determinations.length - 1].ID;
        (<ViewDeterminationList>this.viewDeterminations).changeIndexByIds(newDtmId, dataItem.ID);
        this.viewDeterminations.setSelectIndexByID(dataItem.ID);
      } else {
        return;
      }
    }
  }
  /**
 * 
 * @param cmpMetadata 
 */
  excuteAfterGetMetadata(selectedCmpInfo: SelectedComponentInfo) {
    this.updateOperationInfo(selectedCmpInfo);
  }

  /**
   * Operation
   * @param cmpMetadata 
   */
  updateOperationInfo(selectedCmpInfo: SelectedComponentInfo) {
    const id = this.viewDeterminations.currentSelectItem.ID;
    this.viewDeterminations.updateByID(id, elementView => {
      elementView.update('ComponentId', oldValue => selectedCmpInfo.component.ComponentID);
      elementView.update('ComponentName', oldValue => selectedCmpInfo.cmpFileName);
      elementView.update('ComponentPkgName', oldValue => selectedCmpInfo.cmpPkgName);
      elementView.update('IsGenerateComponent', oldValue => false);
      return elementView;
    });
    const op = this.viewDeterminations.getItemByID(id) as ViewOperation;
    if (isNullOrEmptyString(op.Code)) {
      this.viewDeterminations.updateByID(id, elementView => {
        elementView.update('Code', oldValue => selectedCmpInfo.component.ComponentCode);
        return elementView;
      });
    }
    if (isNullOrEmptyString(op.Name)) {
      this.viewDeterminations.updateByID(id, elementView => {
        elementView.update('Name', oldValue => selectedCmpInfo.component.ComponentName);
        return elementView;
      });
    }
    if (isNullOrEmptyString(op.Description)) {
      this.viewDeterminations.updateByID(id, elementView => {
        elementView.update('Description', oldValue => selectedCmpInfo.component.ComponentDescription);
        return elementView;
      });
    }
  }
}



