import { List, Map } from 'immutable';

import { ViewOperationList } from './viewOperationList';
import { ViewBizAction, ViewBizMgrAction, ViewActionBase } from './viewActionBase';

import { BizAction } from '../../ref';

abstract class ViewActionBaseList extends ViewOperationList {

    constructor(bizActionArray: BizAction[] | List<any>) {
        super(bizActionArray);
    }

    protected abstract getViewOperation(action: Map<string, any>): ViewActionBase;
}

export class ViewBizActionList extends ViewActionBaseList {
    protected getViewOperation(action: Map<string, any>): ViewActionBase {
        return new ViewBizAction(action);
    }
}

export class ViewBizMgrActionList extends ViewActionBaseList {
    protected getViewOperation(action: Map<string, any>): ViewActionBase {
        return new ViewBizMgrAction(action);
    }
}



