import { Component, OnInit, Input, OnChanges, SimpleChanges, ViewChild } from '@angular/core';
import { ViewActionBase, BizVoidReturnType, ViewBizMgrAction } from '../../util/be-reference';
import { BizActionMethodparamListComponent } from './methodparam-list/biz-action-methodpara-list.component';
import { MgrActionMethodparamListComponent } from './methodparam-list/mgr-action-methodpara-list.component';
import { ReturnInfoCardComponent } from './return-info-card/return-info-card.component';

@Component({
  selector: 'app-base-action-paras',
  templateUrl: './base-action-paras.component.html',
  styleUrls: ['./base-action-paras.component.css']
})
export class BaseActionParasComponent {

  tabNameParaInfo = '';
  tabNameReturnValue = '';

  @Input() currentViewAction: ViewActionBase;
  /**
   * MgrAction-MgrPara
   */
  @Input() isMgrAction = true;

  /**
   * 
   */
  @Input() isEditable: boolean;

  @ViewChild('mgrAction') mgrAction: MgrActionMethodparamListComponent;
  @ViewChild('beAction') beAction: BizActionMethodparamListComponent;
  @ViewChild('returnValue') returnValue: ReturnInfoCardComponent;

  constructor() { }

  refresh() {
    if (this.returnValue) {
      this.returnValue.refresh();
    }
    if (this.isMgrAction) {
      if (this.mgrAction) {
        this.mgrAction.load(this.currentViewAction);
      }
    } else {
      if (this.beAction) {
        this.beAction.load(this.currentViewAction);
      }
    }
  }

  disabled() {
    // if (this.returnValue) {
    //   this.returnValue.disabled();
    // }
    // if (this.isMgrAction) {
    //   if (this.mgrAction) {
    //     this.mgrAction.disabled();
    //   }
    // } else {
    //   if (this.beAction) {
    //     this.beAction.disabled();
    //   }
    // }
  }
}



