import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ListBase } from '../base/listBase';
import { MapBase } from '../base/mapBase';
import { ViewAuthFieldActionInfoList } from './viewAuthFiledActionInfo';

import { AuthFieldInfo, AuthConst } from '../../ref';


export class ViewAuthFiledInfoList extends ListBase<ViewAuthFiled> {

  constructor(enumArray: AuthFieldInfo[] | List<any>) {
    super(enumArray);
    if (isNullOrUndefined(enumArray)) {
      return;
    }
    this.data = this.getViewAuthFields(this.data);
  }

  getViewAuthFields(authInfos: List<any>): any {
    if (isNullOrUndefined(authInfos)) {
      return;
    }

    let viewAuthInfos = authInfos;

    if (authInfos.size > 0) {
      for (let i = 0; i < authInfos.size; i++) {
        viewAuthInfos = viewAuthInfos.update(i, value => this.getViewAuthField(authInfos.get(i)));
      }
      return viewAuthInfos;
    }
  }

  getViewAuthField(authField: AuthFieldInfo): any {
    return new ViewAuthFiled(authField);
  }
}

export class ViewAuthFiled extends MapBase {
  /**
   * 
   * @param element
   */
  constructor(authField: AuthFieldInfo | Map<string, any>) {
    super(authField);
    this.data = this.getViewAuthField(this.data);
  }

  /**
   * Map
   * @param bizObject
   */
  private getViewAuthField(authField: Map<string, any>): Map<string, any> {
    if (isNullOrUndefined(authField)) {
      return;
    }
    let af = authField;
    af = af.update(AuthConst.AuthFieldActionInfos, authActionInfos => this.getAuthActionInfos(authActionInfos));
    return af;
  }

  private getAuthActionInfos(authActionInfos: List<any>): any {
    return new ViewAuthFieldActionInfoList(authActionInfos);
  }

  get authActionInfos(): ViewAuthFieldActionInfoList {
    return this.get(AuthConst.AuthFieldActionInfos);
  }

  set authActionInfos(value: ViewAuthFieldActionInfoList) {
    this.set(AuthConst.AuthFieldActionInfos, value);
  }
}




