// angular
import { Component, OnInit, ViewChild, AfterViewInit, AfterViewChecked } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';

// service
import { CmpCodeTemplateService } from './service/cmp-code-template.service';
import { BePublishService } from './service/be-publish.service';

// component
import { ObjectTreeComponent } from './object-tree/object-tree.component';
import { ElementCollectionComponent } from './element-collection/element-collection.component';
import {
  ViewModel, ViewObject, BizOperationConst, GSPBusinessEntity,
  MetadataDto, ViewOperationList, ViewOperation, HttpUrlConst, GSPCommonObjectType, BizObjectConst, BECategory, BizEntityConst,
} from './util/be-reference';
import { BeMgrActionComponent } from './be-mgr-action/be-mgr-action.component';
import { BizEntityActionComponent } from './biz-entity-action/biz-entity-action.component';
import { ValidationsTabComponent } from './validations-tab/validations-tab.component';
import { DeterminationsTabComponent } from './determinations-tab/determinations-tab.component';
import { BasicInfoTabComponent } from './basic-info-tab/basic-info-tab.component';
import { BeDesignerGuideComponent } from './refModule/be-designer-guide/be-designer-guide.component';
import { BeMetadataService } from './service/be-metadata.service';
import { BeChangeIDElementLength } from './util/be-change-id-length';
import { ElementPropertyConfig } from '@farris/ide-devkit';
import { BePropertyBag, BePropertyBagType } from './service/be-propertybag';
// import { TreeModel } from '../../../../packages/file-tree-panel-package/models/tree-model';

declare var window: any;
declare var $: any;

@Component({
  selector: 'app-bedesigner-frame',
  templateUrl: './be-designer-frame.component.html',
  styleUrls: ['./be-designer-frame.component.css'],
  providers: [BePublishService, CmpCodeTemplateService, BeMetadataService]
})
export class BeDesignerFrameComponent implements OnInit, AfterViewChecked {

  /**
   * 
   */
  _metadataContnet: MetadataDto;
  set metadataContent(metadataContent: any) {
    this._metadataContnet = metadataContent;
    this.loadBizEntity(metadataContent);
  }

  /**
   * uribe
   */
  set metadataUri(uri: string) {
    const fileName = this.getFileNameFromUri(uri);
    const path = this.getPathFromUri(uri);
    HttpUrlConst.currentPath = path;
    this.beMetadataService.loadBeMetadata(fileName, path).subscribe(
      data => {
        this._metadataContnet = data;
        this.loadBizEntity(data);
      });
  }

  /**
   * 
   */
  get isBeNode(): boolean {
    return this.objTree.isBeNodeSelect();
  }

  /**
   * ()
   */
  loadTimer = 0;
  subscriptionOfSelectItem: Subscription = new Subscription();

  /**
   * 
   */
  showCmpCodeWindow = false;
  cmpGenerateCode: string;

  /**
   * 
   */
  showBeGuideWindow = false;

  /**
   * 
   */
  showErrorStackWindow = false;
  errorStack: string;
  errorStackHead: string;
  errorToken = '#GSPBefError#';
  savedErrorToken = '#saved#';

  tabNameElement = '';
  tabNameMgrAction = '';
  tabNameBeAction = '';
  tabNameDetermination = '';
  tabNameValidation = '';
  tabNameBasicInfo = '';
  tabNameAuth = '';
  currentTabTitle = this.tabNameElement;

  /**
   * Tab
   */
  currentBeTabTitle = this.tabNameMgrAction;
  currentObjTabTitle = this.tabNameElement;

  @ViewChild('objTree') objTree: ObjectTreeComponent;
  @ViewChild('eleCollection') eleCollection: ElementCollectionComponent;
  @ViewChild('mgrActions') mgrActions: BeMgrActionComponent;
  @ViewChild('beActions') beActions: BizEntityActionComponent;
  @ViewChild('validations') validations: ValidationsTabComponent;
  @ViewChild('determinations') determinations: DeterminationsTabComponent;
  @ViewChild('basicInfo') basicInfo: BasicInfoTabComponent;

  @ViewChild('guide') guide: BeDesignerGuideComponent;

  /**
   * Tab-
   */
  viewModel: ViewModel;
  bizEntity: GSPBusinessEntity;
  /**
   * Tab-
   */
  currentViewObject: ViewObject;

  /**
   * Tab-
   */
  currentSelectItem: ViewObject | ViewModel;

  /**
   * Tab-
   * [Tab][Tab][Tab][Tab]
   */
  get currentViewOperations(): ViewOperationList {
    let viewOperations = null;
    switch (this.currentTabTitle) {
      case this.tabNameElement:
        return;
      case this.tabNameMgrAction:
        viewOperations = this.viewModel ? this.viewModel.mgrActions : this.viewModel;
        break;
      case this.tabNameBeAction:
        viewOperations = this.currentViewObject ? this.currentViewObject.beActions : this.currentViewObject;
        break;
      case this.tabNameDetermination:
        viewOperations = this.currentViewObject ? this.currentViewObject.determinations : this.currentViewObject;
        break;
      case this.tabNameValidation:
        viewOperations = this.currentViewObject ? this.currentViewObject.validations : this.currentViewObject;
        break;
      case this.tabNameBasicInfo:
        return;
    }
    return viewOperations;
  }

  /**
   * []
   * Tab
   * BeAction
   */
  get isChildObject(): boolean {
    if (this.currentViewObject) {
      return this.currentViewObject.get(BizObjectConst.ObjectType) === GSPCommonObjectType.ChildObject;
    }
    return true;
  }

  // constructor(treeModel: TreeModel, lcmService: GSPMetadataService, metadataService: BePublishService,
  //   cmpGenerateService: CmpCodeTemplateService) {
  constructor(private beMetadataService: BeMetadataService, private bePublishService: BePublishService,
    private cmpGenerateService: CmpCodeTemplateService) {

    // // ts,
    // const bizEntity = InitBizEntity.initBizEntity('newBe', '', 'Inspur.SD.SalesOrder');
    // this.bizEntity = bizEntity;
    // console.log(JSON.stringify(bizEntity));
    // this.viewModel = new ViewModel(JSON.parse(JSON.stringify(bizEntity)));
    // this.currentViewObject = this.viewModel.mainObject;
    // this.currentSelectItemChange(this.viewModel.mainObject);
  }

  /**
   * -------------------------/--------------------------------
   */

  /**
   * 
   * @param uri
   */
  getFileNameFromUri(uri: string): string {
    const s = uri.split('/');
    return s[s.length - 1];
  }

  /**
   * 
   * @param uri
   */
  getPathFromUri(uri: string): any {
    return uri.substring(1, uri.lastIndexOf('/'));
  }

  ngOnInit() {
  }

  ngAfterViewChecked(): void {
    this.subscribePropertyBagCurrentItem();
  }

  /**
   * 
   * //
   */
  public selectNewObject(selectItem: ViewObject) {
    if (!selectItem) {
      alert('');
      return;
    }

    this.currentViewObject = selectItem;
    if (this.isBeNode) {
      this.currentTabTitle = this.currentBeTabTitle;
    } else {
      this.currentTabTitle = this.currentObjTabTitle;
    }
  }

  /**
   * 
   * @param item 
   */
  currentSelectItemChange(item: ViewModel | ViewObject) {
    this.unsubscribeSelectItem();
    this.currentSelectItem = item;
    this.subscribeSelectItem();

    // this.subscribeElementPropertyBagCurrentItem();
  }

  /**
   * 
   */
  subscribeSelectItem(): any {
    this.subscriptionOfSelectItem = this.currentSelectItem.refreshName.subscribe(changeName => {
      this.objTree.refresh(this.viewModel);
    });
  }

  /**
   * 
   */
  subscribeBeRename() {
    this.viewModel.refreshName.subscribe(hasChangeName => {
      this.objTree.refresh(this.viewModel);
    });
  }

  /**
   * 
   */
  unsubscribeSelectItem(): any {
    this.subscriptionOfSelectItem.unsubscribe();
  }
  // ---------------------------------------------------------------------------------------





  // ---------------------------Tab------------------------------------------------

  /**
   * 
   */
  checkOut(): boolean {
    return true;
  }

  /**
   * [] 
   */
  addChildObject() {
    if (!this.checkOut()) {
      return;
    }
    this.objTree.addChildObject();
  }

  /**
   * [] 
   */
  addSiblingObject() {
    if (!this.checkOut()) {
      return;
    }

    this.objTree.addSiblingObject();
  }

  /**
   * [] 
   */
  deleteObject() {
    if (!this.checkOut()) {
      return;
    }

    this.objTree.deleteObject();
  }

  /**
   * [] 
   */
  addElement() {
    if (!this.checkOut()) {
      return;
    }

    this.eleCollection.addHandler();
  }

  /**
   * [] 
   */
  addVirtualElement() {
    if (!this.checkOut()) {
      return;
    }

    this.eleCollection.addVirtualHandler();
  }

  /**
   * [] 
   */
  deleteElement() {
    if (!this.checkOut()) {
      return;
    }

    this.eleCollection.removeSelectedElement();
  }

  /**
   * [] 
   */
  addMgrAction() {
    if (!this.checkOut()) {
      return;
    }
    this.mgrActions.addMgrAction();
  }

  /**
   * [] 
   */
  removeMgrAction() {
    if (!this.checkOut()) {
      return;
    }
    this.mgrActions.remove();
  }

  /**
   * [] 
   */
  addBeAction() {
    if (!this.checkOut()) {
      return;
    }
    this.beActions.addBeAction();
  }

  /**
   * [] 
   */
  removeBeAction() {
    if (!this.checkOut()) {
      return;
    }
    this.beActions.remove();
  }

  /**
   * [] 
   */
  addValidation() {
    if (!this.checkOut()) {
      return;
    }
    this.validations.addValidation();
  }

  /**
   * [] 
   */
  removeValidation() {
    if (!this.checkOut()) {
      return;
    }
    this.validations.remove();
  }

  /**
   * [] 
   */
  addDetermination() {
    if (!this.checkOut()) {
      return;
    }
    this.determinations.addDetermination();
  }

  /**
   * [] 
   */
  removeDetermination() {
    if (!this.checkOut()) {
      return;
    }
    this.determinations.remove();
  }

  // ---------------------------------------------------------------------------------------

  /**
   * ,
   * @param args
   */
  tabSelectChange(args: any) {
    const { title } = args;
    this.currentTabTitle = title;
    if (this.isBeNode) {
      this.currentBeTabTitle = title;
    } else {
      this.currentObjTabTitle = title;
      if (this.currentObjTabTitle !== this.tabNameElement) {
        this.initPropertyBag();
      }
    }
  }






  // ---------------------------[] [] []-----------------------------------------

  /**
   * 
   * @param bizEntity 
   */
  loadBizEntity(dto: MetadataDto) {
    if (isNullOrUndefined(dto) || isNullOrUndefined(dto.content)) {
      return;
    }
    // 
    HttpUrlConst.currentPath = dto.relativePath;

    this.bizEntity = JSON.parse(dto.content);

    // --------------------------------------
    // id22=>36,201810
    BeChangeIDElementLength.changeIDElementLength(this.bizEntity);
    // --------------------------------------

    if (isNullOrUndefined(this.bizEntity)) {
      this.viewModel = null;
      this.currentViewObject = null;
      this.currentSelectItem = null;
      this.loadTimer = 0;
      return;
    }
    this.viewModel = new ViewModel(this.bizEntity as GSPBusinessEntity);
    this.subscribeBeRename();

    if (this.loadTimer === 0) {
      // 
      this.currentViewObject = this.viewModel.mainObject;
      this.currentSelectItemChange(this.viewModel.mainObject);
      this.initPropertyBag();
    }
    this.loadTimer++;
  }

  updateViewModelToDto() {
    const be = this.viewModel.toJS();
    this.consoleShow(be); // 

    const beJson = JSON.stringify(be);
    this._metadataContnet.content = beJson;
  }

  loadMetadataService(): Observable<any> {
    return this.beMetadataService.loadBeMetadata(this._metadataContnet.fileName, this._metadataContnet.relativePath);
  }

  saveMetadataService(): Observable<any> {
    this.updateViewModelToDto();
    return this.beMetadataService.saveBeMetadata(this._metadataContnet);
  }

  /**
   * 
   */
  saveMetadata() {
    this.saveMetadataService().subscribe(saveResponse => {
      // dbobe
      this.loadMetadataService().subscribe(
        data => {
          this.loadBizEntity(data);
          alert('');
        }
      );
    }, error => {
      let head = '';
      const errorString = <string>(error.text ? error.text() : error.text);
      // 
      if (errorString.includes(this.savedErrorToken)) {
        // dbobe
        this.loadMetadataService().subscribe(
          data => {
            this.loadBizEntity(data);
            head = '';
            this.handleErrorString(head, errorString);
          }
        );
      } else {
        this.handleErrorString(head, errorString);
      }
    });
  }

  /**
   * 
   * @param bizEntity
   */
  consoleShow(bizEntity: GSPBusinessEntity) {
    console.log('');
    console.log(bizEntity);
    console.log('JSON');
    console.log(JSON.stringify(bizEntity));
  }

  /**
   * []
   */
  reloadMetadata() {
    this.loadMetadataService().subscribe(
      data => {
        this.loadBizEntity(data);
        alert('');
        // 
        if (!isNullOrUndefined(this.objTree)) {
          this.objTree.refresh(this.viewModel);
        }
      }
    );
  }
  // ---------------------------------------------------------------------------------------





  // ---------------------------[]--------------------------------------------------------

  /**
   * 
   */
  saveAndSysnBizEntity() {
    //  [].be
    // step1.[]
    // step2.[]
    this.saveMetadataService().subscribe(saveResponse => {
      this.bePublishService.saveAndSysnBizEntity(this._metadataContnet.fileName, this._metadataContnet.relativePath).subscribe(data => {
        // dbobe
        this.loadMetadataService().subscribe(
          data2 => {
            this.loadBizEntity(data2);
            // cmp,dbo
            // this.treeModel.refresh();
            alert('');
          }
        );
      }, error => {
        let head = '';
        const errorString = <string>(error.text ? error.text() : error.text);
        // 
        if (errorString.includes(this.savedErrorToken)) {
          // dbobe
          this.loadMetadataService().subscribe(
            data => {
              this.loadBizEntity(data);
              head = '';
              this.handleErrorString(head, errorString);
            }
          );
        } else {
          this.handleErrorString(head, errorString);
        }
      });
    });
  }

  /**
   * 
   */
  buildBizEntity() {
    this.bePublishService.buildBizEntity(this._metadataContnet.fileName, this._metadataContnet.relativePath).subscribe(data => {
      alert('');
    }, error => {
      const head = '';
      const errorString = <string>(error.text ? error.text() : error.text);
      this.handleErrorString(head, errorString);
    });
  }

  /**
   * 
   */
  generateBizEntity() {
    this.bePublishService.generate(this._metadataContnet.fileName, this._metadataContnet.relativePath).subscribe(data => {
      alert('');
    }, error => {
      const head = '';
      const errorString = <string>(error.text ? error.text() : error.text);
      this.handleErrorString(head, errorString);
    });
  }

  /**
   * 
   */
  publishService() {
    this.bePublishService.publishService(this._metadataContnet.fileName, this._metadataContnet.relativePath).subscribe(data => {
      // eapibff
      // this.treeModel.refresh();
      alert('');
    }, error => {
      const head = '';
      const errorString = <string>(error.text ? error.text() : error.text);
      this.handleErrorString(head, errorString);
    });
  }

  /**
   * 
   */
  oneButtonPublishService() {
    //  [].be
    // step1.[]
    // step2.[]
    this.saveMetadataService().subscribe(saveResponse => {
      this.bePublishService.oneButtonPublishService(this._metadataContnet.fileName, this._metadataContnet.relativePath).subscribe(data => {
        // dbobe
        this.loadMetadataService().subscribe(
          data2 => {
            this.loadBizEntity(data2);
            // cmp,dbo
            // this.treeModel.refresh();
            alert('');
          }
        );
      }, error => {
        let head = '';
        const errorString = <string>(error.text ? error.text() : error.text);
        // 
        if (errorString.includes(this.savedErrorToken)) {
          // dbobe
          this.loadMetadataService().subscribe(
            data => {
              this.loadBizEntity(data);
              head = '';
              this.handleErrorString(head, errorString);
            }
          );
        } else {
          this.handleErrorString(head, errorString);
        }
      });
    });
  }

  /**
   * 
   */
  deploy() {
    this.bePublishService.deploy(this._metadataContnet.fileName, this._metadataContnet.relativePath);
  }
  // ---------------------------------------------------------------------------------------




  // ------------------------------------------------------------------------

  /**
   *  []
   * 
   * '',
   */
  getCurrentObjectCode(): string {
    if (isNullOrUndefined(this.currentViewObject)) {
      return '';
    }
    if (this.currentViewObject.objectType === GSPCommonObjectType.MainObject) {
      return '';
    } else {
      return this.currentViewObject.Code;
    }
  }


  /**
   * []
   * 
   */
  getCurrentOperation(): ViewOperation {
    if (isNullOrUndefined(this.currentViewOperations)) {
      return null;
    }
    return this.currentViewOperations.currentSelectItem;
  }


  /**
   * []
   * 
   */
  getCurrentOperationType(): string {
    let type = null;
    switch (this.currentTabTitle) {
      case this.tabNameElement:
        return;
      case this.tabNameMgrAction:
        type = BizOperationConst.BizMgrAction;
        break;
      case this.tabNameBeAction:
        type = BizOperationConst.BizAction;
        break;
      case this.tabNameDetermination:
        type = BizOperationConst.Determination;
        break;
      case this.tabNameValidation:
        type = BizOperationConst.Validation;
        break;
      case this.tabNameBasicInfo:
        return;
    }
    return type;
  }

  /**
   * 
   */
  getComponentCodeTemplate() {
    if (!this.validataBeAssemblyName()) {
      return;
    }

    const childObjectCode = this.getCurrentObjectCode();
    const assemblyName = this.viewModel.ComponentAssemblyName;
    const viewOperation = this.getCurrentOperation();
    const type = this.getCurrentOperationType();

    if (isNullOrUndefined(viewOperation)) {
      alert('');
      return;
    }
    const operation = viewOperation.toJS();
    if (!this.cmpGenerateService.validateOperation(type, operation)) {
      return;
    }
    this.cmpGenerateService.generateCode(
      this._metadataContnet.relativePath, this._metadataContnet.fileName, operation, type, assemblyName, childObjectCode).subscribe(
        returnResult => {
          if (returnResult.status === 200) {
            this.cmpGenerateCode = returnResult.text();
            this.showCmpCodeWindow = true;
          }
        }
        , error => {
          const head = '';
          const errorString = <string>(error.text ? error.text() : error.text);
          this.handleErrorString(head, errorString);
        }
      );
  }

  validataBeAssemblyName(): boolean {
    if (isNullOrUndefined(this.viewModel.ComponentAssemblyName) || this.viewModel.ComponentAssemblyName.length === 0) {
      alert('');
      return false;
    }
    return true;
  }

  /**
   * 
   */
  copyGenerateTemplate() {
    const range = document.createRange();
    range.selectNode(document.getElementById('generateTemplate'));

    const selection = window.getSelection();
    if (selection.rangeCount > 0) {
      selection.removeAllRanges();
    }

    selection.addRange(range);
    document.execCommand('copy');
    alert('');
  }

  /**
   * 
   */
  closeCmpCodeWindow(): void {
    this.showCmpCodeWindow = false;
  }
  // ---------------------------------------------------------------------------------------




  // -----------------------------------------------------------------------------
  /**
   * 
   */
  showErrorStack(errorHead: string, errorMessage: string) {
    this.errorStackHead = errorHead;
    this.errorStack = errorMessage;
    this.showErrorStackWindow = !this.showErrorStackWindow;
  }

  /**
   * 
   * @param head
   * @param errorString
   */
  handleErrorString(head: string, errorString: string) {
    if (!isNullOrUndefined(errorString) && errorString.includes(this.errorToken)) {
      const strings = errorString.split(this.errorToken);
      const errorMessage = strings[1];
      this.showErrorStack(head, errorMessage);
    } else {
      this.showErrorStack(head, errorString);
    }
  }
  // ---------------------------------------------------------------------------------------

  // -----------------------------------------------------------------------------
  showGuide() {
    this.guide.metadataContent = this._metadataContnet;
    // be
    this.guide.createMetadataEvent.subscribe(meta => {
      this.metadataContent = meta;
    });
    this.showBeGuideWindow = true;
  }
  // ---------------------------------------------------------------------------------------

  isDbe(): boolean {
    if (isNullOrUndefined(this.bizEntity)) {
      return false;
    }
    return this.bizEntity.Category === BECategory.DependentBusinessObject;
  }

  // -----------------------------------------------------------------------------
  // tslint:disable-next-line:member-ordering
  propertyConfig = new Array<ElementPropertyConfig>();
  // tslint:disable-next-line:member-ordering
  propertyData: any;

  // tslint:disable-next-line:member-ordering
  elementPropertyBagSubscription: Subscription = new Subscription();
  // tslint:disable-next-line:member-ordering
  objectPropertyBagSubscription: Subscription = new Subscription();
  // tslint:disable-next-line:member-ordering
  modelPropertyBagSubscription: Subscription = new Subscription();


  initPropertyBag() {
    BePropertyBag.currentBePropertyBagType = BePropertyBagType.Object;
    this.propertyConfig = BePropertyBag.BizObjectPropertyConfig;
    this.propertyData = this.currentViewObject.toJS();
  }

  /**
   * 
   * 
   */
  subscribePropertyBagCurrentItem() {
    this.subscribeModelPropertyBagCurrentItem();
    this.subscribeObjectPropertyBagCurrentItem();
    this.subscribeElementPropertyBagCurrentItem();
  }

  /**
   * 
   */
  subscribeModelPropertyBagCurrentItem() {
    if (this.modelPropertyBagSubscription) {
      this.modelPropertyBagSubscription.unsubscribe();
    }
    // 
    this.modelPropertyBagSubscription = this.objTree.currentSelectItem.subscribe(vm => {
      if (!this.isBeNode) {
        return;
      }
      BePropertyBag.currentBePropertyBagType = BePropertyBagType.Model;
      this.propertyConfig = BePropertyBag.BizEntityPropertyConfig;
      this.propertyData = vm.toJS();
      this.subscribeObjectPropertyBagCurrentItem();
    });
  }

  /**
   * 
   */
  subscribeObjectPropertyBagCurrentItem() {
    if (this.objectPropertyBagSubscription) {
      this.objectPropertyBagSubscription.unsubscribe();
    }
    // 
    this.objectPropertyBagSubscription = this.objTree.currentSelectItem.subscribe(vo => {
      if (this.isBeNode) {
        return;
      }
      BePropertyBag.currentBePropertyBagType = BePropertyBagType.Object;
      this.propertyConfig = BePropertyBag.BizObjectPropertyConfig;
      this.propertyData = vo.toJS();
    });
  }

  /**
   * 
   */
  subscribeElementPropertyBagCurrentItem() {
    if (!this.eleCollection) {
      return;
    }
    if (this.elementPropertyBagSubscription) {
      this.elementPropertyBagSubscription.unsubscribe();
    }

    // 
    this.elementPropertyBagSubscription = this.eleCollection.currentSelectElementChange.subscribe(viewEle => {
      BePropertyBag.currentBePropertyBagType = BePropertyBagType.Element;
      this.propertyConfig = BePropertyBag.BizElementPropertyConfig;
      this.propertyData = viewEle.toJS();
    }
    );
  }

  propertyBagValueChanged(args: any) {
    const { propertyID, propertyValue } = args;
    switch (BePropertyBag.currentBePropertyBagType) {
      case BePropertyBagType.Model:
        if (propertyID === BizEntityConst.Name) {
          this.viewModel.Name = propertyValue;
          break;
        }
        this.viewModel.set(propertyID, propertyValue);
        break;
      case BePropertyBagType.Object:
        if (propertyID === BizObjectConst.Name) {
          this.currentViewObject.Name = propertyValue;
          break;
        }
        this.currentViewObject.set(propertyID, propertyValue);
        break;
      case BePropertyBagType.Element:
        this.currentViewObject.elements.currentSelectItem.set(propertyID, propertyValue);
    }
  }
  // ------------------------------------------------------------------------------------
}







