import {
    Component, Input, ViewChild, Renderer2, OnInit, OnChanges, Output, EventEmitter, SimpleChanges
} from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { AddEvent, EditEvent, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';
import {
    ViewActionBase, ViewParameterListBase, BizActionParameter,
    ParameterModes, BizParameterTypes, Guid, ParameterMode,
    BizParameterMode, BizParameterType, ViewBizActionList, ViewBizActionParameter,
    ViewBizActionParaList, ViewBizAction, ViewBizMgrActionParaList, ViewParameterBase, BizParameterBase
} from '../../../util/be-reference';
import { isNullOrUndefined } from 'util';

@Component({
    selector: 'app-methodparam-list',
    templateUrl: './methodparam-list.component.html',
    styleUrls: ['./methodparam-list.component.css']
})
export class MethodparamListComponent implements OnInit, OnChanges {

    @ViewChild('grid') grid: GridComponent;

    /**
     * 
     */
    @Input() currentViewAction: ViewActionBase;
    @Input() paras: BizActionParameter[];
    @Input() isEditable: boolean;
    viewAction: ViewActionBase;
    parameters: BizActionParameter[];
    /**
     * 
     */
    paramModes = ParameterModes;
    paramTypes = BizParameterTypes;

    /*  */
    public paramIDSelection: any[] = [];
    public selectableSettings: SelectableSettings = {
        checkboxOnly: false,
        mode: 'single'
    };

    constructor(private formBuilder: FormBuilder, private renderer: Renderer2) { }

    public ngOnInit(): void {
        this.load(<ViewBizAction>this.viewAction);
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.grid.closeCell();
        this.viewAction = this.currentViewAction;
        this.load(<ViewBizAction>this.viewAction);
    }

    createNewViewParameters(): ViewParameterListBase {
        const paras = new Array<BizActionParameter>();
        return new ViewBizActionParaList(paras);
    }

    /**
     * 
     */
    createViewParaInstance(para: BizParameterBase): ViewParameterBase {
        return new ViewBizActionParameter(para);
    }

    createParaInstance(): BizParameterBase {
        return new BizActionParameter();
    }

    /**
     * 
     */
    createNewParameter(): BizParameterBase {
        const newParam = this.createParaInstance(); // 
        newParam.ID = Guid.newGuid();
        newParam.Mode = BizParameterMode.IN; // in
        const stringType = BizParameterType.String;
        newParam.ParameterType = stringType;
        newParam.Assembly = this.getAssembly(stringType);
        newParam.ClassName = this.getClassName(stringType);
        return newParam;
    }

    GetParamAssemblyDisplay(dataItem: BizParameterBase): string {
        if (dataItem.ParameterType === BizParameterType.Custom) {
            return dataItem.Assembly;
        } else {
            return '';
        }
    }

    GetParamClassNameDisplay(dataItem: BizParameterBase): string {
        if (dataItem.ParameterType === BizParameterType.Custom) {
            return dataItem.ClassName;
        } else {
            return '';
        }
    }

    getAssembly(type: BizParameterType): string {
        return this.paramTypes.find(item => item.value === type).assembly;
    }

    getClassName(type: BizParameterType): string {
        return this.paramTypes.find(item => item.value === type).className;
    }

    /**
     * 
     * @param viewAction
     */
    load(viewAction: ViewActionBase) {
        this.viewAction = viewAction;
        if (isNullOrUndefined(viewAction)) {
            this.clearData();
            return;
        }
        if (isNullOrUndefined(this.viewAction.Parameters) && isNullOrUndefined(this.viewAction.Parameters.toJS())) {
            this.viewAction.Parameters = this.createNewViewParameters();
            this.clearData();
        } else {
            this.parameters = this.viewAction.Parameters.toJS();
        }

        this.viewAction.Parameters.refreshView.subscribe(
            paras => {
                if (isNullOrUndefined(paras)) {
                    this.clearData();
                    return;
                }
                this.parameters = paras.toJS();
            }
        );
    }

    clearData() {
        this.parameters = [];
    }

    /**
     * 
     * @param param0
     */
    cellClickHandler({ sender, rowIndex, columnIndex, dataItem, isEdited }) {
        if (!isEdited && this.isEditable) {
            sender.editCell(rowIndex, columnIndex, this.createFormGroup(dataItem));
        }
    }

    createFormGroup(dataItem: any): FormGroup {
        if (dataItem.ParameterType === BizParameterType.Custom) {
            return this.formBuilder.group({
                'ParamCode': [dataItem.ParamCode, Validators.required],
                'ParamName': [dataItem.ParamName, Validators.required],
                'Mode': [dataItem.Mode, Validators.required],
                'ParameterType': [dataItem.ParameterType, Validators.required],
                'Assembly': [dataItem.Assembly, Validators.required],
                'ClassName': [dataItem.ClassName, Validators.required],
                'ParamDescription': dataItem.ParamDescription,
            });
        } else {
            return this.formBuilder.group({
                'ParamCode': [dataItem.ParamCode, Validators.required],
                'ParamName': [dataItem.ParamName, Validators.required],
                'Mode': [dataItem.Mode, Validators.required],
                'ParameterType': [dataItem.ParameterType, Validators.required],
                'ParamDescription': dataItem.ParamDescription,
            });
        }
    }

    /**
     * 
     * @param args
     */
    cellCloseHandler(args: any) {
        const { formGroup, sender, rowIndex, columnIndex, dataItem, isEdited, column } = args;

        this.viewAction.Parameters.setSelectIndex(rowIndex);
        const currentViewPara = this.viewAction.Parameters.currentSelectItem;
        // if (!formGroup.valid) {
        //     args.preventDefault();
        // } else
        if (formGroup.dirty) {
            const propertyName = column.field;
            const newValue = formGroup.value[propertyName];

            switch (column.field) {
                case 'ParamCode':
                    currentViewPara.ParamCode = newValue;
                    break;
                case 'ParamName':
                    currentViewPara.ParamName = newValue;
                    break;
                case 'Mode':
                    currentViewPara.Mode = newValue;
                    break;
                case 'ParameterType':
                    currentViewPara.ParameterType = newValue;
                    currentViewPara.Assembly = this.getAssembly(newValue);
                    currentViewPara.ClassName = this.getClassName(newValue);
                    break;
                case 'Assembly':
                    currentViewPara.Assembly = newValue;
                    break;
                case 'ClassName':
                    currentViewPara.ClassName = newValue;
                    break;
                case 'ParamDescription':
                    currentViewPara.ParamDescription = newValue;
                    break;
            }
        }
        sender.cancelCell();
    }

    /**
     * 
     * @param param0
     */
    addHandler() {
        const newPara = this.createNewParameter();
        const newViewPara = this.createViewParaInstance(newPara);
        this.viewAction.Parameters.add(newViewPara);
    }

    /**
     * 
     * @param args
     */
    removeHandler(): void {
        if (isNullOrUndefined(this.paramIDSelection) || this.paramIDSelection.length === 0) {
            alert('');
        }
        const rowIndex = this.paramIDSelection[0];
        this.viewAction.Parameters.removeByID(rowIndex);
    }

    getParamMode(mode: any): any {
        return this.paramModes.find(item => item.value === mode).text;
    }

    getParamType(type: any): any {
        return this.paramTypes.find(item => item.value === type).text;
    }

    /**
     * 
     */
    disabled() {
        this.isEditable = false;
    }
}



