import { isNullOrUndefined } from 'util';
import { BizOperation } from './be-reference';

export class InternalBeActionUtil {
  /**
   * id
   */
  private static deleteBeActionId = 'aD73GrGaKEiA7N9jsPxIOQ';
  private static modifyBeActionId = 'tf9MVINdJ0yhUrsJgfcvBA';
  private static retrieveBeActionId = 'cJwzDlFfa0uz4rChlTEd4g';

  /**
   * 
   * @param action
   */
  static isDeleteBeAction(action: BizOperation): boolean {
    if (isNullOrUndefined(action)) {
      return false;
    }
    return action.ID === this.deleteBeActionId;
  }

  /**
   * 
   * @param action
   */
  static isModifyBeAction(action: BizOperation): boolean {
    if (isNullOrUndefined(action)) {
      return false;
    }
    return action.ID === this.modifyBeActionId;
  }

  /**
   * 
   * @param action
   */
  static isRetrieveBeAction(action: BizOperation): boolean {
    if (isNullOrUndefined(action)) {
      return false;
    }
    return action.ID === this.retrieveBeActionId;
  }

  /**
   * BizAction
   * @param action
   */
  static isInternalBizAction(action: BizOperation): boolean {
    return this.isDeleteBeAction(action) || this.isModifyBeAction(action) || this.isRetrieveBeAction(action);
  }
}



