import { Component } from '@angular/core';
import { ViewBizMgrAction, BizMgrAction } from '../util/be-reference';
import { BaseOperationComponentComponent } from '../base-operation-component/base-operation-component.component';

@Component({
  selector: 'app-be-mgr-action',
  templateUrl: './be-mgr-action.component.html',
  styleUrls: ['./be-mgr-action.component.css']
})
export class BeMgrActionComponent extends BaseOperationComponentComponent {
  constructor() {
    super();
  }

  addMgrAction() {
    const newAction = new BizMgrAction();
    this.viewOperations.add(new ViewBizMgrAction(newAction));
    this.viewOperations.setSelectIndexByID(newAction.ID);
    this.refresh();
  }
}



