import { GSPBusinessEntity, GSPBizEntityObject, GSPElementObjectType, GSPBizEntityElement, IGSPCommonElement } from './be-reference';
import { isNullOrUndefined } from 'util';

export class BeChangeIDElementLength {

  /**
   * id22=>36
   * 201810
   */
  static changeIDElementLength(be: GSPBusinessEntity) {
    this.changeObjectIDElemenetLength(be.MainObject);
  }

  static changeObjectIDElemenetLength(bizObject: GSPBizEntityObject) {
    bizObject.ContainElements.forEach(ele => {
      if (this.isIDType(bizObject, ele)) {
        ele.Length = 36;
      }
    });
    if (bizObject.ContainChildObjects && bizObject.ContainChildObjects.length > 0) {
      bizObject.ContainChildObjects.forEach(
        child => {
          this.changeObjectIDElemenetLength(child as GSPBizEntityObject);
        }
      );
    }
  }


  /**
   * id
   * id,parentId,
   * @param dataItem 
   */
  private static isIDType(obj: GSPBizEntityObject, dataItem: IGSPCommonElement) {
    if (this.isIDElement(obj, dataItem.ID) || this.isParentIDElement(obj, dataItem.ID)
      || dataItem.ObjectType === GSPElementObjectType.Association) {
      return true;
    }
    return false;
  }

  /**
 * id
 * @param id
 */
  private static isIDElement(obj: GSPBizEntityObject, id: string) {
    if (isNullOrUndefined(obj.ColumnGenerateID)) {
      return false;
    } else {
      return id === obj.ColumnGenerateID.ElementID;
    }

  }

  /**
   * 
   * @param id
   */
  private static isParentIDElement(obj: GSPBizEntityObject, id: string) {
    if (isNullOrUndefined(obj.Keys) || obj.Keys.length === 0) {
      return false;
    } else {
      const assoKey = obj.Keys.find(key => key.SourceElement === id);
      return assoKey != null;
    }
  }
}



