import { Injectable } from '@angular/core';
import { Http, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { CacheService } from '@farris/ide-devkit/services';
import { HttpUrlConst } from '../util/be-reference';
import { BeMetadataService } from './be-metadata.service';
import { debounceTime, switchMap, catchError, retry, tap } from 'rxjs/operators';
import { Subject } from 'rxjs/Subject';

/**
 * be
 */
@Injectable()
export class BePublishService {

    constructor(private http: Http, private cache: CacheService, private metadataService: BeMetadataService) {
    }
    private urlBase = `${HttpUrlConst.currentUrl}/api/dev/main/v1.0/becodegenerate`;
    private generateUrl = this.urlBase;
    private publishServiceUrl = this.urlBase + '/publish';
    private saveAndSysnUrl = this.urlBase + '/sysnbe';
    private buildbeUrl = this.urlBase + '/buildbe';
    private deployUrl = this.urlBase + '/deploy';
    private oneButtonPublishServiceUrl = this.urlBase + '/oneButtonPublishService';

    /**
     * 
     */
    generate(fileName: string, path: string): Observable<any> {
        const jitData = new JitMetadataContent();
        jitData.name = fileName;
        jitData.path = path;
        const options = this.getRequestOptionsWithSessionId();
        return this.http.put(this.generateUrl, jitData, options);
    }


    /**
     * 
     */
    oneButtonPublishService(fileName: string, path: string): Observable<any> {
        const jitData = new JitMetadataContent();
        jitData.name = fileName;
        jitData.path = path;
        const options = this.getRequestOptionsWithSessionId();
        return this.http.put(this.oneButtonPublishServiceUrl, jitData, options);
    }

    /**
     * 
     */
    publishService(fileName: string, path: string): Observable<any> {
        const jitData = new JitMetadataContent();
        jitData.name = fileName;
        jitData.path = path;
        const options = this.getRequestOptionsWithSessionId();
        return this.http.put(this.publishServiceUrl, jitData, options);
    }


    /**
     * 
     */
    saveAndSysnBizEntity(fileName: string, path: string): Observable<any> {
        const jitData = new JitMetadataContent();
        jitData.name = fileName;
        jitData.path = path;

        const options = this.getRequestOptionsWithSessionId();
        return this.http.put(this.saveAndSysnUrl, jitData, options);
    }


    /**
     * 
     */
    buildBizEntity(fileName: string, path: string): Observable<any> {
        const jitData = new JitMetadataContent();
        jitData.name = fileName;
        jitData.path = path;
        const options = this.getRequestOptionsWithSessionId();
        return this.http.put(this.buildbeUrl, jitData, options);
    }

    /**
     * 
     */
    deploy(fileName: string, path: string) {
        const info = new DeployInfo();
        info.path = path;
        const options = this.getRequestOptionsWithSessionId();
        this.http.put(this.deployUrl, info, options).subscribe(() => {
            // 59*1000+3000(ms)=62(s)
            const firstTime = 3000; // (ms)
            const retryTime = 60; // 
            const timeInterval = 1000; // (ms)

            let deploySuccess = false;
            const numbers = Observable.timer(firstTime, timeInterval).take(retryTime);
            numbers.subscribe(x => {
                if (deploySuccess) {
                    return;
                } else {
                    if (x === retryTime - 1) {
                        alert('');
                    } else {
                        this.metadataService.loadBeMetadata(fileName, path).subscribe(
                            data => {
                                if (!deploySuccess) {
                                    alert('');
                                    // alert(`${x},: ${(firstTime + timeInterval * x) / 1000}s`);
                                    deploySuccess = true;
                                }
                            }
                        );
                    }
                }
            });

        }, error => {
            alert('');
        });
    }

    /**
     * sessionIdHeader
     */
    private getRequestOptionsWithSessionId(): RequestOptions {
        const sessionId = this.cache.get('sessionId');
        const headers = new Headers();
        headers.append('sessionId', sessionId);

        const options = new RequestOptions({
            params: {},
            headers: headers
        });
        return options;
    }
}

/**
 * jit
 */
class JitMetadataContent {
    name: string;
    path: string;
}

class DeployInfo {
    path: string;
}



