import { isNullOrUndefined } from 'util';
import { ValidateBizOperation } from './validateBizOperation';
import { BizActionBase, BizReturnValue, BizParameterType, BizParameterBase } from '../validateUtil/ref';
import { ValidateUtil } from '../validateUtil/validateUtil';

/**
 * BEActionMgrAction
 */
export class ValidateBizActionBase extends ValidateBizOperation {

    /**
     * Action
     */
    static Validate(action: BizActionBase): boolean {
        if (!super.Validate(action)) {
            return false;
        }
        if (!this.ValidateBizParameters(action.Parameters)) {
            return false;
        }
        if (!this.ValidateBizReturnValue(action.ReturnValue)) {
            return false;
        }
        return true;
    }

    /**
     * 
     * @param returnValue 
     */
    private static ValidateBizReturnValue(returnValue: BizReturnValue): boolean {
        if (this.IsVoidReturnType(returnValue)) {
            return true;
        }
        return this.validateCustomType('', returnValue);
    }

    /**
     * void
     * @param returnValue 
     */
    private static IsVoidReturnType(returnValue: BizReturnValue): boolean {
        if (isNullOrUndefined(returnValue)) {
            return true;
        }
        if (returnValue.ClassName === 'Void' && returnValue.Assembly === 'mscorlib.dll') {
            return true;
        } else {
            return false;
        }
    }

    private static validateCustomType(itemName: string, returnValue: BizParameterBase): boolean {
        if (returnValue.ParameterType === BizParameterType.Custom) {
            if (ValidateUtil.isEmptyString(returnValue.Assembly) || ValidateUtil.isEmptyString(returnValue.ClassName)) {
                alert(`${itemName}`);
                return false;
            }
            const className = returnValue.ClassName;
            const array = className.split('.');
            const message = `${itemName}'System.String'`;
            if (array.length < 2) {
                alert(message);
                return false;
            }
            for (const item of array) {
                if (ValidateUtil.isEmptyString(item)) {
                    alert(message);
                    return false;
                }
            }
        }
        return true;
    }

    /**
     * 
     * @param paras 
     */
    static ValidateBizParameters(paras: BizParameterBase[]): boolean {
        if (ValidateUtil.isEmptyList(paras)) {
            return true;
        }
        let flag = true;
        paras.forEach(para => {
            if (!this.ValidateBizParaBase(para)) {
                flag = false;
            }
        });
        return flag;
    }

    /**
     * 
     * @param para 
     */
    static ValidateBizParaBase(para: BizParameterBase): boolean {
        if (ValidateUtil.isEmptyString(para.ParamCode)) {
            alert('');
            return false;
        }
        if (ValidateUtil.isEmptyString(para.ParamName)) {
            alert('');
            return false;
        }
        return this.validateCustomType(`${para.ParamName}`, para);
    }
}



