import { Component, Input, OnInit, ViewChild, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import {
  GSPBizEntityElement, GSPAssociation, GSPBizEntityObject, ViewElement, GSPElementObjectType, GSPBusinessEntity
} from '../../util/be-reference';
import { GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';
import { EnumEditComponent } from '../enum-edit/enum-edit.component';
import { Subscription } from 'rxjs/Subscription';
import { AssociationEditComponent } from '../association-edit/association-edit.component';

@Component({
  selector: 'app-object-type-editor',
  templateUrl: './object-type-editor.component.html',
  styleUrls: ['./object-type-editor.component.css']
})
export class ObjectTypeEditorComponent implements OnInit {

  public formGroup: FormGroup;

  @Input() bizEntity: GSPBusinessEntity;
  @Input() currentObject: GSPBizEntityObject;
  @Output() changeObjType: EventEmitter<GSPElementObjectType> = new EventEmitter<GSPElementObjectType>();
  @Output() closeModalEmitter: EventEmitter<any> = new EventEmitter<any>();

  /**
   * ()
   */
  canEdit: boolean;

  get currentElement(): GSPBizEntityElement {
    if (isNullOrUndefined(this.viewElement)) {
      return null;
    }
    return this.viewElement.toJS();
  }

  @ViewChild('grid') grid: GridComponent;
  @ViewChild('associationComponent') associationComponent: AssociationEditComponent;
  @ViewChild('enumsComponent') enumsComponent: EnumEditComponent;

  IsObjectTypeNone = false;
  IsObjectTypeAsso = false;
  IsObjectTypeEnum = false;

  /**
   * 
   * Subscription
   */
  private formGroupSubscriptions: any[] = new Array<Subscription>();

  isNew = false;
  active = false;

  viewElement: ViewElement;
  element: GSPBizEntityElement;
  assoValues: GSPAssociation[];
  tempAssos: GSPAssociation[];
  public seletedKeys: any[] = [];
  public selectableSettings: SelectableSettings = {
    checkboxOnly: false,
    mode: 'single'
  };

  get canAdd(): boolean {
    if (isNullOrUndefined(this.assoValues)) {
      return true;
    }
    return this.assoValues.length < 1;
  }

  ngOnInit(): void {
    this.load(this.viewElement, this.canEdit);
  }

  constructor(private formBuilder: FormBuilder) {
  }

  load(viewEle: ViewElement, canEdit: boolean) {
    this.active = true;
    this.viewElement = viewEle;
    this.canEdit = canEdit;
    this.createForm();

    this.element = <GSPBizEntityElement>viewEle.toJS();
    this.setFormValue(this.element.ObjectType);
    // 
    if (!this.canEdit) {
      this.formGroup.disable();
    } else {
      this.formGroup.enable();
    }
  }

  /**
   * 
   */
  private createForm() {
    this.formGroup = this.formBuilder.group({
      objectType: GSPElementObjectType.None,
      IsObjectTypeNone: false,
      IsObjectTypeAsso: false,
      IsObjectTypeEnum: false,
    });
  }

  /**
   * 
   * @param action
   */
  private setFormValue(objectType: GSPElementObjectType) {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(objectType)) {
      this.clearFormValue();
      return;
    }
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.enable();
    this.formGroup.patchValue({
      objectType: objectType
    });
    this.subscribeFormGroupValueChanges();
  }
  /**
   * 
   */
  private clearFormValue() {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.disable();
    this.formGroup.patchValue({
      objectType: GSPElementObjectType.None
    });
    this.subscribeFormGroupValueChanges();
  }

  /**
   * 
   */
  private subscribeFormGroupValueChanges() {
    const subscriptionObjectType = this.formGroup.controls.objectType.valueChanges.subscribe(type => {
      this.element.ObjectType = type;
    });

    this.formGroupSubscriptions.push(subscriptionObjectType);
  }

  /**
   * 
   */
  private unsubscribeFormGroupValueChanges() {
    if (this.formGroupSubscriptions && this.formGroupSubscriptions.length > 0) {
      while (this.formGroupSubscriptions.length > 0) {
        const subscription = this.formGroupSubscriptions.pop();
        subscription.unsubscribe();
      }
    }
  }

  /**
   * 
   */
  getCurrentObjecttTypeEditor() {
    switch (this.element.ObjectType) {
      case GSPElementObjectType.Enum:
        return this.enumsComponent;
      case GSPElementObjectType.Association:
        return this.associationComponent;
    }
  }

  /**
   * 
   * @param e
   */
  public onConfirm(): void {
    const childEditor = this.getCurrentObjecttTypeEditor();
    if (!isNullOrUndefined(childEditor)) {
      // 
      if (!childEditor.checkBeforeSave()) {
        return;
      } else {
        // 
        childEditor.onConfirm();
      }
    }

    // 
    this.viewElement.objectType = this.element.ObjectType;
    // const type = this.viewElement.objectType;
    // if (type !== GSPElementObjectType.Association) {
    //   this.viewElement.clearAssociations();
    // }
    // if (type !== GSPElementObjectType.Enum) {
    //   this.viewElement.clearEnumValues();
    // }

    this.changeObjType.next(this.element.ObjectType);

    this.close();
  }

  /**
   * 
   */
  public onCancel(): void {
    this.close();
  }

  /**
   * 
   */
  private closeForm(): void {
    this.close();
  }

  private close() {
    this.active = false;
    this.closeModalEmitter.next();
  }
}




