import { List, Map } from 'immutable';

import { ViewOperationList } from './viewOperationList';
import { ViewDetermination } from './viewDetermination';

import { Determination } from '../../ref';

export class ViewDeterminationList extends ViewOperationList {

    constructor(bizActionArray: Determination[] | List<any>) {
        super(bizActionArray);
    }

    protected getViewOperation(dtm: Map<string, any>): ViewDetermination {
        return new ViewDetermination(dtm);
    }

    changeIndexByIds(id1: string, id2: string) {
        const index1 = this.getIndexByID(id1);
        const index2 = this.getIndexByID(id2);
        if (index1 === -1 || index2 === -1 || index1 === index2) {
            return;
        }
        const max = index1 > index2 ? index1 : index2;
        const min = index1 > index2 ? index2 : index1;
        let list1;
        let list2;
        let list3;
        if (min === 0) {
            list1 = List();
        } else {
            list1 = this.data.slice(0, min);
        }
        list2 = this.data.slice(min + 1, max);
        if (max === this.length) {
            list3 = List();
        } else {
            list3 = this.data.slice(max + 1, this.length);
        }
        const list = list1.concat(this.get(max)).concat(list2).concat(this.get(min)).concat(list3);
        this.data = List(list);
        this.refreshViewLayer();
    }
}



