import {
  Component, OnInit, OnChanges, Input, ViewChild, SimpleChanges
} from '@angular/core';
import {
  BizActionBase, ViewAuthFiled, AuthFieldActionInfo,
  AuthConst, ViewAuthFieldActionInfo, AuthFieldInfo
} from '../../util/be-reference';
import { isNullOrUndefined } from 'util';
import { AuthoperationEntity } from '../../refModule/authorization/entity/authoperationentity';

@Component({
  selector: 'app-auth-op-grid',
  templateUrl: './auth-op-grid.component.html',
  styleUrls: ['./auth-op-grid.component.css']
})
export class AuthOpGridComponent implements OnInit, OnChanges {

  /**
   * []-
   */
  @Input() bizActions: BizActionBase[];

  /**
   * []-
   */
  @Input() viewAuthFieldInfo: ViewAuthFiled;

  /**
   * []--AuthOp
   */
  authActionInfos: AuthFieldActionInfo[];

  /**
   * []--BizAction
   */
  authActionInfosDisplay: AuthFieldActionInfo[];
  authActionSelection = [];

  /**
   * 
   */
  showAuthOpWindow = false;

  get authorizationId(): string {
    if (this.viewAuthFieldInfo) {
      const authFieldInfo = <AuthFieldInfo>this.viewAuthFieldInfo.toJS();
      return authFieldInfo.AuthorizationID;
    }
  }

  get authfield(): string {
    if (this.viewAuthFieldInfo) {
      const authFieldInfo = <AuthFieldInfo>this.viewAuthFieldInfo.toJS();
      return authFieldInfo.AuthFieldID;
    }
  }

  constructor() { }

  ngOnInit() {
    this.load();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.load();
  }

  load() {
    if (isNullOrUndefined(this.viewAuthFieldInfo) || isNullOrUndefined(this.viewAuthFieldInfo.authActionInfos)) {
      this.clearAll();
      return;
    }
    this.authActionInfos = this.viewAuthFieldInfo.authActionInfos.toJS();
    this.initDisplay();
    this.viewAuthFieldInfo.authActionInfos.refreshView.subscribe(item => this.load());
  }

  clearAll() {
    this.authActionInfos = [];
  }

  /**
   * []
   * bizAction
   * []
   */
  initDisplay() {
    if (this.bizActions && this.bizActions.length > 0) {
      const display = new Array<AuthFieldActionInfo>();
      for (let i = 0; i < this.bizActions.length; i++) {
        const bizAction = this.bizActions[i];
        const bizActionId = bizAction.ID;
        const newInfo = new AuthFieldActionInfo();
        newInfo.ActionID = bizActionId;
        if (this.authActionInfos && this.authActionInfos.length !== 0) {
          // authOpInfo,
          const authOpInfo = this.authActionInfos.find(authActionInfo => authActionInfo.ActionID === bizActionId);
          if (authOpInfo) {
            newInfo.OperationID = authOpInfo.OperationID;
            newInfo.OperationName = authOpInfo.OperationName;
          }
        }

        // 
        display.push(newInfo);
      }
      this.authActionInfosDisplay = display;
      return;
    }
    this.authActionInfosDisplay = [];
  }

  /**
   * 
   */
  getResultAuthActions(): AuthFieldActionInfo[] {
    if (this.authActionInfos && this.authActionInfos.length > 0) {
      const result = new Array<AuthFieldActionInfo>();
      for (let i = 0; i < this.authActionInfos.length; i++) {
        const auth = this.authActionInfos[i];
        if (auth.OperationID && auth.OperationID.length > 0) {
          result.push(auth);
        }
      }
    }
    return null;
  }

  /**
   * []
   */
  editAuthActionInfo() {
    if (this.authActionSelection.length === 0) {
      alert('');
      return;
    }
    this.showAuthOpWindow = !this.showAuthOpWindow;
  }

  /**
   * []
   */
  clearAuthActionInfo() {
    if (this.authActionSelection.length === 0) {
      alert('');
      return;
    }
    const currentActionId = this.authActionSelection[0];
    if (this.authActionInfos.length > 0) {
      const index = this.authActionInfos.findIndex(item => item.ActionID === currentActionId);
      if (index !== -1) {
        this.viewAuthFieldInfo.authActionInfos.remove(index);
      }
    }
  }

  /**
   * BizAction
   */
  getBizActionName(dataItem: AuthFieldActionInfo) {
    const actionId = dataItem.ActionID;
    return this.bizActions.find(action => action.ID === actionId).Name;
  }

  /**
   * 
   * @param result
   */
  getAuthOp(result: AuthoperationEntity) {
    if (isNullOrUndefined(result)) {
      this.clearAuthActionInfo();
      return;
    }
    const actionId = this.authActionSelection[0];
    const newInfo = new AuthFieldActionInfo();
    newInfo.OperationID = result.id;
    newInfo.OperationName = result.name;
    newInfo.ActionID = actionId;
    if (isNullOrUndefined(this.authActionInfos)) {
      this.authActionInfos = new Array<AuthFieldActionInfo>();
    }
    const index = this.authActionInfos.findIndex(item => item.ActionID === actionId);
    if (index === -1) {
      // 
      this.viewAuthFieldInfo.authActionInfos.add(new ViewAuthFieldActionInfo(newInfo));
    } else {
      // 
      this.viewAuthFieldInfo.authActionInfos.update(index, oldValue => {
        return new ViewAuthFieldActionInfo(newInfo);
      });
    }
  }

  cellClickHandler(args: any) {
    const { sender, rowIndex, columnIndex, column, dataItem, isEdited, isNew } = args;

    //  
    switch (column.field) {
      case AuthConst.OperationName:
      case 'ActionID':
        this.editAuthActionInfo();
        break;
    }
  }
}



