import { ElementPropertyConfig } from '@farris/ide-devkit';
import { BizElementConst, BizObjectConst, BizEntityConst } from '../util/be-reference';

export enum BePropertyBagType {
  Element,
  Object,
  Model
}


export class BePropertyBag {
  /**
   * 
   */
  static currentBePropertyBagType = BePropertyBagType.Object;

  /**
   * 
   */
  private static bizElementCategoryId = 'bizElementCategoryId';
  private static bizElementCategoryName = '';

  public static BizElementPropertyConfig: ElementPropertyConfig[] = [
    {
      categoryId: BePropertyBag.bizElementCategoryId,
      categoryName: BePropertyBag.bizElementCategoryName,
      properties: [
        {
          propertyID: BizObjectConst.ID,
          propertyName: '',
          propertyType: 'string',
          description: '',
          defaultValue: '',
          readonly: true,
          visible: true
        },
        {
          propertyID: BizElementConst.Readonly,
          propertyName: '',
          propertyType: 'boolean',
          description: '',
          defaultValue: '',
          readonly: false,
          visible: true
        },
        {
          propertyID: BizElementConst.IsRequire,
          propertyName: '',
          propertyType: 'boolean',
          description: '',
          defaultValue: '',
          readonly: false,
          visible: true
        }
      ]
    }];

  /**
   * 
   */
  private static bizObjectCategoryId = 'bizObjectCategoryId';
  private static bizObjectCategoryName = '';
  public static BizObjectPropertyConfig: ElementPropertyConfig[] = [
    {
      categoryId: BePropertyBag.bizObjectCategoryId,
      categoryName: BePropertyBag.bizObjectCategoryName,
      properties: [
        {
          propertyID: BizObjectConst.ID,
          propertyName: '',
          propertyType: 'string',
          description: '',
          defaultValue: '',
          readonly: true,
          visible: true
        },
        {
          propertyID: BizObjectConst.Code,
          propertyName: '',
          propertyType: 'string',
          description: '',
          defaultValue: '',
          readonly: false,
          visible: true
        },
        {
          propertyID: BizObjectConst.Name,
          propertyName: '',
          propertyType: 'string',
          description: '',
          defaultValue: '',
          readonly: false,
          visible: true
        }
      ]
    }];

  /**
   * 
   */
  private static bizEntityCategoryId = 'bizEntityCategoryId';
  private static bizEntityCategoryName = '';
  public static BizEntityPropertyConfig: ElementPropertyConfig[] = [
    {
      categoryId: BePropertyBag.bizEntityCategoryId,
      categoryName: BePropertyBag.bizEntityCategoryName,
      properties: [
        {
          propertyID: BizEntityConst.ID,
          propertyName: '',
          propertyType: 'string',
          description: '',
          defaultValue: '',
          readonly: true,
          visible: true
        },
        {
          propertyID: BizEntityConst.Code,
          propertyName: '',
          propertyType: 'string',
          description: '',
          defaultValue: '',
          readonly: false,
          visible: true
        },
        {
          propertyID: BizEntityConst.Name,
          propertyName: '',
          propertyType: 'string',
          description: '',
          defaultValue: '',
          readonly: false,
          visible: true
        },
        {
          propertyID: BizEntityConst.GeneratingAssembly,
          propertyName: '',
          propertyType: 'string',
          description: '',
          defaultValue: '',
          readonly: false,
          visible: true
        }
      ]
    }];

}



