import { BECategory } from './Enum/BECategory';
import { GSPDataLockType } from './Enum/GSPDataLockType';
import { GSPBizEntityObject } from './GSPBizEntityObject';
import { isNullOrUndefined } from 'util';
import { BizMgrAction } from './Operation/BizMgrAction';
import { GSPCommonModel } from '../../CommonModel';
import { BizAction } from './Operation/BizAction';
import { Determination } from './Operation/Determination';
import { Validation } from './Operation/Validation';
import { BizOperation } from './Operation/BizOperation';
import { AuthorizationInfo } from './Authority/AuthorizationInfo';



/**
* BE
* @author wangjiegj<wangjiegj@inspur.com>
 */


/**
* BE
*/
export class GSPBusinessEntity extends GSPCommonModel {
    /**
     * 
     */
    DependentEntityPackageName: string;

    /**
     * 
     */
    ComponentAssemblyName: string;
    AssemblyName: string;

    /**
     * BE
     */
    Category: BECategory;

    /**
     * BE
     */
    SourceEntity: GSPBusinessEntity;

    /**
     * BE
     */
    MainObject: GSPBizEntityObject;

    /**
     * 
     * 
     *  
     */
    DataLockType: GSPDataLockType;

    /**
     * 
     */
    get ExtendType() {
        return 'GSPBusinessEntity';
    }

    /**
     * id
     */
    DependentEntityId: string;
    DependentEntityName: string;

    /**
     * 
     */
    IsUsingTimeStamp: boolean;

    /**
     * BEManagerAction
     */
    BizMgrActions: BizMgrAction[];

    /**
     * BEID
     */
    Authorizations: AuthorizationInfo[];

    static getAllMgrActions(be: GSPBusinessEntity) {
        return be.BizMgrActions;
    }

    /**
     * bizActions
     */
    static getAllBizEntityActions(be: GSPBusinessEntity): BizAction[] {
        return this.getChidrenObjectsActions(be.MainObject);
    }

    static getChidrenObjectsActions(bizObject: GSPBizEntityObject): BizAction[] {
        const result = new Array<BizAction>();
        if (isNullOrUndefined(bizObject)) {
            return result;
        }
        if (bizObject.BizActions && bizObject.BizActions.length > 0) {
            result.push(...bizObject.BizActions);
        }
        if (bizObject.ContainChildObjects && bizObject.ContainChildObjects.length > 0) {
            for (let i = 0; i < bizObject.ContainChildObjects.length; i++) {
                result.push(...this.getChidrenObjectsActions(bizObject.ContainChildObjects[i] as GSPBizEntityObject));
            }
        }
        return result;
    }

    /**
     * determination
     */
    static getAllDeterminations(be: GSPBusinessEntity): Determination[] {
        return this.getChidrenObjectsDeterminations(be.MainObject);
    }

    static getChidrenObjectsDeterminations(bizObject: GSPBizEntityObject): Determination[] {
        const result = new Array<Determination>();
        if (isNullOrUndefined(bizObject)) {
            return result;
        }
        if (bizObject.Determinations && bizObject.Determinations.length > 0) {
            result.push(...bizObject.Determinations);
        }
        if (bizObject.ContainChildObjects && bizObject.ContainChildObjects.length > 0) {
            for (let i = 0; i < bizObject.ContainChildObjects.length; i++) {
                result.push(...this.getChidrenObjectsDeterminations(bizObject.ContainChildObjects[i] as GSPBizEntityObject));
            }
        }
        return result;
    }

    /**
     * validation
     */
    static getAllValidations(be: GSPBusinessEntity): Validation[] {
        return this.getChidrenObjectsValidations(be.MainObject);
    }

    static getChidrenObjectsValidations(bizObject: GSPBizEntityObject): Validation[] {
        const result = new Array<Validation>();
        if (isNullOrUndefined(bizObject)) {
            return result;
        }
        if (bizObject.Validations && bizObject.Validations.length > 0) {
            result.push(...bizObject.Validations);
        }
        if (bizObject.ContainChildObjects && bizObject.ContainChildObjects.length > 0) {
            for (let i = 0; i < bizObject.ContainChildObjects.length; i++) {
                result.push(...this.getChidrenObjectsValidations(bizObject.ContainChildObjects[i] as GSPBizEntityObject));
            }
        }
        return result;
    }

    /**
     * 
     */
    static getAllOperations(be: GSPBusinessEntity): BizOperation[] {
        const result = new Array<BizOperation>();
        result.push(...this.getAllBizEntityActions(be));
        result.push(...this.getAllMgrActions(be));
        result.push(...this.getAllDeterminations(be));
        result.push(...this.getAllValidations(be));
        return result;
    }

    /**
     * validation
     * @param eleId ID
     */
    static countainValidationRequestElement(be: GSPBusinessEntity, eleId: string): string {
        const validations = this.getAllValidations(be);
        if (!isNullOrUndefined(validations) && validations.length > 0) {
            for (let i = 0; i < validations.length; i++) {
                const b = Validation.countainRequestElement(validations[i], eleId);
                if (b) {
                    const result = validations[i].Name;
                    return result;
                }
            }
        }
        return null;
    }

    /**
     * determination
     * @param eleId ID
     */
    static countainDeterminationRequestElement(be: GSPBusinessEntity, eleId: string): string {
        const dtms = this.getAllDeterminations(be);
        if (!isNullOrUndefined(dtms) && dtms.length > 0) {
            for (let i = 0; i < dtms.length; i++) {
                const b = Determination.countainRequestElement(dtms[i], eleId);
                if (b) {
                    const result = dtms[i].Name;
                    return result;
                }
            }
        }
        return null;
    }
}



