import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { of } from 'rxjs/observable/of';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';
import { AuthorizationService } from '../../service/authorization.service';
import { FuncoperationEntity } from '../../entity/funcoperationentity';

@Component({
  selector: 'app-funcoperation-tree',
  templateUrl: './funcoperation-tree.component.html',
  styleUrls: ['./funcoperation-tree.component.css']
})
export class FuncoperationTreeComponent implements OnInit {

  expandedKeys: string[] = [];
  selectedItem: FuncoperationEntity;
  datas: FuncoperationEntity[] = [];
  @Output() funcoperationEntity: FuncoperationEntity;
  treeElement: FuncoperationEntity;

  // @Input()
  @Input() authorizationId: string;
  @Input() authfield: string;

  constructor(private authorizationService: AuthorizationService) { }

  ngOnInit() {
    this.treeElement = new FuncoperationEntity();
    this.authorizationService.GetFuncoperations().subscribe(data => {
      let arr = new Array();
      arr = data;
      arr.forEach(item => {
        if (item.children && item.children.length > 0) {
          this.datas.push(item);
        }
      });
    });
  }

  dialogOk() {
    return this.selectedItem;
  }


  public hasChildren = (item: any) => item.children && item.children.length > 0;
  public fetchChildren = (item: any) => of(item.children);

  //  
  iconClass({ type }: any): any {
    return {
      'k-i-folder': type.toString() !== '1',
      'k-i-gear': type.toString() === '1',
      'k-icon': true
    };
  }

  // 
  handleSelection({ dataItem }: any): void {
    this.selectedItem = dataItem;
  }

}




