import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';

import { MapBase } from '../base/mapBase';
import { ViewObject } from '../viewObject/viewObject';
import { ViewBizMgrActionList } from '../viewOperation/viewActionBaseList';
import { ViewAuthorizationInfoList } from '../viewAuth/viewAuthoritionInfo';
import { ViewAssociationList } from '../viewAssociation/viewAssociationList';

import { GSPBusinessEntity, BECategory, BizEntityConst } from '../../ref';

export class ViewModel extends MapBase {

    // AssemblyName->ComponentAssemblyName
    private assemblyName = 'Inspur.Scm.SD.SalesOrder';

    /**
     * 
     */
    refreshName: Subject<boolean> = new Subject<boolean>();

    /**
     * 
     * @param bizEntity
     */
    constructor(bizEntity: GSPBusinessEntity) {
        super(bizEntity);
        this.data = this.initBizEntity(this.data);
    }

    /**
     * Map
     * @param map
     */
    private initBizEntity(map: Map<string, any>) {
        if (isNullOrUndefined(map)) {
            return;
        }
        const vm = map.update(
            BizEntityConst.MainObject, mainObject => this.getViewObject(mainObject as Map<string, any>)
        ).update(
            BizEntityConst.BizMgrActions, bizActions => this.getViewMgrActions(bizActions as List<any>)
        ).update(
            BizEntityConst.Authorizations, authInfos => this.getViewAuthInfos(authInfos as List<any>)
        );
        return vm;
    }

    /**
     * Map
     * @param bizObject
     */
    private getViewObject(bizObject: Map<string, any>): ViewObject {
        if (isNullOrUndefined(bizObject)) {
            return;
        }
        const vo = new ViewObject(bizObject);
        return vo;
    }

    private getViewMgrActions(mgrBizActions: List<any>) {
        if (isNullOrUndefined(mgrBizActions)) {
            return;
        }
        const viewMgrActions = new ViewBizMgrActionList(mgrBizActions);
        return viewMgrActions;
    }

    private getViewAuthInfos(authInfos: List<any>) {
        if (isNullOrUndefined(authInfos)) {
            return;
        }
        const viewAuthInfos = new ViewAuthorizationInfoList(authInfos);
        return viewAuthInfos;
    }

    /**
     * MgrAction
     */
    get mgrActions(): ViewBizMgrActionList {
        const viewMgrActions = this.get(BizEntityConst.BizMgrActions);
        if (isNullOrUndefined(viewMgrActions)) {
            this.set(BizEntityConst.BizMgrActions, new ViewBizMgrActionList(null));
        }
        return viewMgrActions;
    }

    /**
     * 
     */
    get authrizations(): ViewAuthorizationInfoList {
        const authrizations = this.get(BizEntityConst.Authorizations);
        if (isNullOrUndefined(authrizations)) {
            this.set(BizEntityConst.BizMgrActions, new ViewAssociationList(null));
        }
        return authrizations;
    }

    set authrizations(auths: ViewAuthorizationInfoList) {
        this.set(BizEntityConst.Authorizations, auths);
    }

    get mainObject(): ViewObject {
        return this.get(BizEntityConst.MainObject);
    }

    get ID(): string {
        return this.get(BizEntityConst.ID);
    }

    get Code(): string {
        return this.get(BizEntityConst.Code);
    }

    set Code(code: string) {
        this.set(BizEntityConst.Code, code);
    }

    get Name(): string {
        return this.get(BizEntityConst.Name);
    }

    set Name(name: string) {
        this.set(BizEntityConst.Name, name);
        this.refreshName.next(true);
    }
    get Category(): BECategory {
        const value = this.get(BizEntityConst.Category);
        if (this.isNullOrEmptyString(value)) {
            this.Category = BECategory.Standard;
            return this.Category;
        } else {
            return this.get(BizEntityConst.Category);
        }
    }

    set Category(beType: BECategory) {
        this.set(BizEntityConst.Category, beType);
    }

    get DependentEntityId(): string {
        return this.get(BizEntityConst.DependentEntityId);
    }

    set DependentEntityId(dbeID: string) {
        this.set(BizEntityConst.DependentEntityId, dbeID);
    }

    get DependentEntityName(): string {
        return this.get(BizEntityConst.DependentEntityName);
    }

    set DependentEntityName(dbeID: string) {
        this.set(BizEntityConst.DependentEntityName, dbeID);
    }

    get DependentEntityPackageName(): string {
        return this.get(BizEntityConst.DependentEntityPackageName);
    }

    set DependentEntityPackageName(pkgName: string) {
        this.set(BizEntityConst.DependentEntityId, pkgName);
    }

    get IsUsingTimeStamp(): boolean {
        return this.get(BizEntityConst.IsUsingTimeStamp);
    }

    set IsUsingTimeStamp(isUsingTimeStamp: boolean) {
        this.set(BizEntityConst.IsUsingTimeStamp, isUsingTimeStamp);
    }

    get ComponentAssemblyName(): string {
        const value = this.get(BizEntityConst.ComponentAssemblyName);
        if (this.isNullOrEmptyString(value)) {
            this.ComponentAssemblyName = this.assemblyName;
            return this.assemblyName;
        } else {
            return this.get(BizEntityConst.ComponentAssemblyName);
        }
    }

    set ComponentAssemblyName(assemblyName: string) {
        this.set(BizEntityConst.ComponentAssemblyName, assemblyName);
    }

    get GeneratingAssembly(): string {
        const value = this.get(BizEntityConst.GeneratingAssembly);
        if (this.isNullOrEmptyString(value)) {
            this.GeneratingAssembly = this.assemblyName;
            return this.assemblyName;
        } else {
            return this.get(BizEntityConst.GeneratingAssembly);
        }
    }

    set GeneratingAssembly(assemblyName: string) {
        this.set(BizEntityConst.GeneratingAssembly, assemblyName);
    }

    toJS(): GSPBusinessEntity {
        return super.toJS() as GSPBusinessEntity;
    }

    // AssemblyName->ComponentAssemblyName
    isNullOrEmptyString(value: string): boolean {
        if (isNullOrUndefined(value)) {
            return true;
        }
        if (value.length === 0) {
            return true;
        }
        return false;
    }
}



