import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ListBase } from '../base/listBase';
import { ViewOperation } from './viewOperation';

import { BizOperation } from '../../ref';

export class ViewOperationList extends ListBase<ViewOperation> {

    constructor(bizOperationArray: BizOperation[] | List<any>) {
        super(bizOperationArray);
        if (bizOperationArray) {
            this.data = this.getViewOperations(this.data);
        }
    }

    protected getViewOperation(operation: Map<string, any>): ViewOperation {
        return new ViewOperation(operation);
    }

    private getViewOperations(bizActionArray: List<any>): List<ViewOperation> {
        if (isNullOrUndefined(bizActionArray)) {
            return;
        }

        let viewMgrActionList = bizActionArray;

        if (bizActionArray.size > 0) {
            for (let i = 0; i < bizActionArray.size; i++) {
                viewMgrActionList = viewMgrActionList.update(i, () => this.getViewOperation(bizActionArray.get(i)));
            }
            return viewMgrActionList;
        }
    }
}



