import { Map } from 'immutable';

import { MapBase } from '../base/mapBase';

import { GSPAssociationKey, BizElementConst } from '../../ref';

export class ViewAssoKey extends MapBase {

    /**
     * 
     * @param element
     */
    constructor(asso: GSPAssociationKey | Map<string, any>) {
        super(asso);
    }

    updateSourceElement(id: string, name: string) {
        this.set(BizElementConst.SourceElement, id);
        this.set(BizElementConst.SourceElementDisplay, name);
    }

    updateTargetElement(id: string, name: string) {
        this.set(BizElementConst.TargetElement, id);
        this.set(BizElementConst.TargetElementDisplay, name);
    }
}



