import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ListBase } from '../base/listBase';
import { MapBase } from '../base/mapBase';
import { ViewOperation } from './viewOperation';
import { ViewRequestChildElementList } from './viewRequestChildElements/viewRequestChildElementList';

import { Validation, RequestNodeTriggerType, RequestChildElement, BizOperationConst } from '../../ref';

export class ViewValidation extends ViewOperation {

    constructor(validation: Validation | Map<string, any>) {
        super(validation);
        this.update(BizOperationConst.RequestElements, values => this.getRequestElements(values));
        this.update(BizOperationConst.RequestChildElements, values => this.getRequestChildElements(values));
    }

    private getRequestElements(elementIds: List<any>): ListBase<MapBase> {
        return new ListBase<MapBase>(elementIds);
    }

    /**
     * 
     * @param list
     */
    private getRequestChildElements(list: RequestChildElement[] | List<any>): ViewRequestChildElementList {
        return new ViewRequestChildElementList(list);
    }

    get RequestNodeTriggerType(): RequestNodeTriggerType {
        if (isNullOrUndefined(this.get(BizOperationConst.RequestNodeTriggerType))) {
            this.RequestNodeTriggerType = RequestNodeTriggerType.None;
        }
        return this.get(BizOperationConst.RequestNodeTriggerType);
    }
    set RequestNodeTriggerType(value: RequestNodeTriggerType) {
        this.set(BizOperationConst.RequestNodeTriggerType, value);
    }

    get RequestElements(): ListBase<any> {
        return this.get(BizOperationConst.RequestElements);
    }
    set RequestElements(value: ListBase<any>) {
        this.set(BizOperationConst.RequestElements, value);
    }

    get RequestChildElements(): ListBase<any> {
        return this.get(BizOperationConst.RequestChildElements);
    }
    set RequestChildElements(value: ListBase<any>) {
        this.set(BizOperationConst.RequestChildElements, value);
    }
}



