import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import {
  GSPAssociationKey, RefViewElementList,
  GSPBizEntityElement, ViewAssoKey, ViewAssoKeyList, ViewModel, BizElementConst, ViewObject, ViewElementList,
  GSPBizEntityObject, ViewAssociationList, ViewElement, BizEntityConst, GSPBusinessEntity, GSPCommonObject, IGSPCommonObject,
  ViewAssociation, IGSPCommonElement
} from '../../../util/be-reference';
import { isNullOrUndefined } from 'util';
import { Subscription } from 'rxjs/Subscription';
import { List, Map, fromJS } from 'immutable';

/**
 * 6.1
 * ContainElementsContainElements
 * ContainElements
 * ##  
 *
 *  20180717,
 *  // 1AssoKey
 *  // [TargetElement:]
 *  // [SourceElement:ID]
 */

@Component({
  selector: 'app-association-element-gird',
  templateUrl: './association-element-gird.component.html',
})
export class AssociationElementGirdComponent implements OnInit, OnDestroy {

  /**
   *  or 
   */
  @Input() public isNew = false;
  @Input() currentElement: GSPBizEntityElement;
  @Input() currentObject: GSPBizEntityObject;

  /**
   * 
   */
  @Input() viewAsso: ViewAssociation;

  /**
   * 
   */
  @Input() public refModel: GSPBusinessEntity;

  /**
   * 
   */
  assoKeys: GSPAssociationKey[];
  currentTargetElements: GSPBizEntityElement[];
  refBizEntityElements: GSPBizEntityElement[];

  constructor(private formBuilder: FormBuilder) {
  }

  ngOnInit() {
    this.load();
  }

  ngOnDestroy(): void {
  }

  refresh(refModel: GSPBusinessEntity) {
    if (!isNullOrUndefined(refModel) && !isNullOrUndefined(this.currentElement)) {
      this.refModel = refModel;
      this.load();
    } else {
      this.assoKeys = null;
    }
  }

  private load() {
    if (this.viewAsso && this.viewAsso.viewAssoKeys) {
      this.assoKeys = this.viewAsso.viewAssoKeys.toJS();
    }
    if (this.refModel) {
      const refElementID = this.refModel.MainObject.ColumnGenerateID.ElementID;
      const refElement = this.refModel.MainObject.ContainElements.find(ele => ele.ID === refElementID);
      const newAssoKey = new GSPAssociationKey();
      newAssoKey.RefdataModelName = this.refModel.Name;
      newAssoKey.SourceElement = refElement.ID;
      newAssoKey.SourceElementDisplay = refElement.Name;
      newAssoKey.TargetElement = this.currentElement.ID;
      newAssoKey.TargetElementDisplay = this.currentElement.Name;

      this.assoKeys = new Array<GSPAssociationKey>();
      this.assoKeys.push(newAssoKey);
    }
  }

  confirm() {
    if (isNullOrUndefined(this.viewAsso)) {
      return;
    }
    this.viewAsso.viewAssoKeys = new ViewAssoKeyList(this.assoKeys);
  }
}



