import { ViewChild, TemplateRef, Component, OnInit, OnChanges, EventEmitter, Input, Output, SimpleChanges } from '@angular/core';
import {
  MetadataDto, GSPMetadataService, GSPBusinessEntity, Metadata4RefDto, BECategory, ViewModel, BECategories, HttpUrlConst, MetadataTypeName
} from '../../util/be-reference';
// import { TreeModel } from '../../../../../../packages/file-tree-panel-package/models/tree-model';
import { isNullOrUndefined } from 'util';
import { BeInitWithDbeService } from '../../service/be-init-with-dbe';

@Component({
  selector: 'app-be-designer-guide',
  templateUrl: './be-designer-guide.component.html',
  styleUrls: ['./be-designer-guide.component.css'],
  providers: [BeInitWithDbeService]
})
export class BeDesignerGuideComponent implements OnInit, OnChanges {

  @ViewChild('btn') btns: TemplateRef<any>;

  @Input() triggerShowWindow: boolean;

  /**
   * be(dbe)
   */
  beTypeName = MetadataTypeName.GSPBusinessEntity;

  /**
   * 
   */
  createMetadataEvent: EventEmitter<any> = new EventEmitter<any>();

  /**
   * 
   */
  closeModal = new EventEmitter<any>();

  /**
   * 
   */
  beTypes = BECategories;
  beType = BECategory.Standard;

  /**
   * 
   */
  useDbeTypes: Array<{ text: string, value: boolean }> = [
    { text: '', value: true },
    { text: '', value: false },
  ];
  useDbe = false;

  /**
   * 
   */
  showLoadMetadataWindow = false;

  /**
   * 
   */
  _metadataContnet: MetadataDto;

  /**
   * be
   */
  dependentInfo: BeGuideInfo = new BeGuideInfo();

  /**
   * 
   */
  createMetadataSuccess: EventEmitter<any> = new EventEmitter<any>();
  path: any;
  projInfo: any;
  metadata = {
    nameSpace: '',
    code: '',
    name: '',
    bizobjectID: '',
    boCode: '',
    location: ''
  };

  set metadataContent(metadataContent: any) {
    this._metadataContnet = metadataContent;
    this.load(metadataContent);
  }

  /**
   * 
   */
  get dependentBeName(): string {
    return this.dependentInfo.dbeName ? this.dependentInfo.dbeName : '';
  }

  showGuideWindow = false;

  // tslint:disable-next-line:max-line-length
  // constructor(treeModel: TreeModel, gspMetadataService: GSPMetadataService, initBeService: BeInitWithDbeService) {
  constructor(private gspMetadataService: GSPMetadataService, private initBeService: BeInitWithDbeService) {
    this.createMetadataEvent = new EventEmitter<any>();
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!this.showGuideWindow) {
      this.showModal();
    }
  }

  initNameSpace(): any {
    this.metadata.location = this.path.replace(/\\/g, '/');
    this.gspMetadataService.GetProjectInfo(this.path.substring(1)).subscribe(data => {
      this.projInfo = data;
      if (this.projInfo) {
        this.metadata.nameSpace = this.projInfo.nameSpace;
      }
      this.gspMetadataService.GetGspProjInfo(this.projInfo.projectPath).subscribe(item => {
        if (item) {
          this.metadata.bizobjectID = item.bizobjectID;
          this.metadata.boCode = item.bizobjectCode;
        }
      });
    });

  }

  showModal(): void {
    this.showGuideWindow = true;
  }

  load(data: MetadataDto): any {
    this.showModal();
    HttpUrlConst.currentPath = data.relativePath;
  }

  createBe() {
    // webApi-CreateMetadata()
    //  
    //  be/

    const metadatadto = this._metadataContnet;
    // 
    this.gspMetadataService.InitializeMetadataEntity(metadatadto).subscribe(response => {
      //  id
      metadatadto.id = response.id;
      this.dependentInfo.isStandardBe = this.beType === BECategory.Standard;
      this.dependentInfo.content = response.content;
      this.initBeService.init(this.dependentInfo).subscribe(beJson => {
        const be = beJson.json();
        //  content=be
        metadatadto.content = JSON.stringify(be);
        this.gspMetadataService.CreateMetadata(metadatadto).subscribe(saveResponse => {
          this.createMetadataEvent.emit(metadatadto);
          // 
          // this.treeModel.addFileNode(metadatadto.id, metadatadto.fileName, '/' + metadatadto.relativePath + '/' + metadatadto.fileName);
          this.createMetadataSuccess.emit({
            action: '1',
            node: {
              nodeID: metadatadto.fileName,
              nodeName: metadatadto.fileName,
              nodePath: this.metadata.location + '/' + metadatadto.fileName,
              nodeType: 'file'
            }
          });

          alert('!');
        }, error => {
          alert('!');
        });
      }, error => {
        alert(',!');
      });
    }, error => {
      alert(',!');
    });
  }

  /**
   * 
   */
  closeWindow() {
    this.showGuideWindow = false;
    this.closeModal.emit();
  }

  /**
   * 
   */
  commit() {
    if (this.useDbe && isNullOrUndefined(this.dependentInfo.dbeID)) {
      alert('');
      return;
    }
    this.closeWindow();
    this.createBe();
  }

  /**
   * 
   */
  cancel() {
    this.closeWindow();
  }

  /**
   * 
   */
  selectBeTypeChange(newType: BECategory) {
    this.beType = newType;
  }

  /**
   * be
   */
  selectDependentBe() {
    if (this.useDbe) {
      this.showLoadMetadataWindow = !this.showLoadMetadataWindow;
    }
  }


  /**
   * [be]bebe
   * @param dbeMetadata
   */
  GetDbeMetadata(dbeMetadata: Metadata4RefDto) {
    // 
    if (isNullOrUndefined(dbeMetadata)) {
      this.clearDbeInfo();
      return;
    }

    // 
    const be = JSON.parse(dbeMetadata.metadata.content) as GSPBusinessEntity;

    if (be.Category !== BECategory.DependentBusinessObject) {
      alert('[]');
      return;
    }
    // 
    if (dbeMetadata.packageHeader) {
      this.dependentInfo.dbePkgName = dbeMetadata.packageHeader.name;
    } else {
      this.dependentInfo.dbePkgName = '';
    }
    // 
    this.dependentInfo.dbeID = dbeMetadata.metadata.id;
    this.dependentInfo.dbeName = dbeMetadata.metadata.name;
    this.dependentInfo.dbePath = dbeMetadata.metadata.relativePath;
    this.dependentInfo.dbeFileName = dbeMetadata.metadata.fileName;
  }

  clearDbeInfo() {
    this.dependentInfo.dbePkgName = null;
    this.dependentInfo.dbeID = null;
    this.dependentInfo.dbeName = null;
    this.dependentInfo.dbePath = null;
    this.dependentInfo.dbeFileName = null;
  }

  /**
   * 
   * @param $event
   */
  selectUseDbeChange(useDbe: boolean) {
    this.useDbe = useDbe;
    if (!useDbe) {
      this.clearDbeInfo();
    }
  }
}

/**
 * 
 */
export class BeGuideInfo {
  // dbe
  dbePath: string;
  dbeFileName: string;

  // be-be,be
  isStandardBe: boolean;

  // 
  dbePkgName: string;
  dbeID: string;
  dbeName: string;

  // path: string;
  // fileName: string;
  content: string;
}




