    // get ObjectType() { if (!this._objectType) { this._objectType = GSPElementObjectType.None } return this._objectType; }
    // set ObjectType(value: any) { this._objectType = value; }

    /**
     * 
     */
    public ChildAssociations: GSPAssociation[];
    // private _childAssociations: GSPAssociation[];
