import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';

import { MapBase } from '../base/mapBase';
import { ViewElementList } from '../viewElement/viewElementList';
import { ListBase } from '../base/listBase';
import { ViewValidationList } from '../viewOperation/viewValidationList';
import { ViewDeterminationList } from '../viewOperation/viewDeterminationList';
import { ViewDetermination } from '../viewOperation/viewDetermination';
import { ViewBizActionList } from '../viewOperation/viewActionBaseList';
import { ViewAuthFiledInfoList } from '../viewAuth/viewAuthFiledInfo';

import {
    GSPBizEntityObject, GSPCommonObjectType, BizObjectConst
} from '../../ref';

export class ViewObject extends MapBase {

    /**
     * 
     */
    refreshName: Subject<boolean> = new Subject<boolean>();

    /**
     * 
     * @param object
     */
    constructor(object: GSPBizEntityObject | Map<string, any>) {
        super(object);
        if (isNullOrUndefined(object)) {
            return;
        }
        this.data = this.getCurrentViewObject(this.data);
    }

    /**
     * Map
     * @param bizObject
     */
    private getCurrentViewObject(bizObject: Map<string, any>): Map<string, any> {
        if (isNullOrUndefined(bizObject)) {
            return;
        }
        let vo = bizObject;
        vo = vo.update(BizObjectConst.ContainChildObjects, bizObjects => this.getChildrenViewObjects(bizObjects));
        vo = vo.update(BizObjectConst.ContainElements, elements => this.getElementViewList(elements));
        vo = vo.update(BizObjectConst.BizActions, actions => this.getViewActionList(actions));
        vo = vo.update(BizObjectConst.Validations, validations => this.getViewValidationList(validations));
        vo = vo.update(BizObjectConst.Determinations, determinations => this.getViewDeterminationList(determinations));
        vo = vo.update(BizObjectConst.AuthFieldInfos, authFields => this.getViewAuthFields(authFields));
        return vo;
    }

    private getElementViewList(bizElements: List<any>): ViewElementList {
        const elementList = new ViewElementList(bizElements);
        return elementList;
    }

    private getChildrenViewObjects(objectArray: List<any>): ViewObjectList {
        const objects = new ViewObjectList(objectArray);
        return objects;
    }

    private getViewActionList(actions: List<any>): ViewBizActionList {
        return new ViewBizActionList(actions);
    }

    private getViewValidationList(validations: List<any>): ViewValidationList {
        return new ViewValidationList(validations);
    }

    private getViewDeterminationList(determinations: List<any>): ViewDeterminationList {
        return new ViewDeterminationList(determinations);
    }

    private getViewAuthFields(authFields: List<any>): ViewAuthFiledInfoList {
        return new ViewAuthFiledInfoList(authFields);
    }

    get objectType(): GSPCommonObjectType {
        return this.get(BizObjectConst.ObjectType);
    }

    get elements(): ViewElementList {
        return this.get(BizObjectConst.ContainElements);
    }

    set elements(value: ViewElementList) {
        this.set(BizObjectConst.ContainElements, value);
    }

    get childrenObjects(): ViewObjectList {
        return this.get(BizObjectConst.ContainChildObjects);
    }

    get ID() {
        return this.get(BizObjectConst.ID);
    }

    get ParentObjectID() {
        return this.get(BizObjectConst.ParentObjectID);
    }


    get Code(): string {
        return this.get(BizObjectConst.Code);
    }

    set Code(code: string) {
        this.set(BizObjectConst.Code, code);
    }

    get Name(): string {
        return this.get(BizObjectConst.Name);
    }

    set Name(name: string) {
        this.set(BizObjectConst.Name, name);
        this.refreshName.next(true);
    }

    get authFields(): ViewAuthFiledInfoList {
        if (isNullOrUndefined(this.get(BizObjectConst.AuthFieldInfos))) {
            this.authFields = new ViewAuthFiledInfoList(null);
        }
        return this.get(BizObjectConst.AuthFieldInfos);
    }

    set authFields(authFields: ViewAuthFiledInfoList) {
        this.set(BizObjectConst.AuthFieldInfos, authFields);
    }

    addChildObject(childObject: ViewObject) {
        if (isNullOrUndefined(this.childrenObjects)) {
            const childrenList = this.getChildrenViewObjects(List());
            childrenList.add(childObject);
            this.set(BizObjectConst.ContainChildObjects, childrenList);
            return;
        } else {
            this.childrenObjects.add(childObject);
            this.refreshViewLayer();
        }
    }

    /**
     * MgrAction
     */
    get beActions(): ViewBizActionList {
        const viewBizActions = this.get(BizObjectConst.BizActions);
        if (isNullOrUndefined(viewBizActions)) {
            this.set(BizObjectConst.BizActions, new ViewBizActionList(null));
        }
        return viewBizActions;
    }

    /**
     * MgrAction
     */
    get validations(): ViewValidationList {
        const viewValidations = this.get(BizObjectConst.Validations);
        if (isNullOrUndefined(viewValidations)) {
            this.set(BizObjectConst.Validations, new ViewValidationList(null));
        }
        return viewValidations;
    }

    /**
     * Determination
     */
    get determinations(): ViewDetermination {
        const viewDeterminations = this.get(BizObjectConst.Determinations);
        if (isNullOrUndefined(viewDeterminations)) {
            this.set(BizObjectConst.Determinations, new ViewDeterminationList(null));
        }
        return viewDeterminations;
    }

}



export class ViewObjectList extends ListBase<ViewObject> {

    constructor(objectArray: GSPBizEntityObject[] | List<any>) {
        super(objectArray);
        if (isNullOrUndefined(objectArray)) {
            return;
        }
        this.data = this.getChildrenViewObjects(this.data);
    }

    /**
     * List
     * @param objectArray
     */
    private getChildrenViewObjects(objectArray: List<any>): List<any> {
        if (isNullOrUndefined(objectArray)) {
            return;
        }

        let viewObjectList = objectArray;

        if (objectArray.size > 0) {
            for (let i = 0; i < objectArray.size; i++) {
                viewObjectList = viewObjectList.update(i, () => this.getViewObject(objectArray.get(i) as Map<string, any>));

            }
            return viewObjectList;
        }
    }

    private getViewObject(bizObject: Map<string, any>): ViewObject {
        return new ViewObject(bizObject);
    }

}






