import { Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter, SimpleChanges } from '@angular/core';
// import { ModalDirective } from '@farris/ui/container/modal/modal';

import { AuthorizationEntity } from '../../entity/authorizationentity';
import { AuthfieldTreeComponent } from '../authfield-tree/authfield-tree.component';
import { isNullOrUndefined } from 'util';

import { AuthEntryEntity } from '../../entity/authentryentity';
import { AuthFieldEntity } from '../../entity/authfieldentity';
import { ModalDirective } from '@farris/ui/container/modal/modal';
@Component({
  // tslint:disable-next-line:component-selector
  selector: 'authfield-select',
  templateUrl: './authfield-select.component.html',
  styleUrls: ['./authfield-select.component.css']
})
export class AuthfieldSelectComponent implements OnInit, OnChanges {

  @ViewChild('autoShownModal') autoShownModal: ModalDirective;
  @ViewChild('treeDialog') treeDialog: AuthfieldTreeComponent;
  // @Input()
  // 
  // @Input() selectedsIn: AuthEntryEntity[];
  // 
  @Input() authorizationIds: string[];
  @Input() triggerShowWindow: boolean;
  isModalShown = false;
  authentrysSelected: AuthEntryEntity[];

  // 
  @Output() authentrys: EventEmitter<AuthEntryEntity[]> = new EventEmitter<AuthEntryEntity[]>();
  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    // 
    if (isNullOrUndefined(changes.triggerShowWindow) || isNullOrUndefined(changes.triggerShowWindow.previousValue)) {
      return;
    }
    if (!this.isModalShown) {
      this.showModal();
    }
  }

  showModal(): void {
    this.isModalShown = true;
  }

  hideModal(): void {
    this.autoShownModal.hide();
  }

  onHidden(): void {
    this.isModalShown = false;
  }

  btnOkClick() {
    this.authentrysSelected = this.treeDialog.dialogOk();
    this.hideModal();
    this.authentrys.next(this.authentrysSelected);
  }

}




