import { Component, Input } from '@angular/core';
import { BaseOperationComponentComponent } from '../base-operation-component/base-operation-component.component';
import { Validation, ViewValidation, GSPBizEntityObject } from '../util/be-reference';

@Component({
  selector: 'app-be-validations-tab',
  templateUrl: './validations-tab.component.html',
  styleUrls: ['./validations-tab.component.css']
})
export class ValidationsTabComponent extends BaseOperationComponentComponent {

  @Input() currentObject: GSPBizEntityObject;

  addValidation() {
    const newValidation = new Validation();
    this.viewOperations.add(new ViewValidation(newValidation));
    this.viewOperations.setSelectIndexByID(newValidation.ID);
    this.refresh(); // 
  }
}



