import { PackageView } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector } from '@angular/core';
import { BEDesignerFrameModule } from './bedesigner-frame/be-designer-frame.module';
import { BeDesignerFrameComponent } from './bedesigner-frame/be-designer-frame.component';
import { BeDesignerGuideComponent } from './bedesigner-frame/refModule/be-designer-guide/be-designer-guide.component';

export class BeDesignerPackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(BeDesignerFrameComponent);
    const moduleRef = this.injector.get(BEDesignerFrameModule) as BEDesignerFrameModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}

export class BeDesignerGuidePackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(BeDesignerGuideComponent);
    const moduleRef = this.injector.get(BEDesignerFrameModule) as BEDesignerFrameModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}



