import { Component, OnInit } from '@angular/core';
import { BaseActionCardComponent } from '../../base-operation-component/base-action-card/base-action-card.component';
import {
  ViewBizAction, BizAction, ViewReturnValue, ViewBizActionParaList,
  BizActionParameter, BizParameterType, BizVoidReturnType, BizReturnValue, BizParameterTypes,
  BizParameterModes, IGSPComponent, ICompParameter, ReturnValue, BEComponent, ParameterModes, SelectedComponentInfo
} from '../../util/libs';

/**
 * 
 * 
 * isMgrAction=false,isVisible
 */
@Component({
  selector: 'app-biz-entity-action-card',
  templateUrl: '../../base-operation-component/base-action-card/base-action-card.component.html',
})
export class BizEntityActionCardComponent extends BaseActionCardComponent implements OnInit {
  private paramTypes = BizParameterTypes;
  ngOnInit() {
    super.ngOnInit();
    this.isMgrAction = false;
    this.opType = 'BizAction';
  }
  // createNewViewOperation() {
  //   this.viewOperationData.ComponentId = '';
  //   this.viewOperationData.Code = '';
  //   this.viewOperationData.Name = '';
  //   this.viewOperationData.Description = '';
  //   const actionParameters = new Array<BizActionParameter>();
  //   this.viewOperationData.Parameters =  new ViewBizActionParaList(actionParameters);
  //   const bizReturnValue = new BizVoidReturnType();
  //   this.viewOperationData.ReturnValue = new ViewReturnValue(bizReturnValue);
  // }

  updateOperationInfo(selectedCmpInfo: SelectedComponentInfo) {
    super.updateOperationInfo(selectedCmpInfo);
    const component = selectedCmpInfo.component as BEComponent;
    const actionParameters = this.buildOperationParaList(component.BEMethod.BEParams);
    const paraList = new ViewBizActionParaList(actionParameters);
    this.viewOperationData.Parameters = paraList;
    const bizReturnValue = this.buildOperationReturnValue(component.BEMethod.ReturnValue);
    this.viewOperationData.ReturnValue = new ViewReturnValue(bizReturnValue);
  }

  buildOperationParaList(parameters: Array<ICompParameter>): Array<BizActionParameter> {
    const actionParameters = new Array<BizActionParameter>();
    parameters.forEach(element => {
      const parameter = new BizActionParameter();
      parameter.ID = element.ID;
      parameter.ParamCode = element.ParamCode;
      parameter.ParamName = element.ParamName;
      parameter.ParamDescription = element.ParamDescription;
      const modeText = ParameterModes.find(item => item.value === element.Mode).text;
      parameter.Mode = BizParameterModes.find(item => item.text === modeText).value;
      parameter.ParameterType = BizParameterTypes.find(item => item.value.toString() === element.ParameterType.toString()).value;
      parameter.Assembly = element.Assembly;
      parameter.ClassName = element.ClassName;
      actionParameters.push(parameter);
    });
    return actionParameters;
  }
  buildOperationReturnValue(returnValue: ReturnValue) {
    if (returnValue.ClassName === 'Void') {
      return this.createNewBizVoidReturnType(returnValue);
    } else {
      return this.createNewBizReturnType(returnValue);
    }
  }
  private createNewBizVoidReturnType(returnValue: ReturnValue): BizVoidReturnType {
    const bizVoidReturnType = new BizVoidReturnType();
    bizVoidReturnType.ID = returnValue.ID;
    bizVoidReturnType.ParamDescription = returnValue.ParamDescription;
    return bizVoidReturnType;
  }

  private createNewBizReturnType(returnValue: ReturnValue): BizReturnValue {
    const bizReturnValue = new BizReturnValue();
    bizReturnValue.ID = returnValue.ID;
    bizReturnValue.ParameterType = BizParameterTypes.find(item => item.value.toString() === returnValue.ParameterType.toString()).value;
    bizReturnValue.ClassName = returnValue.ClassName;
    bizReturnValue.Assembly = returnValue.Assembly;
    bizReturnValue.ParamDescription = returnValue.ParamDescription;
    return bizReturnValue;
  }

  getAssembly(type: BizParameterType): string {
    const paraType = this.paramTypes.find(item => item.value === type);
    return paraType ? paraType.assembly : '';
  }

  getClassName(type: BizParameterType): string {
    const paraType = this.paramTypes.find(item => item.value === type);
    return paraType ? paraType.className : '';
  }
}





