/**
 * 
 */
enum GSPElementObjectType {
    /**
     * 
     */
    None = 'None',

    /**
     * 
     */
    Association = 'Association',

    /**
     * 
     */
    Enum = 'Enum',
}

export { GSPElementObjectType };

export const GSPElementObjectTypes: Array<{ text: string, value: GSPElementObjectType }> = [
    { text: '', value: GSPElementObjectType.None },
    { text: '', value: GSPElementObjectType.Association },
    { text: '', value: GSPElementObjectType.Enum }
];



