import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ListBase } from '../base/listBase';
import { MapBase } from '../base/mapBase';

import { AuthFieldInfo, AuthFieldActionInfo } from '../../ref';

export class ViewAuthFieldActionInfoList extends ListBase<ViewAuthFieldActionInfo> {

  constructor(enumArray: AuthFieldInfo[] | List<any>) {
    super(enumArray);
    if (isNullOrUndefined(enumArray)) {
      return;
    }
    this.data = this.getViewAuthActions(this.data);
  }

  getViewAuthActions(authInfos: List<any>): any {
    if (isNullOrUndefined(authInfos)) {
      return;
    }

    let viewAuthInfos = authInfos;

    if (authInfos.size > 0) {
      for (let i = 0; i < authInfos.size; i++) {
        viewAuthInfos = viewAuthInfos.update(i, value => this.getViewAuthAction(authInfos.get(i)));
      }
      return viewAuthInfos;
    }
  }

  getViewAuthAction(authField: AuthFieldActionInfo): any {
    return new ViewAuthFieldActionInfo(authField);
  }
}

export class ViewAuthFieldActionInfo extends MapBase {

  /**
   * 
   * @param element
   */
  constructor(authField: AuthFieldActionInfo | Map<string, any>) {
    super(authField);
  }
}




