
import { Component, OnInit, Input, Output, OnChanges, SimpleChanges, OnDestroy, ViewChild, EventEmitter } from '@angular/core';
import { BaseOperationComponentComponent } from '../base-operation-component/base-operation-component.component';
import {
  Validation, Guid, ViewValidation, Determination,
  ViewDetermination, BEDeterminationType, BETriggerTimePointType,
  ViewDeterminationList, GSPBizEntityObject, BETriggerTimePointTypes, BizOperationConst
} from '../util/be-reference';
import { List, Map, fromJS } from 'immutable';
import { isNullOrUndefined } from 'util';
import { BaseDeterminationGridComponent } from './base-determination-grid/base-determination-grid.component';
import {
  DeterminationTriggerElementGridComponent
} from './determination-trigger-element-grid/determination-trigger-element-grid.component';

@Component({
  selector: 'app-be-determinations-tab',
  templateUrl: './determinations-tab.component.html',
  styleUrls: ['./determinations-tab.component.css']
})
export class DeterminationsTabComponent implements OnInit, OnChanges, OnDestroy {

  @ViewChild('afterLoadingGrid') afterLoadingGrid: BaseDeterminationGridComponent;
  @ViewChild('retrieveDefaultGrid') retrieveDefaultGrid: BaseDeterminationGridComponent;
  @ViewChild('afterModifyGrid') afterModifyGrid: BaseDeterminationGridComponent;
  @ViewChild('determineGrid') determineGrid: BaseDeterminationGridComponent;
  @ViewChild('beforeCheckGrid') beforeCheckGrid: BaseDeterminationGridComponent;
  @ViewChild('afterNumbersAdjustedGrid') afterNumbersAdjustedGrid: BaseDeterminationGridComponent;
  @ViewChild('afterModifyTriggerElementGrid') afterModifyTriggerElementGrid: DeterminationTriggerElementGridComponent;
  @ViewChild('beforeCheckTriggerElementGrid') beforeCheckTriggerElementGrid: DeterminationTriggerElementGridComponent;

  /**
     * 
     */
  @Input() viewDeterminations: ViewDeterminationList;
  @Input() currentObject: GSPBizEntityObject;

  @Output() componentCodeTemplate: EventEmitter<any> = new EventEmitter();

  triggerTypes = BETriggerTimePointTypes;
  curStep = 1;
  // Tab
  currentTriggerType: BETriggerTimePointType = BETriggerTimePointType.AfterLoading;

  tabNameAfterLoading = ''; // 1
  tabNameRetrieveDefault = ''; // 2
  tabNameAfterModify = ''; // 3
  tabNameDetermine = ''; // 4
  tabNameBeforeCheck = ''; // 5
  tabNameAfterNumbersAdjusted = ''; // 6
  /**
   * 
   */
  viewDetermination: ViewDetermination;

  get currentTriggerTimeDtm() {
    if (isNullOrUndefined(this.viewDetermination) || isNullOrUndefined(this.viewDetermination.toJS())) {
      return null;
    }
    const dtm = this.viewDetermination.toJS();
    const triggerTime = this.triggerTypes.find(item => item.index === this.curStep).value;
    if ((<Determination>dtm).TriggerTimePointType === triggerTime) {
      return this.viewDetermination;
    } else {
      return null;
    }
  }
  constructor() { }
  ngOnInit() {
    this.refresh();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['viewDeterminations']) {
      this.refresh();
    }
  }
  ngOnDestroy(): void {
  }

  protected refresh() {

    if (isNullOrUndefined(this.viewDeterminations) || isNullOrUndefined(this.viewDeterminations.toJS())) {
      this.viewDetermination = null;
      return;
    }
    const item = this.viewDeterminations.toJS().find(dtm => {
      return (<Determination>dtm).TriggerTimePointType === this.currentTriggerType;
    });
    if (!isNullOrUndefined(item)) {
      this.viewDeterminations.setSelectIndexByID(item.ID);
      this.viewDetermination = this.viewDeterminations.currentSelectItem as ViewDetermination;
    } else {
      this.viewDetermination = null;
    }

    this.viewDeterminations.selectNewItem.subscribe(
      value => {
        this.viewDetermination = value as ViewDetermination;
      }
    );
  }

  addDetermination() {
    const newDetermination = new Determination();
    newDetermination.TriggerTimePointType = this.triggerTypes.find(item => item.index === this.curStep).value;
    // newDetermination.TriggerTimePointType = this.currentTriggerType;
    this.viewDeterminations.add(new ViewDetermination(newDetermination));
    this.viewDeterminations.setSelectIndexByID(newDetermination.ID);
    if (this.viewDeterminations.length === 1) {
      this.refresh();
    }
  }

  getCurrentGrid(): BaseDeterminationGridComponent {
    const triggerTime = this.triggerTypes.find(item => item.index === this.curStep).value;
    switch (triggerTime) {
      case BETriggerTimePointType.AfterLoading:
        return this.afterLoadingGrid;
      case BETriggerTimePointType.RetrieveDefault:
        return this.retrieveDefaultGrid;
      case BETriggerTimePointType.AfterModify:
        return this.afterModifyGrid;
      case BETriggerTimePointType.Determine:
        return this.determineGrid;
      case BETriggerTimePointType.BeforeCheck:
        return this.beforeCheckGrid;
      case BETriggerTimePointType.AfterNumbersAdjusted:
        return this.afterNumbersAdjustedGrid;
    }
  }

  getCurrentTriggerGrid(): DeterminationTriggerElementGridComponent {
    const triggerTime = this.triggerTypes.find(item => item.index === this.curStep).value;
    switch (triggerTime) {
      case BETriggerTimePointType.AfterModify:
        return this.afterModifyTriggerElementGrid;
      case BETriggerTimePointType.BeforeCheck:
        return this.beforeCheckTriggerElementGrid;
    }
  }

  remove() {
    const currentGrid = this.getCurrentGrid();
    currentGrid.remove();
  }

  /**
   * 
   * 
   * @param args
   */
  tabSelectChange(args: any) {
    const { title } = args;
    switch (title) {
      case this.tabNameAfterLoading:
        this.currentTriggerType = BETriggerTimePointType.AfterLoading;
        break;
      case this.tabNameRetrieveDefault:
        this.currentTriggerType = BETriggerTimePointType.RetrieveDefault;
        break;
      case this.tabNameAfterModify:
        this.currentTriggerType = BETriggerTimePointType.AfterModify;
        break;
      case this.tabNameDetermine:
        this.currentTriggerType = BETriggerTimePointType.Determine;
        break;
      case this.tabNameBeforeCheck:
        this.currentTriggerType = BETriggerTimePointType.BeforeCheck;
        break;
      case this.tabNameAfterNumbersAdjusted:
        this.currentTriggerType = BETriggerTimePointType.AfterNumbersAdjusted;
        break;
    }
    // this.getCurrentViewDetermination();
  }

  jumpStep(destStep) {
    if (this.curStep === destStep) {
      return;
    }
    this.curStep = destStep;
    this.currentTriggerType = this.triggerTypes.find(item => item.index === this.curStep).value;
  }

  /**
   * 
   * @param dataItem 
   */
  upwardDetermination() {
    const currentItem = this.viewDeterminations.currentSelectItem;
    if (isNullOrUndefined(currentItem)
      || currentItem.get(BizOperationConst.TriggerTimePointType) !== this.currentTriggerType) {
      alert('');
    }
    this.getCurrentGrid().upwardDetermination(currentItem.toJS());
  }

  /**
   * 
   * @param dataItem 
   */
  downwardDetermination() {
    const currentItem = this.viewDeterminations.currentSelectItem;
    if (isNullOrUndefined(currentItem)
      || currentItem.get(BizOperationConst.TriggerTimePointType) !== this.currentTriggerType) {
      alert('');
    }
    this.getCurrentGrid().downwardDetermination(currentItem.toJS());
  }

  /**
   * 
   * @param dataItem 
   */
  topwardDetermination() {
    const currentItem = this.viewDeterminations.currentSelectItem;
    if (isNullOrUndefined(currentItem)
      || currentItem.get(BizOperationConst.TriggerTimePointType) !== this.currentTriggerType) {
      alert('');
    }
    this.getCurrentGrid().topwardDetermination(currentItem.toJS());
  }

  /**
   * 
   * @param dataItem 
   */
  bottomwardDetermination() {
    const currentItem = this.viewDeterminations.currentSelectItem;
    if (isNullOrUndefined(currentItem)
      || currentItem.get(BizOperationConst.TriggerTimePointType) !== this.currentTriggerType) {
      alert('');
    }
    this.getCurrentGrid().bottomwardDetermination(currentItem.toJS());
  }

  /**
   * 
   */
  getComponentCodeTemplate() {
    this.componentCodeTemplate.next(true);
  }

  editTriggerElements() {
    if (this.isDbeOp()) {
      return;
    }
    this.getCurrentTriggerGrid().editTriggerElements();
  }

  isDbeOp() {
    const item = this.viewDeterminations.currentSelectItem;
    if (item && item.toJS() && (<Determination>item.toJS()).IsRef) {
      return true;
    }
    return false;
  }
}



