import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { ViewModel, ViewObject } from '../util/be-reference';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-basic-info-tab',
  templateUrl: './basic-info-tab.component.html',
  styleUrls: ['./basic-info-tab.component.css']
})
export class BasicInfoTabComponent implements OnInit, OnChanges {

  @Input() currentSelectItem: ViewModel | ViewObject;
  isSelectBizEntityNode: boolean;

  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (isNullOrUndefined(this.currentSelectItem)) {
      return;
    }

    // 
    if ((<ViewModel>this.currentSelectItem).mainObject != null) {
      this.isSelectBizEntityNode = true;
    } else {
      this.isSelectBizEntityNode = false;
    }
  }
}



