import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';
import { AuthorizationService } from '../../service/authorization.service';
import { AuthorizationEntity } from '../../entity/authorizationentity';
import { AuthEntryEntity } from '../../entity/authentryentity';
import { AuthFieldEntity } from '../../entity/authfieldentity';
import { isNullOrUndefined } from 'util';
import { of } from 'rxjs/observable/of';
@Component({
  // tslint:disable-next-line:component-selector
  selector: 'authfield-tree',
  templateUrl: './authfield-tree.component.html',
  styleUrls: ['./authfield-tree.component.css']
})
export class AuthfieldTreeComponent implements OnInit {

  expandedKeys: string[] = [];
  authEntriesEntity: AuthEntryEntity[];
  treeAuthorization: AuthorizationEntity;
  treeEntity: AuthEntryEntity;
  datas: AuthFieldEntity[] = [];
  selectedItem: AuthFieldEntity;

  // @Input
  @Input() authorizationIds: string[];

  constructor(private authorizationService: AuthorizationService) { }

  ngOnInit() {
    this.treeAuthorization = new AuthorizationEntity();
    this.treeEntity = new AuthEntryEntity();
    this.authorizationService.GetAuthfields(this.authorizationIds).subscribe(data => {
      let arr = new Array();
      arr = data;
      arr.forEach(item => {
        // 
        this.treeAuthorization = item;
        const authObj = new AuthFieldEntity(); //  idid
        authObj.id = item.id;
        authObj.code = item.code;
        authObj.name = item.name;
        authObj.type = '0';
        authObj.children = [];
        if (!isNullOrUndefined(this.authorizationIds)) {
          const find = this.authorizationIds.find(id => id === this.treeAuthorization.id);
          if (isNullOrUndefined(find)) {
            return;
          }
        }

        // 
        const authEntries = this.treeAuthorization.authEntry;
        if (isNullOrUndefined(authEntries) || authEntries.length === 0) {
          return;
        }
        this.expandedKeys.push(authObj.id); // 
        authEntries.forEach(child => {
          this.treeEntity = child;
          const authField = new AuthFieldEntity(); //  idid
          authField.id = this.treeEntity.authField;
          authField.code = this.treeEntity.authFieldCode;
          authField.name = this.treeEntity.authFieldName;
          authField.type = '1';
          authField.authorizationId = authObj.id;
          authObj.children.push(authField);
        });
        this.datas.push(authObj);
      });
    });
  }



  dialogOk() {
    if (this.selectedItem.type !== '1') {
      alert('');
    }
    this.authEntriesEntity = [];
    const item = new AuthEntryEntity();
    item.authField = this.selectedItem.id;
    item.authFieldName = this.selectedItem.name;
    item.authorizationId = this.selectedItem.authorizationId;
    this.authEntriesEntity.push(item);
    return this.authEntriesEntity;
  }

  public hasChildren = (item: any) => item.children && item.children.length > 0;
  public fetchChildren = (item: any) => of(item.children);

  //  
  iconClass({ type }: any): any {
    return {
      'k-i-folder': type.toString() !== '1',
      'k-i-gear': type.toString() === '1',
      'k-icon': true
    };
  }

  // 
  handleSelection({ dataItem }: any): void {
    this.selectedItem = dataItem;
  }
}



